import React, { useState, useEffect } from "react";
import { Alert, AlertTitle } from "@material-ui/lab";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import "./AutomatischeVervollstaendigung.scss";

const AutomatischeVervollstaendigung = ({ user, vertrag }) => {
  const [userState, setUserState] = useState();

  useEffect(() => {
    setUserState(user);
  }, [user]);

  if (vertrag.length === 0) {
    return <div />;
  } else {
    return (
      <div className="container">
        <Alert className="infoAlert" severity="info">
          <AlertTitle>
            {userState ? <strong>Hallo {userState.name},</strong> : null}
          </AlertTitle>
          <p>
            es fehlen noch Daten in {vertrag.length}{" "}
            {vertrag.length === 1 ? "Vertrag." : "Verträgen."} Lasse diese
            bequem durch uns ergänzen. Wir machen das!
          </p>
          <Link to="/vertragsUebersicht/dialogAssistent">
            <strong className="infoText">Assistent hier klicken</strong>
          </Link>
        </Alert>
      </div>
    );
  }
};

const mapStateToProps = (state) => {
  return {
    user: state.userReducer.user,
  };
};

export default connect(mapStateToProps)(AutomatischeVervollstaendigung);
