import React from "react";
import { Link } from "react-router-dom";

import logo from "../../../../../assets/images/logo.png";
import "./Logo.scss";

const Logo = props => {
  return (
    <Link to="/vertragsUebersicht">
      <img id="vertragsengel-logo" src={logo} alt="logo" />
    </Link>
  );
};

export default Logo;
