import React from "react";

import Logo from "./SidebarElements/Logo/Logo";
import NavElements from "./SidebarElements/NavElements/NavElements";
import "./Sidebar.scss";

const SideBar = ({ openSidebar, showSidebar, user }) => {
  if (showSidebar) {
    return (
      <div style={{ display: "block" }} id="nav" className="nav">
        <Logo />
        <NavElements openSidebar={openSidebar} user={user} />
      </div>
    );
  } else {
    return (
      <div style={{ display: "none" }} id="nav" className="nav">
        <Logo />
        <NavElements openSidebar={openSidebar} user={user} />
      </div>
    );
  }
};

export default SideBar;
