import React, { useState, Fragment } from "react";
import axios from "axios";
import { connect } from "react-redux";

import "./Kontakt.scss";
import InfoTop from "./KontaktPageElements/InfoTop/InfoTop";
import InfoErledigt from "./KontaktPageElements/InfoErledigt/InfoErledigt";
import InfoHandy from "./KontaktPageElements/InfoHandy/InfoHandy";
import Form from "./KontaktPageElements/Form/Form";

const Kontakt = ({ token, user }) => {
  const [state, setState] = useState({
    value0: 0,
    value1: 0,
    submit: false,
    msgTitel: "",
    msg: "",
    msgLength: 0,
  });

  const click = (e) => {
    let valueNumber = "";
    if (e.target.value === "Bitte auswählen") {
      valueNumber = "0";
    } else if (e.target.value === "Versicherungsschaden melden") {
      valueNumber = "1";
    } else if (e.target.value === "Kundenservice kontaktieren") {
      valueNumber = "2";
    }

    setState({
      ...state,
      value0: valueNumber,
    });
  };

  const click1 = (e) => {
    console.log(e.target.value);
    let valueNumber = "";
    if (e.target.value === "Bitte auswählen") {
      valueNumber = "0";
    } else if (e.target.value === "Um meinen Vertrag") {
      valueNumber = "1";
    } else if (e.target.value === "Um mein Benutzerkonto") {
      valueNumber = "2";
    } else if (e.target.value === "Um meine Kündigung") {
      valueNumber = "3";
    } else if (e.target.value === "Um eine Rechnung") {
      valueNumber = "4";
    } else if (e.target.value === "Sonstiges") {
      valueNumber = "5";
    }

    setState({
      ...state,
      msgTitel: e.target.value,
      value1: valueNumber,
    });
  };

  const handleOnChange = (e) => {
    setState({
      ...state,
      msgLength: e.target.value.length,
      msg: e.target.value,
    });
  };

  const clickButton = () => {
    setState({
      ...state,
      submit: !state.submit,
    });
    const { name, email } = user;
    const { msgTitel, msg } = state;
    axios({
      method: "post",
      url: "/vertragsUebersicht/kontakt",
      headers: { "x-auth-token": token },
      data: {
        name,
        email,
        msgTitel,
        msg,
      },
    });
  };

  return (
    <Fragment>
      <div id="right-container-info">
        <InfoTop />
        <InfoErledigt submitValue={state.submit} />
        <InfoHandy
          value0={state.value0}
          value1={state.value1}
          submitValue={state.submit}
        />
        <Form
          msgLength={state.msgLength}
          click1={click1}
          click={click}
          value0={state.value0}
          value1={state.value1}
          submit={clickButton}
          submitValue={state.submit}
          handleOnChange={handleOnChange}
        />
      </div>
    </Fragment>
  );
};
const mapStateToProps = (state) => ({
  token: state.userReducer.token,
  user: state.userReducer.user,
});
export default connect(mapStateToProps)(Kontakt);
