import {
  USER_LOADING,
  USER_LOADED,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT_SUCCESS,
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  CHANGE_USER_DATA_SUCCESS,
  USER_BIRTHDAY_LOADING,
  USER_BIRTHDAY_LOADED,
  USER_FIRST_NAME_LOADING,
  USER_FIRST_NAME_LOADED,
  USER_LAST_NAME_LOADING,
  USER_LAST_NAME_LOADED,
  USER_ADDRESS_LOADING,
  USER_ADDRESS_LOADED,
  USER_HANDYNUMMER_LOADING,
  USER_HANDYNUMMER_LOADED,
} from "./userTypes";

const initialState = {
  token: localStorage.getItem("token"),
  isAuthenticated: null,
  isLoading: false,
  user: {
    contactEmail: "",
    contactVorname: "",
    contactNachname: "",
    contactStrasse: "",
    contactPostleitzahl: "",
    contactOrt: "",
    contactLand: "",
    contactGeburtsdatum: "",
    contactHandynummer: "",
  },
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case USER_BIRTHDAY_LOADING:
      return {
        ...state,
      };
    case USER_BIRTHDAY_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: { ...state.user, ...action.payload },
      };
    case USER_HANDYNUMMER_LOADING:
      return {
        ...state,
      };
    case USER_HANDYNUMMER_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: { ...state.user, ...action.payload },
      };
    case USER_ADDRESS_LOADING:
      return {
        ...state,
      };
    case USER_ADDRESS_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: { ...state.user, ...action.payload },
      };
    case USER_FIRST_NAME_LOADING:
      return {
        ...state,
      };
    case USER_FIRST_NAME_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: { ...state.user, ...action.payload },
      };
    case USER_LAST_NAME_LOADING:
      return {
        ...state,
      };
    case USER_LAST_NAME_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: { ...state.user, ...action.payload },
      };
    case USER_LOADING:
      return {
        ...state,
      };
    case USER_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        isLoading: false,
        user: { ...state.user, ...action.payload },
      };
    case LOGIN_SUCCESS:
      sessionStorage.setItem("login", "true");
      localStorage.setItem("token", action.payload.token);
      return {
        ...state,
        ...action.payload,
        user: action.payload.user,
        isAuthenticated: true,
        isLoading: false,
      };
    case REGISTER_SUCCESS:
      return {
        ...state,
        isAuthenticated: false,
        isLoading: false,
      };
    case AUTH_ERROR:
    case LOGIN_FAIL:
    case LOGOUT_SUCCESS:
    case REGISTER_FAIL:
      localStorage.removeItem("token");
      sessionStorage.clear();
      return {
        ...state,
        token: null,
        user: null,
        isAuthenticated: false,
        isLoading: false,
      };
    case CHANGE_USER_DATA_SUCCESS:
      return {
        ...state,
      };
    default:
      return state;
  }
};

export default reducer;
