import React, { Fragment } from "react";
import { Link } from "react-router-dom";

function NotFound() {
  return (
    <Fragment>
      <div style={{ textAlign: "center" }}>
        <h1>Ups ... Etwas schief gelaufen</h1>
        <p>
          <Link to="/login">Weiter zur Login Seite</Link>
        </p>
      </div>
    </Fragment>
  );
}

export default NotFound;
