import {
  GET_SPARTEN,
  SPARTEN_LOADING,
  GET_GESELLSCHAFTEN,
  GESELLSCHAFTEN_LOADING,
} from "./vertragsArrayTypes";

const initialState = {
  vertragsList: "",
  vertragsGesellschaften: "",
  loading: false,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case SPARTEN_LOADING:
      return {
        ...state,
        loading: true,
      };
    case GET_SPARTEN:
      return {
        ...state,
        vertragsList: action.payload,
        loading: false,
      };
    case GESELLSCHAFTEN_LOADING:
      return {
        ...state,
        loading: true,
      };
    case GET_GESELLSCHAFTEN:
      return {
        ...state,
        vertragsGesellschaften: action.payload,
        loading: false,
      };
    default:
      return state;
  }
};

export default reducer;
