import React, { useState } from "react";

import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";

const Form = (props) => {
  const [state, setState] = useState("Bitte auswählen");
  const [state2, setState2] = useState("Bitte auswählen");

  const handleChangeSelectValue = (e) => {
    props.click(e);
  };

  const handleChangeSelectValue2 = (e) => {
    props.click1(e);
  };

  const handleValueName = (e) => {
    setState(e);
  };

  const handleValueName2 = (e) => {
    setState2(e);
  };

  if (props.value0 === "1" || props.value1 === "1" || props.value1 === "2") {
    return null;
  } else if (props.submitValue === true) {
    return null;
  } else {
    return (
      <form id="right-container-info-form" method="post" action="">
        <div className="right-container-info-form-text">
          <p>Hallo,</p>
          <span>
            bitte beantworte die folgenden Fragen, damit ich die beste Lösung
            für dich finden kann.
          </span>
        </div>
        <div className="right-container-info-form-fields">
          <label>Was möchtest du?</label>
          <FormControl variant="standard">
            <Select
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              value={state}
              onChange={handleChangeSelectValue}
            >
              <MenuItem
                onClick={(e) => handleValueName("Bitte auswählen")}
                value="Bitte auswählen"
              >
                Bitte auswählen
              </MenuItem>
              <MenuItem
                onClick={(e) => handleValueName("Versicherungsschaden melden")}
                value={"Versicherungsschaden melden"}
              >
                Versicherungsschaden melden
              </MenuItem>
              <MenuItem
                onClick={(e) => handleValueName("Kundenservice kontaktieren")}
                value={"Kundenservice kontaktieren"}
              >
                Kundenservice kontaktieren
              </MenuItem>
            </Select>
          </FormControl>
        </div>
        {props.value0 === "2" ? (
          <div id="right-container-info-form-fields2">
            <label>Um was geht es genau?</label>
            <FormControl variant="standard">
              <Select
                labelId="demo-simple-select-outlined-label"
                id="demo-simple-select-outlined"
                value={state2}
                onChange={handleChangeSelectValue2}
              >
                <MenuItem
                  onClick={(e) => handleValueName2("Bitte auswählen")}
                  value="Bitte auswählen"
                >
                  Bitte auswählen
                </MenuItem>
                <MenuItem
                  onClick={(e) => handleValueName2("Um meinen Vertrag")}
                  value={"Um meinen Vertrag"}
                >
                  Um meinen Vertrag
                </MenuItem>
                <MenuItem
                  onClick={(e) => handleValueName2("Um mein Benutzerkonto")}
                  value={"Um mein Benutzerkonto"}
                >
                  Um mein Benutzerkonto
                </MenuItem>
                <MenuItem
                  onClick={(e) => handleValueName2("Um meine Kündigung")}
                  value={"Um meine Kündigung"}
                >
                  Um meine Kündigung
                </MenuItem>
                <MenuItem
                  onClick={(e) => handleValueName2("Um eine Rechnung")}
                  value={"Um eine Rechnung"}
                >
                  Um eine Rechnung
                </MenuItem>
                <MenuItem
                  onClick={(e) => handleValueName2("Sonstiges")}
                  value={"Sonstiges"}
                >
                  Sonstiges
                </MenuItem>
              </Select>
            </FormControl>
            {/* <select
              onClick={props.click1}
              name="name"
              id="right-container-select-2"
            >
              <option value="0">Bitte auswählen</option>
              <option value="1">Um meinen Vertrag</option>
              <option value="2">Um mein Benutzerkonto</option>
              <option value="3">Um meine Kündigung</option>
              <option value="4">Um eine Rechnung</option>
              <option value="5">Sonstiges</option>
            </select> */}
          </div>
        ) : null}
        {props.value1 === "3" ||
        props.value1 === "4" ||
        props.value1 === "5" ? (
          <div id="right-container-info-form-fields3">
            <label>
              Bitte schreibe dein Anliegen kurz auf, damit sich das richtige
              Team schnellstmöglich darum kümmern kann.
            </label>
            <textarea
              onChange={props.handleOnChange}
              name="right-container-textarea"
              id="right-container-textarea"
              cols="60"
              rows="4"
            ></textarea>
            {props.msgLength !== 0 ? (
              <div
                onClick={props.submit}
                id="right-container-button-submit"
                name="submit"
                type="submit"
              >
                Anfrage jetzt absenden
              </div>
            ) : null}
          </div>
        ) : null}
      </form>
    );
  }
};

export default Form;
