import React from "react";

import "./VertragsGrid.scss";
import VertragsGridElement from "./VertragsGridElement/VertragsGridElement";

const VertragsGrid = ({
  handleSetVertrag,
  vertrags,
  setVertrags,
  setRemovedVertragsEl,
  removedVertragsEl,
  vertragsGesellschaften,
  handleShowMessage,
}) => {
  return (
    <div className="auswahl-panel">
      <div className="auswahl-list">
        <VertragsGridElement
          handleSetVertrag={handleSetVertrag}
          vertrags={vertrags}
          setVertrags={setVertrags}
          setRemovedVertragsEl={setRemovedVertragsEl}
          removedVertragsEl={removedVertragsEl}
          vertragsGesellschaften={vertragsGesellschaften}
          handleShowMessage={handleShowMessage}
        />
      </div>
    </div>
  );
};

export default VertragsGrid;
