import React from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";

const InfoTop = (props) => {
  return (
    <div className="right-container-info-top">
      <div className="right-container-info-header">
        <Link to="/vertragsUebersicht">
          <i>
            <FontAwesomeIcon
              icon={faTimes}
              id="info-header-close"
              className="fa fa-window-close info-header-close"
            />
          </i>
        </Link>
      </div>
      <div className="right-container-info-header-avatar"></div>
      <div className="right-container-info-header-avatar-text">
        Rene,
        <br /> <i>Kundenbetreuung</i>
      </div>
    </div>
  );
};

export default InfoTop;
