import React from "react";

const StatistikSliderElement = ({
  vertragNameWithKosten,
  kosten,
  color,
  headerText,
}) => {
  return (
    <div>
      <div className="sliderContener">
        <p>{headerText}</p>
        <div className="container">
          <div className="ring">
            <div className="ring2">
              {vertragNameWithKosten.map((el, i) => {
                let elementStyle = "0%";
                if (el.kosten / kosten === 1) {
                  elementStyle = "100%";
                } else {
                  elementStyle = `${(el.kosten / kosten) * 100 - 1}%`;
                }

                return (
                  <div
                    key={i}
                    style={{
                      background: color,
                      height: elementStyle,
                    }}
                    className={`ring-element`}
                  >
                    <span>{el.kosten} €</span>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="contentSlider">
            {vertragNameWithKosten.map((el, i) => {
              return (
                <div
                  key={i}
                  style={{
                    height: `${(el.kosten / kosten) * 100 - 1}%`,
                  }}
                  className={`contentSlider-element`}
                >
                  <span> - {el.vertragAnbieter}</span>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default StatistikSliderElement;
