import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { Link, useParams, Redirect, useLocation } from "react-router-dom";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import Popup from "reactjs-popup";

import {
  deleteFullVertrag,
  getFullVertrag,
  addFullVertrag,
} from "../../../redux/index";
import "./VertragsDetail.scss";
import VertragForm from "../VertragsContract/Contracts/VertragForm";

const VertragsDetail = ({
  setShowDetails,
  fullVertrag,
  deleteFullVertrag,
  handleShowMessage,
  getFullVertrag,
  gesellschaftsImage,
  addFullVertrag,
  user,
}) => {
  const [isMatchId, setIsMatchId] = useState(false);
  const [redirect, setRedirect] = useState(false);
  const [redirect2, setRedirect2] = useState(false);
  let [vertragDetail, setVertragDetail] = useState([]);

  let location = useLocation();

  let { id } = useParams();

  useEffect(() => {
    if (!redirect2) {
      setShowDetails(true);
    }
  });

  const handleShowDetails = () => {
    setShowDetails(false);
  };

  const alert = (e) => {
    const result = window.confirm(
      "Sie sind sicher, dass Sie diesen Vertrag löschen möchten?"
    );
    if (result) {
      deleteFullVertrag(id);
      handleShowMessage("showMessage2");
      handleShowDetails();
      setRedirect2(true);
      getFullVertrag();
    } else {
      return null;
    }
  };

  id = parseInt(id);

  useEffect(
    () => {
      if (!isMatchId && vertragDetail.length !== 0) {
        if (vertragDetail[0].vertrag_id === id) {
          setRedirect(false);
          setIsMatchId(true);
        } else {
          setIsMatchId(false);
          setRedirect(true);
        }
      }
    }, // eslint-disable-next-line
    [vertragDetail]
  );

  useEffect(
    () => {
      let vertragDetailProp = fullVertrag.filter(
        (vertrag) => `'${vertrag.vertrag_id}'` === `'${id}'`
      );
      setVertragDetail(vertragDetailProp);
    }, // eslint-disable-next-line
    [fullVertrag]
  );

  const editFullVertrags = (values) => {
    addFullVertrag(values);
    setRedirect2(true);
    /* getFullVertrag(); */
  };

  if (isMatchId) {
    let imageLogo = gesellschaftsImage.filter(
      (el) =>
        el.field_gesellschaft_name_value === vertragDetail[0].vertrag_anbieter
    );
    let gesellschaftsImages;
    try {
      gesellschaftsImages = `${process.env.PUBLIC_URL}/assets/gesellschaftsImages/${imageLogo[0].gesellschaft_image}`;
      if (!gesellschaftsImages.includes(".png")) {
        gesellschaftsImages = require(`../../../assets/vertragsImages/${vertragDetail[0].vertrag_name}.png`);
      }
    } catch (e) {}
    return (
      <Popup
        modal
        closeOnDocumentClick={false}
        defaultOpen={true}
        closeOnEscape={false}
        className="popup3"
      >
        <div key={vertragDetail[0].vertrag_id} id="info-vertrag-uebersicht">
          <div className="vertrag-uebersicht-content">
            <div className="background"></div>
            <div className="vertrag-uebersicht-head">
              <div className="vertragLogo">
                <img
                  className="image"
                  alt={vertragDetail[0].vertrag_anbieter}
                  src={gesellschaftsImages}
                />
              </div>
              <Link
                onClick={handleShowDetails}
                style={{ color: "#9798a2" }}
                to={
                  location.pathname.includes("gekündigteVerträge")
                    ? "/vertragsUebersicht/gekündigteVerträge"
                    : "/vertragsUebersicht"
                }
              >
                <i id="button-close-info-vertrag-uebersicht">
                  <FontAwesomeIcon icon={faTimes} />
                </i>
              </Link>
              <div className="vertrag-head-text">
                <div className="vertrag-uebersicht-titel">
                  {vertragDetail[0].vertrag_anbieter}
                </div>
                <div className="vertrag-uebersicht-info">
                  <p>
                    {new Date(vertragDetail[0].vertragsanfang)
                      .toISOString()
                      .slice(0, 10)
                      .split("-")
                      .join(".") +
                      " - " +
                      new Date(vertragDetail[0].vertragsende)
                        .toISOString()
                        .slice(0, 10)
                        .split("-")
                        .join(".")}
                  </p>
                </div>
              </div>
            </div>
            <VertragForm
              editFullVertrags={editFullVertrags}
              vertragDetail={vertragDetail[0]}
              alert={alert}
              userId={user._id}
            />
          </div>
          <div>{redirect2 && <Redirect to="/vertragsUebersicht" />}</div>
        </div>
      </Popup>
    );
  } else {
    return <div>{redirect && <Redirect to="/vertragsUebersicht" />}</div>;
  }
};

const mapStateToProps = (state) => {
  return {
    fullVertrag: state.fullVertragReducer.fullVertrags,
    gesellschaftsImage: state.gesellschaftenReducer.gesellschaftenList,
    user: state.userReducer.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    { deleteFullVertrag, getFullVertrag, addFullVertrag },
    dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(VertragsDetail);
