import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Formik, Form } from "formik";
import { Button } from "@material-ui/core";
import * as Yup from "yup";
import { Link } from "react-router-dom";
import MySelectField2 from "../../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MySelectField2";

import MyTextField from "../../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextField";
import { register, clearErrors } from "../../../redux/index";

const SignupSchema = Yup.object().shape({
  name: Yup.string().required("Dieses Feld ist erforderlich"),
  vorname: Yup.string().required("Dieses Feld ist erforderlich"),
  email: Yup.string().email().required("Dieses Feld ist erforderlich"),
  password: Yup.string().required("Dieses Feld ist erforderlich"),
  registerOptionen: Yup.string().required(),
  berater: Yup.mixed().when("registerOptionen", {
    is: (val) => val === "1",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  empfehlung: Yup.mixed().when("registerOptionen", {
    is: (val) => val === "5",
    then: Yup.string().required("Dieses Feld ist erforderlich"),
    otherwise: Yup.string(),
  }),
});

const Register = ({ register, error }) => {
  const [state, setState] = useState("");

  const backgroundImage = require(`../../../assets/images/logo1.png`);
  const backgroundImage2 = require(`../../../assets/images/logo.png`);

  useEffect(() => {
    setState(error);
  }, [error]);

  return (
    <div className="loginWrapper">
      <div className="loginContainer">
        <div
          className="logo1"
          style={{
            backgroundImage: `url(${backgroundImage})`,
          }}
        ></div>
        <div
          className="logo2"
          style={{
            backgroundImage: `url(${backgroundImage})`,
          }}
        ></div>
        <div className="loginContentContainer">
          <div className="my-text-field">
            <div
              className="logo"
              style={{
                backgroundImage: `url(${backgroundImage2})`,
              }}
            ></div>
            {state === "Benutzer existiert bereits" ? (
              <h4 className="alert-info">{state}</h4>
            ) : null}
            {state === "Erfolgreich registriert" ? (
              <h4 className="alert-info3">{state}</h4>
            ) : null}
          </div>
          <Formik
            initialValues={{
              name: "",
              vorname: "",
              email: "",
              password: "",
              registerOptionen: "",
              empfehlung: "",
              berater: "",
            }}
            enableReinitialize={true}
            validationSchema={SignupSchema}
            onSubmit={(data, error) => {
              if (data.berater !== "" && data.registerOptionen !== "1") {
                data.berater = "";
              }
              if (data.empfehlung !== "" && data.registerOptionen !== "5") {
                data.empfehlung = "";
              }
              register(data);
            }}
          >
            {({ values }) => (
              <Form
                style={{
                  display: "flex",
                  flexDirection: "column",
                  width: "300px",
                  margin: "auto",
                }}
              >
                <MyTextField
                  placeholder="Name"
                  name="name"
                  type="text"
                  label="Name"
                />
                <MyTextField
                  placeholder="Vorname"
                  name="vorname"
                  type="text"
                  label="Vorname"
                />
                <MyTextField
                  placeholder="E-Mail"
                  name="email"
                  type="email"
                  label="E-Mail"
                />
                <MyTextField
                  placeholder="Passwort"
                  name="password"
                  type="password"
                  label="Passwort"
                />
                <MySelectField2
                  name="registerOptionen"
                  label="Wie hast du uns kennengelernt?"
                  placeholder="Wie hast du uns kennengelernt?"
                />
                {values.registerOptionen === "1" ? (
                  <MyTextField
                    placeholder="Berater ID"
                    name="berater"
                    type="berater"
                    label="Berater ID"
                  />
                ) : null}
                {values.registerOptionen === "5" ? (
                  <MyTextField
                    placeholder="Wer hat empfohlen?"
                    name="empfehlung"
                    type="empfehlung"
                    label="Wer hat empfohlen?"
                  />
                ) : null}
                <Button className="button" type="submit" variant="contained">
                  Register
                </Button>
                <Link to="/login">
                  <p className="text">Einlogen</p>
                </Link>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.userReducer.isAuthenticated,
  error: state.errorReducer.msg,
  user: state.userReducer,
});

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ register, clearErrors }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Register);
