import React from "react";
import { useField } from "formik";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import FormHelperText from "@material-ui/core/FormHelperText";

const MySelectField = ({ label, placeholder, type, ...props }) => {
  const [field, meta] = useField(props);
  const errorText = meta.error && meta.touched ? meta.error : null;

  return (
    <FormControl variant="outlined" style={{ width: "46.5%" }}>
      <InputLabel id="outlined-adornment-amount">Zahlweise</InputLabel>
      <Select
        {...field}
        label={label}
        placeholder={placeholder}
        variant="outlined"
        type={type}
        error={errorText ? true : false} //or error={!!errorText}
      >
        <MenuItem value="Monatlich">Monatlich</MenuItem>
        <MenuItem value="Vierteljährlich">Vierteljährlich</MenuItem>
        <MenuItem value="Halbjährlich">Halbjährlich</MenuItem>
        <MenuItem value="Jährlich">Jährlich</MenuItem>
        <MenuItem value="Einmalig">Einmalig</MenuItem>
      </Select>
      {errorText ? (
        <FormHelperText style={{ color: "#f44336" }}>
          Dieses Field ist erforderlich
        </FormHelperText>
      ) : null}
    </FormControl>
  );
};

export default MySelectField;
