import React, { Fragment, useEffect, useState } from "react";
import DonutChart from "react-svg-donut-chart";
import { connect } from "react-redux";

import TopNav from "../vertragsUebersicht/Header/Nav/TopNav";
import SideBar from "../vertragsUebersicht/Sidebar/Sidebar";
import Header from "../vertragsUebersicht/Header/Header";
import InfoPanel from "../vertragsUebersicht/InfoPanel.js/InfoPanel";
import "./Statistiken.scss";
import StatistkElement from "./StatistikElement/StatistkElement";
import StatistikSlider from "./StatistikSlider/StatistikSlider";

const Statistiken = ({ openSidebar, showSidebar, user, fullVertrag }) => {
  const [totalKosten, setTotalKosten] = useState();
  let [total, setTotal] = useState([0]);
  let [energyKosten, setEnergyKosten] = useState([]);
  let [contactKosten, setContactKosten] = useState([]);
  let [gesundKosten, setGesundKosten] = useState([]);
  let [geldKosten, setGeldKosten] = useState([]);
  let [versicherungKosten, setVersicherungKosten] = useState([]);
  const [energyKostenProMonat, setEnergyKostenProMonat] = useState(0);
  const [contactKostenProMonat, setContactKostenProMonat] = useState(0);
  const [gesundKostenProMonat, setGesundKostenProMonat] = useState(0);
  const [geldKostenProMonat, setGeldKostenProMonat] = useState(0);
  const [versicherungKostenProMonat, setVersicherungKostenProMonat] = useState(
    0
  );
  const [
    energyVertragNameWithKosten,
    setEnergyVertragNameWithKosten,
  ] = useState([]);
  const [
    contactVertragNameWithKosten,
    setContactVertragNameWithKosten,
  ] = useState([]);
  const [
    gesundVertragNameWithKosten,
    setGesundVertragNameWithKosten,
  ] = useState([]);
  const [geldVertragNameWithKosten, setGeldVertragNameWithKosten] = useState(
    []
  );
  const [
    versicherungVertragNameWithKosten,
    setVersicherungVertragNameWithKosten,
  ] = useState([]);

  const logo = require(`../../assets/images/logo1.png`);

  useEffect(() => {
    // eslint-disable-next-line
    total = [0];
    fullVertrag.map(
      (
        el // eslint-disable-next-line
      ) => {
        if (el.zahlweise === "Monatlich") {
          const kosten = el.kosten;
          setTotal((total = [...total, kosten]));
        } else if (el.zahlweise === "Vierteljährlich") {
          const kosten = el.kosten / 3;
          setTotal((total = [...total, kosten]));
        } else if (el.zahlweise === "Halbjährlich") {
          const kosten = el.kosten / 6;
          setTotal((total = [...total, kosten]));
        } else if (el.zahlweise === "Jährlich") {
          const kosten = el.kosten / 12;
          setTotal((total = [...total, kosten]));
        } else {
          const kosten = 0;
          setTotal((total = [...total, kosten]));
        }
      }
    );
  }, [fullVertrag]);

  useEffect(
    () => {
      // eslint-disable-next-line
      fullVertrag.map((el) => {
        if (el.vertrag_name === "Gas" || el.vertrag_name === "Strom") {
          let energyFilteredArray = [];
          energyFilteredArray = energyKosten.filter(
            (el2) => el2.vertrag_id !== el.vertrag_id
          );

          energyFilteredArray.push(el);
          // eslint-disable-next-line
          energyKosten = energyFilteredArray;
          setEnergyKosten(energyFilteredArray);
        } else if (
          el.vertrag_name === "Mobiles Internet" ||
          el.vertrag_name === "Internet" ||
          el.vertrag_name === "Kabel-Fernsehen" ||
          el.vertrag_name === "Handy" ||
          el.vertrag_name === "Pay-TV" ||
          el.vertrag_name === "Festnetz" ||
          el.vertrag_name === "Kabel Internet" ||
          el.vertrag_name === "Online-Games & Konsole" ||
          el.vertrag_name === "Automobil-Club"
        ) {
          let contactFilteredArray = [];

          contactFilteredArray = contactKosten.filter(
            (el2) => el2.vertrag_id !== el.vertrag_id
          );
          contactFilteredArray.push(el);
          // eslint-disable-next-line
          contactKosten = contactFilteredArray;
          setContactKosten(contactFilteredArray);
        } else if (
          el.vertrag_name === "Krankenversicherung" ||
          el.vertrag_name === "Zahnzusatz-Versicherung" ||
          el.vertrag_name === "Patientenverfügung" ||
          el.vertrag_name === "Lebensversicherung" ||
          el.vertrag_name === "Riester Rente" ||
          el.vertrag_name === "Fitnessstudio" ||
          el.vertrag_name === "Krankenzusatz-Versicherung" ||
          el.vertrag_name === "Unfallversicherung" ||
          el.vertrag_name === "Sterbegeld-Versicherung" ||
          el.vertrag_name === "Pflegeversicherung" ||
          el.vertrag_name === "Betriebliche Altersvorsorge" ||
          el.vertrag_name === "Rürup Rente"
        ) {
          let gesundFilteredArray = [];

          gesundFilteredArray = gesundKosten.filter(
            (el2) => el2.vertrag_id !== el.vertrag_id
          );
          gesundFilteredArray.push(el);
          // eslint-disable-next-line
          gesundKosten = gesundFilteredArray;
          setGesundKosten(gesundFilteredArray);
        } else if (
          el.vertrag_name === "Rückabwicklung" ||
          el.vertrag_name === "Kredit" ||
          el.vertrag_name === "Tagesgeldkonto" ||
          el.vertrag_name === "Festgeldkonto" ||
          el.vertrag_name === "Sparkonto" ||
          el.vertrag_name === "Depot"
        ) {
          let geldFilteredArray = [];

          geldFilteredArray = geldKosten.filter(
            (el2) => el2.vertrag_id !== el.vertrag_id
          );
          geldFilteredArray.push(el);
          // eslint-disable-next-line
          geldKosten = geldFilteredArray;
          setGeldKosten(geldFilteredArray);
        } else if (
          el.vertrag_name === "Bauherrenhaftpflicht" ||
          el.vertrag_name === "KFZ-Versicherung" ||
          el.vertrag_name === "Rechtsschutz" ||
          el.vertrag_name === "Hausrat" ||
          el.vertrag_name === "Haftpflicht" ||
          el.vertrag_name === "Gebäudeversicherung" ||
          el.vertrag_name === "Reiseversicherung" ||
          el.vertrag_name === "Motorrad-Versicherung" ||
          el.vertrag_name === "Tierhalterhaftpflicht" ||
          el.vertrag_name === "Grundbesitzer-Haftpflicht" ||
          el.vertrag_name === "Berufsunfähigkeit" ||
          el.vertrag_name === "Auslandsversicherung" ||
          el.vertrag_name === "Elektrogeräte-Versicherung" ||
          el.vertrag_name === "Bausparvertrag" ||
          el.vertrag_name === "Bootsversicherung" ||
          el.vertrag_name === "Fahrrad-Versicherung" ||
          el.vertrag_name === "Glasversicherung"
        ) {
          let versicherungFilteredArray = [];

          versicherungFilteredArray = versicherungKosten.filter(
            (el2) => el2.vertrag_id !== el.vertrag_id
          );

          versicherungFilteredArray.push(el);
          // eslint-disable-next-line
          versicherungKosten = versicherungFilteredArray;
          setVersicherungKosten(versicherungFilteredArray);
        }
      });
    }, // eslint-disable-next-line
    [fullVertrag]
  );

  useEffect(() => {
    setTotalKosten(total.reduce((acc, curr) => acc + curr));
  }, [total, fullVertrag]);

  const dataPie = [
    { value: gesundKostenProMonat, stroke: "#F8AC18", strokeWidth: 2 },
    { value: energyKostenProMonat, stroke: "#8FBC26", strokeWidth: 2 },
    { value: versicherungKostenProMonat, stroke: "#CB2E21", strokeWidth: 2 },
    { value: geldKostenProMonat, stroke: "#00B0F0", strokeWidth: 2 },
    { value: contactKostenProMonat, stroke: "#a43288", strokeWidth: 2 },
  ];
  return (
    <Fragment>
      <TopNav openSidebar={openSidebar} />
      <SideBar
        openSidebar={openSidebar}
        showSidebar={showSidebar}
        user={user}
      />
      <div id="mainVS">
        <Header text={"Statistiken:"} />
        <InfoPanel />
        <div className="donutsContainer">
          <div className="donutsContainerFirstBox" id="donutsContainerFirstBox">
            <DonutChart className="donutChart" data={dataPie} />
            <img className="image" src={logo} alt="logo" />
            <div className="donutsContainerFirstBoxContent">
              <p>
                Gesamt:{" "}
                {totalKosten
                  ? new Intl.NumberFormat("de-DE", {
                      style: "currency",
                      currency: "EUR",
                    }).format(totalKosten)
                  : "0 €"}
              </p>
              <ul>
                <StatistkElement
                  kosten={versicherungKosten}
                  color={"#CB2E21"}
                  setKosten={setVersicherungKostenProMonat}
                  setNameWithKosten={setVersicherungVertragNameWithKosten}
                />
                <StatistkElement
                  kosten={geldKosten}
                  color={"#00B0F0"}
                  setKosten={setGeldKostenProMonat}
                  setNameWithKosten={setGeldVertragNameWithKosten}
                />
                <StatistkElement
                  kosten={contactKosten}
                  color={"#a43288"}
                  setKosten={setContactKostenProMonat}
                  setNameWithKosten={setContactVertragNameWithKosten}
                />
                <StatistkElement
                  kosten={gesundKosten}
                  color={"#F8AC18"}
                  setKosten={setGesundKostenProMonat}
                  setNameWithKosten={setGesundVertragNameWithKosten}
                />
                <StatistkElement
                  kosten={energyKosten}
                  color={"#8FBC26"}
                  setKosten={setEnergyKostenProMonat}
                  setNameWithKosten={setEnergyVertragNameWithKosten}
                />
              </ul>
            </div>
          </div>
          <div
            className="donutsContainerSecondBox"
            id="donutsContainerSecondBox"
          >
            <StatistikSlider
              energyKosten={energyKostenProMonat}
              gesundKosten={gesundKostenProMonat}
              contactKosten={contactKostenProMonat}
              geldKosten={geldKostenProMonat}
              versicherungKosten={versicherungKostenProMonat}
              energyVertragNameWithKosten={energyVertragNameWithKosten}
              contactVertragNameWithKosten={contactVertragNameWithKosten}
              gesundVertragNameWithKosten={gesundVertragNameWithKosten}
              geldVertragNameWithKosten={geldVertragNameWithKosten}
              versicherungVertragNameWithKosten={
                versicherungVertragNameWithKosten
              }
            />
          </div>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  user: state.userReducer.user,
  fullVertrag: state.fullVertragReducer.fullVertrags,
});

export default connect(mapStateToProps)(Statistiken);
