import React, { useState, useEffect } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Formik, Form } from "formik";
import Button from "@material-ui/core/Button";
import * as Yup from "yup";
import { Link } from "react-router-dom";

import MyTextField from "../../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextField";
import { passwortVergessen } from "../../../redux/index";
import "../Login/Login.scss";
import "../Register/Register.scss";

const SignupSchema = Yup.object().shape({
  email: Yup.string().email().required("Dieses Feld ist erforderlich"),
});

export const PasswortVergessen = ({ passwortVergessen, error }) => {
  const [state, setState] = useState("");

  useEffect(() => {
    setState(error);
  }, [error]);

  const backgroundImage = require(`../../../assets/images/logo1.png`);
  const backgroundImage2 = require(`../../../assets/images/logo.png`);
  return (
    <div className="loginWrapper">
      <div className="loginContainer">
        <div
          className="logo1"
          style={{
            backgroundImage: `url(${backgroundImage})`,
          }}
        ></div>
        <div
          className="logo2"
          style={{
            backgroundImage: `url(${backgroundImage})`,
          }}
        ></div>
        <div className="loginContentContainer">
          <div className="my-text-field">
            <div
              className="logo"
              style={{
                backgroundImage: `url(${backgroundImage2})`,
              }}
            ></div>
            <h4
              style={{ color: "#A43288", textAlign: "center", margin: "0px" }}
            >
              Passwort vergessen?
            </h4>
            {typeof state !== "string" ? (
              <h5  style={{ color: "#A43288", textAlign: "center" }}>
                Kein Problem, wir senden Anweisungen an Ihren Posteingang, um
                Ihr Passwort zurückzusetzen.
              </h5>
            ) : null}
            {state ===
            "Besuchen Sie Ihre Mailbox, an die wir den Link zum Zurücksetzen des Passworts gesendet haben" ? (
              <h4 className="alert-info2">{state}</h4>
            ) : null}
            {state === "Benutzer mit dieser E-Mail-Adresse existiert nicht" ? (
              <h4
                className="alert-info"
                style={{ paddingTop: "20px", paddingBottom: "10px" }}
              >
                {state}
              </h4>
            ) : null}
          </div>
          <Formik
            initialValues={{
              email: "",
            }}
            validationSchema={SignupSchema}
            onSubmit={(data) => {
              passwortVergessen(data);
            }}
          >
            {() => (
              <Form
                style={{
                  display: "flex",
                  flexDirection: "column",
                  width: "300px",
                  margin: "auto",
                }}
              >
                <MyTextField
                  placeholder="E-mail"
                  name="email"
                  type="email"
                  label="E-mail"
                />
                <Button type="submit" variant="contained" className="button">
                  Passwort zurücksetzen
                </Button>
                <Link to="/login">
                  <p className="text">Einlogen</p>
                </Link>
                <Link to="/register">
                  <p className="text">Ein Konto anlegen</p>
                </Link>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => ({
  error: state.errorReducer.msg,
});
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ passwortVergessen }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(PasswortVergessen);
