import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import Popup from "reactjs-popup";
import { Formik, Form } from "formik";
import { Button } from "@material-ui/core";
import * as Yup from "yup";
import MyTextField from "../../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextField";
import { Redirect, useParams, Link } from "react-router-dom";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import { bindActionCreators } from "redux";

import {
  getVertrag,
  addGesellschaft,
  getAllGesellschaften,
  getGesellschaftById,
  getGesellschaften,
  getVertragsArray,
  clearGesellschaftById,
} from "../../../redux/index";

const SignupSchema = Yup.object().shape({
  name: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  anschrift: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  telefonnummer: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  fax: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  email: Yup.string()
    .email()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  seite: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  plz: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  ort: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
});

const ManageGesellschaft = ({
  getVertrag,
  addGesellschaft,
  getAllGesellschaften,
  getGesellschaftById,
  gesellschaftByIdReducer,
  getGesellschaften,
  getVertragsArray,
  clearGesellschaftById,
}) => {
  const [spartenList, setSpartenList] = useState([]);
  const [spartenList2, setSpartenList2] = useState([]);
  const [gesellschaftById, setGesellschaftById] = useState({});
  const [redirect, setRedirect] = useState(false);

  const { id } = useParams();

  useEffect(() => {
    getGesellschaftById(id);
  }, [id, getGesellschaftById]);

  useEffect(() => {
    setGesellschaftById(gesellschaftByIdReducer);
  }, [gesellschaftByIdReducer]);

  useEffect(() => {
    if (gesellschaftById.gesellschaft_id) {
      if (gesellschaftById.attached_to_sparte_id !== "") {
        let sparten = gesellschaftById.attached_to_sparte_id.split(",");
        setSpartenList2(sparten);
        let sparten2 = [];
        // eslint-disable-next-line
        sparten.map((el) => {
          if (el === "'1'") {
            sparten2.push(["'1'", "Mobiles Internet"]);
          } else if (el === "'2'") {
            sparten2.push(["'2'", "Internet"]);
          } else if (el === "'3'") {
            sparten2.push(["'3'", "Gas"]);
          } else if (el === "'4'") {
            sparten2.push(["'4'", "Kabel-Fernsehen"]);
          } else if (el === "'5'") {
            sparten2.push(["'5'", "Krankenversicherung"]);
          } else if (el === "'6'") {
            sparten2.push(["'6'", "Handy"]);
          } else if (el === "'7'") {
            sparten2.push(["'7'", "Strom"]);
          } else if (el === "'8'") {
            sparten2.push(["'8'", "Pay-TV"]);
          } else if (el === "'9'") {
            sparten2.push(["'9'", "Zahnzusatz-Versicherung"]);
          } else if (el === "'11'") {
            sparten2.push(["'11'", "Festnetz"]);
          } else if (el === "'12'") {
            sparten2.push(["'12'", "Kabel-Internet"]);
          } else if (el === "'13'") {
            sparten2.push(["'13'", "Patientenverfügung"]);
          } else if (el === "'14'") {
            sparten2.push(["'14'", "Lebensversicherung"]);
          } else if (el === "'15'") {
            sparten2.push(["'15'", "Riester Rente"]);
          } else if (el === "'16'") {
            sparten2.push(["'16'", "Rückabwicklung"]);
          } else if (el === "'17'") {
            sparten2.push(["'17'", "Fitnessstudio"]);
          } else if (el === "'18'") {
            sparten2.push(["'18'", "Kredit"]);
          } else if (el === "'19'") {
            sparten2.push(["'19'", "Bauherrenhaftpflicht"]);
          } else if (el === "'24'") {
            sparten2.push(["'24'", "KFZ-Versicherung"]);
          } else if (el === "'26'") {
            sparten2.push(["'26'", "Rechtsschutz"]);
          } else if (el === "'28'") {
            sparten2.push(["'28'", "Hausrat"]);
          } else if (el === "'30'") {
            sparten2.push(["'30'", "Haftpflicht"]);
          } else if (el === "'32'") {
            sparten2.push(["'32'", "Krankenzusatz-Versicherung"]);
          } else if (el === "'33'") {
            sparten2.push(["'33'", "Gebäudeversicherung"]);
          } else if (el === "'34'") {
            sparten2.push(["'34'", "Reiseversicherung"]);
          } else if (el === "'35'") {
            sparten2.push(["'35'", "Unfallversicherung"]);
          } else if (el === "'36'") {
            sparten2.push(["'36'", "Motorrad-Versicherung"]);
          } else if (el === "'37'") {
            sparten2.push(["'37'", "Sterbegeld-Versicherung"]);
          } else if (el === "'39'") {
            sparten2.push(["'39'", "Tierhalterhaftpflicht"]);
          } else if (el === "'41'") {
            sparten2.push(["'41'", "Tagesgeldkonto"]);
          } else if (el === "'43'") {
            sparten2.push(["'43'", "Berufsunfähigkeit"]);
          } else if (el === "'45'") {
            sparten2.push(["'45'", "Festgeldkonto"]);
          } else if (el === "'46'") {
            sparten2.push(["'46'", "Sparkonto"]);
          } else if (el === "'47'") {
            sparten2.push(["'47'", "Grundbesitzer-Haftpflicht"]);
          } else if (el === "'48'") {
            sparten2.push(["'48'", "Pflegeversicherung"]);
          } else if (el === "'49'") {
            sparten2.push(["'49'", "Automobil-Club"]);
          } else if (el === "'50'") {
            sparten2.push(["'50'", "Betriebliche Altersvorsorge"]);
          } else if (el === "'51'") {
            sparten2.push(["'51'", "Auslandsversicherung"]);
          } else if (el === "'52'") {
            sparten2.push(["'52'", "Elektrogeräte-Versicherung"]);
          } else if (el === "'53'") {
            sparten2.push(["'53'", "Bausparvertrag"]);
          } else if (el === "'54'") {
            sparten2.push(["'54'", "Rürup Rente"]);
          } else if (el === "'55'") {
            sparten2.push(["'55'", "Bootsversicherung"]);
          } else if (el === "'56'") {
            sparten2.push(["'56'", "Fahrrad-Versicherung"]);
          } else if (el === "'57'") {
            sparten2.push(["'57'", "Glasversicherung"]);
          } else if (el === "'58'") {
            sparten2.push(["'58'", "Depot"]);
          } else if (el === "'49'") {
            sparten2.push(["'49'", "Automobil-Club"]);
          }
        });
        setSpartenList(sparten2);
      }
    }
  }, [gesellschaftById]);

  const handleSparten = (e) => {
    if (typeof e.target.value !== "string") {
      return null;
    } else {
      const sparteName = e.target.value.split(" ");
      setSpartenList([...spartenList, sparteName]);
      setSpartenList2([...spartenList2, sparteName[0]]);
    }
  };

  const handleDeleteEl = (e) => {
    setSpartenList(spartenList.filter((el) => el[1] !== e[1]));
    setSpartenList2(spartenList2.filter((el) => el !== e[0]));
  };

  const handleChangeGesellschaft = (values) => {
    setRedirect(true);
    addGesellschaft(values, id);
    getAllGesellschaften();
    getGesellschaften();
    getVertragsArray();
    getVertrag();
    clearGesellschaftById();
  };

  const handleClick = () => {
    clearGesellschaftById();
  };

  if (gesellschaftById.gesellschaft_id) {
    return (
      <Popup
        modal
        closeOnDocumentClick={false}
        defaultOpen={true}
        closeOnEscape={false}
      >
        <Formik
          initialValues={{
            name: gesellschaftById.field_gesellschaft_name_value,
            anschrift: gesellschaftById.field_gesellschaft_strasse_value,
            plz: gesellschaftById.field_gesellschaft_plz_value,
            ort: gesellschaftById.field_gesellschaft_ort_value,
            telefonnummer: gesellschaftById.field_gesellschaft_telefon_value,
            fax: gesellschaftById.field_gesellschaft_fax_value,
            email: gesellschaftById.field_gesellschaft_email_value,
            seite: gesellschaftById.field_gesellschaft_internet_value,
            sparte: "",
          }}
          enableReinitialize={true}
          validationSchema={SignupSchema}
          onSubmit={(values) => {
            values.sparte = spartenList2;
            handleChangeGesellschaft(values);
          }}
        >
          {({ values }) => (
            <Form
              style={{
                backgroundColor: "white",
                display: "flex",
                flexDirection: "column",
                width: "auto",
                margin: "auto",
              }}
            >
              <Link
                style={{
                  display: "flex",
                  justifyContent: "flex-end",
                  margin: "20px",
                }}
                to="/admin"
                onClick={() => handleClick()}
              >
                <ExitToAppIcon />
              </Link>
              <MyTextField
                placeholder="Name"
                name="name"
                type="text"
                label="Name"
              />
              <MyTextField
                placeholder="Anschrift"
                name="anschrift"
                type="text"
                label="Anschrift"
              />
              <MyTextField
                placeholder="PLZ"
                name="plz"
                type="text"
                label="PLZ"
              />
              <MyTextField
                placeholder="Ort"
                name="ort"
                type="text"
                label="Ort"
              />
              <MyTextField
                placeholder="Telefonnummer"
                name="telefonnummer"
                type="text"
                label="Telefonnummer"
              />
              <MyTextField
                placeholder="Web Seite"
                name="seite"
                type="text"
                label="Web Seite"
              />
              <MyTextField
                placeholder="Fax"
                name="fax"
                type="text"
                label="Fax"
              />
              <MyTextField
                placeholder="E-Mail"
                name="email"
                type="email"
                label="E-Mail"
              />
              <FormControl variant="outlined">
                <InputLabel id="demo-simple-select-filled-label">
                  Sparte
                </InputLabel>
                <Select
                  name="sparte"
                  labelId="demo-simple-select-outlined-label"
                  id="demo-simple-select-outlined"
                  label="Sparte"
                  value={values.sparte}
                  onClick={(e) => handleSparten(e)}
                >
                  <MenuItem value="'1' Mobiles Internet">
                    Mobiles Internet
                  </MenuItem>
                  <MenuItem value="'2' Internet">Internet</MenuItem>
                  <MenuItem value="'3' Gas">Gas</MenuItem>
                  <MenuItem value="'4' Kabel-Fernsehen">
                    Kabel-Fernsehen
                  </MenuItem>
                  <MenuItem value="'5' Krankenversicherung">
                    Krankenversicherung
                  </MenuItem>
                  <MenuItem value="'6' Handy">Handy</MenuItem>
                  <MenuItem value="'7' Strom">Strom</MenuItem>
                  <MenuItem value="'8' Pay-TV">Pay-TV</MenuItem>
                  <MenuItem value="'9' Zahnzusatz-Versicherung">
                    Zahnzusatz-Versicherung
                  </MenuItem>
                  <MenuItem value="'11' Festnetz">Festnetz</MenuItem>
                  <MenuItem value="'12' Kabel-Internet">
                    Kabel-Internet
                  </MenuItem>
                  <MenuItem value="'13' Patientenverfügung">
                    Patientenverfügung
                  </MenuItem>
                  <MenuItem value="'14' Lebensversicherung">
                    Lebensversicherung
                  </MenuItem>
                  <MenuItem value="'15' Riester Rente">Riester Rente</MenuItem>
                  <MenuItem value="'16' Rückabwicklung">
                    Rückabwicklung
                  </MenuItem>
                  <MenuItem value="'17' Fitnessstudio">Fitnessstudio</MenuItem>
                  <MenuItem value="'18' Kredit">Kredit</MenuItem>
                  <MenuItem value="'19' Bauherrenhaftpflicht">
                    Bauherrenhaftpflicht
                  </MenuItem>
                  <MenuItem value="'24' KFZ-Versicherung">
                    KFZ-Versicherung
                  </MenuItem>
                  <MenuItem value="'26' Rechtsschutz">Rechtsschutz</MenuItem>
                  <MenuItem value="'28' Hausrat">Hausrat</MenuItem>
                  <MenuItem value="'30' Haftpflicht">Haftpflicht</MenuItem>
                  <MenuItem value="'32' Krankenzusatz-Versicherung">
                    Krankenzusatz-Versicherung
                  </MenuItem>
                  <MenuItem value="'33' Gebäudeversicherung">
                    Gebäudeversicherung
                  </MenuItem>
                  <MenuItem value="'34' Reiseversicherung">
                    Reiseversicherung
                  </MenuItem>
                  <MenuItem value="'35' Unfallversicherung">
                    Unfallversicherung
                  </MenuItem>
                  <MenuItem value="'36' Motorrad-Versicherung">
                    Motorrad-Versicherung
                  </MenuItem>
                  <MenuItem value="'37' Sterbegeld-Versicherung">
                    Sterbegeld-Versicherung
                  </MenuItem>
                  <MenuItem value="'39' Tierhalterhaftpflicht">
                    Tierhalterhaftpflicht
                  </MenuItem>
                  <MenuItem value="'41' Tagesgeldkonto">
                    Tagesgeldkonto
                  </MenuItem>
                  <MenuItem value="'43' Berufsunfähigkeit">
                    Berufsunfähigkeit
                  </MenuItem>
                  <MenuItem value="'45' Festgeldkonto">Festgeldkonto</MenuItem>
                  <MenuItem value="'46' Sparkonto">Sparkonto</MenuItem>
                  <MenuItem value="'47' Grundbesitzer-Haftpflicht">
                    Grundbesitzer-Haftpflicht
                  </MenuItem>
                  <MenuItem value="'48' Pflegeversicherung">
                    Pflegeversicherung
                  </MenuItem>
                  <MenuItem value="'49' Automobil-Club">
                    Automobil-Club
                  </MenuItem>
                  <MenuItem value="'50' Betriebliche Altersvorsorge">
                    Betriebliche Altersvorsorge
                  </MenuItem>
                  <MenuItem value="'51' Auslandsversicherung">
                    Auslandsversicherung
                  </MenuItem>
                  <MenuItem value="'52' Elektrogeräte-Versicherung">
                    Elektrogeräte-Versicherung
                  </MenuItem>
                  <MenuItem value="'53' Bausparvertrag">
                    Bausparvertrag
                  </MenuItem>
                  <MenuItem value="'54' Rürup Rente">Rürup Rente</MenuItem>
                  <MenuItem value="'55' Bootsversicherung">
                    Bootsversicherung
                  </MenuItem>
                  <MenuItem value="'56' Fahrrad-Versicherung">
                    Fahrrad-Versicherung
                  </MenuItem>
                  <MenuItem value="'57' Glasversicherung">
                    Glasversicherung
                  </MenuItem>
                  <MenuItem value="'58' Depot">Depot</MenuItem>
                </Select>
              </FormControl>
              {spartenList.map((el, i) => (
                <li
                  style={{
                    listStyle: "none",
                    margin: "5px",
                    display: "flex",
                    justifyContent: "space-between",
                    cursor: "pointer",
                  }}
                  key={i}
                >
                  {el[1]}
                  <DeleteForeverIcon onClick={() => handleDeleteEl(el)} />
                </li>
              ))}
              <br />
              <Button type="submit" variant="contained" color="primary">
                Speichern
              </Button>
              {redirect ? <Redirect to="/admin" /> : null}
            </Form>
          )}
        </Formik>
      </Popup>
    );
  } else {
    return null;
  }
};

const mapStateToProps = (state) => ({
  gesellschaftByIdReducer: state.gesellschaftenReducer.gesellschaftById,
});

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      addGesellschaft,
      getAllGesellschaften,
      getGesellschaftById,
      getGesellschaften,
      getVertragsArray,
      clearGesellschaftById,
      getVertrag,
    },
    dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(ManageGesellschaft);
