import axios from "axios";
import { tokenConfig } from "../index";
import {
  GET_ALL_GESELLSCHAFTEN,
  ALL_GESELLSCHAFTEN_LOADING,
  DELETE_GESELLSCHAFT,
  GET_GESELLSCHAFT_BY_ID,
  CLEAR_GESELLSCHAFT_BY_ID,
} from "./gesellschaftenTypes";

export const addNewGesellschaft = (data) => (dispatch, getState) => {
  axios({
    method: "post",
    url: `/addNewGesellschaft`,
    headers: tokenConfig(getState).headers,
    data: { newGesselschaft: data },
  }).catch((err) => console.log(err));
};

export const getGesellschaftById = (id) => (dispatch, getState) => {
  dispatch(gesellschaftenLoading());
  axios({
    method: "get",
    url: `/getGesellschaftById/${id}`,
    headers: tokenConfig(getState).headers,
  })
    .then((res) => {
      const gesObj = res.data.gesellschaftByID;
      Object.keys(gesObj).forEach((el) => {
        if (gesObj[el] === null) {
          gesObj[el] = "";
        }
      });
      dispatch({
        type: GET_GESELLSCHAFT_BY_ID,
        payload: gesObj,
      });
    })
    .catch((err) => console.log(err));
};

export const clearGesellschaftById = () => (dispatch) => {
  dispatch({
    type: CLEAR_GESELLSCHAFT_BY_ID,
    payload: {},
  });
};

export const addGesellschaft = (data, id) => (dispatch, getState) => {
  axios({
    method: "post",
    url: `/setGesellschaft`,
    headers: tokenConfig(getState).headers,
    data: { Gesselschaft: data, id: id },
  }).catch((err) => console.log(err));
};

export const getAllGesellschaften = () => (dispatch, getState) => {
  dispatch(gesellschaftenLoading());

  axios({
    method: "get",
    url: `/getGesellschaften`,
    headers: tokenConfig(getState).headers,
  })
    .then((res) => {
      dispatch({
        type: GET_ALL_GESELLSCHAFTEN,
        payload: res.data.gesellschaften,
      });
    })
    .catch((err) => console.log(err));
};

export const deleteGesellschaft = (id) => (dispatch, getState) => {
  axios({
    method: "delete",
    url: "/deleteGesellschaft",
    headers: tokenConfig(getState).headers,
    data: { gesId: id },
  })
    .then((res) => {
      dispatch({
        type: DELETE_GESELLSCHAFT,
        payload: res.data.gesellschaften,
      });
    })
    .catch((err) => console.log(err));
};

export const gesellschaftenLoading = () => {
  return {
    type: ALL_GESELLSCHAFTEN_LOADING,
  };
};
