import axios from "axios";
import { tokenConfig } from "../index";
import { ADD_FULLVERTRAG } from "./fullVertragTypes";
import { GET_FULLVERTRAG } from "./fullVertragTypes";
import { GET_FULLVERTRAGBYID } from "./fullVertragTypes";
import { DELETE_FULLVERTRAG } from "./fullVertragTypes";
import { CHANGE_STATUS_FULLVERTRAG } from "./fullVertragTypes";

export const getFullVertrag = () => (dispatch, getState) => {
  getState().fullVertragReducer.fullVertrags = [];
  axios
    .get("/vertragsUebersicht/getFullVertrags", tokenConfig(getState))
    .then((res) => {
      // eslint-disable-next-line
      res.data.fullVertrags.map((el) => {
        axios({
          method: "post",
          url: "/vertragsUebersicht/getFullVertragsData",
          headers: tokenConfig(getState).headers,
          data: {
            sparteId: el["vertrag_name"],
            gesellschaftId: el["vertrag_anbieter"],
          },
        })
          .then((res) => {
            el["vertrag_name"] = res.data.sparte_name;
            el["vertrag_anbieter"] = res.data.gesellschaft_name;
            dispatch({
              type: GET_FULLVERTRAG,
              payload: el,
            });
          })
          .catch((err) => console.log(err));
      });
    });
};

export const getFullVertragById = (id) => (dispatch, getState) => {
  axios.get(`/Kuendigung/${id}`, tokenConfig(getState)).then((res) => {
    // eslint-disable-next-line
    res.data.fullVertragById.map((el) => {
      const vertragById = [];
      axios({
        method: "post",
        url: "/vertragsUebersicht/getFullVertragsData",
        headers: tokenConfig(getState).headers,
        data: {
          sparteId: el["vertrag_name"],
          gesellschaftId: el["vertrag_anbieter"],
        },
      })
        .then((res) => {
          el["vertrag_name"] = res.data.sparte_name;
          el["vertrag_anbieter"] = res.data.gesellschaft_name;
          vertragById.push(el);
          dispatch({
            type: GET_FULLVERTRAGBYID,
            payload: vertragById,
          });
        })
        .catch((err) => console.log(err));
    });
  });
};

export const changeStatusOnIsGekuendigtToTrue = (id) => (
  dispatch,
  getState
) => {
  if (typeof id !== "object") {
    axios
      .patch(`/Kuendigung/${id}`, { id: id }, tokenConfig(getState))
      .then((res) => {
        getState().fullVertragReducer.fullVertrags = [];
        // eslint-disable-next-line
        res.data.fullVertrags.map((el) => {
          axios({
            method: "post",
            url: "/vertragsUebersicht/getFullVertragsData",
            headers: tokenConfig(getState).headers,
            data: {
              sparteId: el["vertrag_name"],
              gesellschaftId: el["vertrag_anbieter"],
            },
          })
            .then((res) => {
              el["vertrag_name"] = res.data.sparte_name;
              el["vertrag_anbieter"] = res.data.gesellschaft_name;
              dispatch({
                type: CHANGE_STATUS_FULLVERTRAG,
                payload: el,
              });
            })
            .catch((err) => console.log(err));
        });
      })
      .catch((err) => console.log(err));
  }
};

export const addFullVertrag = (fullVertrag) => (dispatch, getState) => {
  getState().fullVertragReducer.fullVertrags = [];
  const formData = new FormData();
  for (let i = 0; i < fullVertrag.file.length; i++) {
    formData.append("file", fullVertrag.file[i]);
  }
  const newArr = Object.entries(fullVertrag);
  newArr.map(
    (
      el // eslint-disable-next-line
    ) => {
      if (el[0] === "file") {
        // eslint-disable-next-line
        return;
      } else {
        if (el[1] === "" && el[0] !== "filesToDelete" && el[0] !== "files") {
          formData.append(el[0], "NULL");
        } else {
          formData.append(el[0], el[1]);
        }
      }
    }
  );
  console.log(fullVertrag);
  console.log(formData);
  axios
    .post("/vertragsUebersicht/contract", formData, tokenConfig(getState))
    .then((res) => {
      getState().fullVertragReducer.fullVertrags = [];
      // eslint-disable-next-line
      res.data.fullVertrags.map((el) => {
        axios({
          method: "post",
          url: "/vertragsUebersicht/getFullVertragsData",
          headers: tokenConfig(getState).headers,
          data: {
            sparteId: el["vertrag_name"],
            gesellschaftId: el["vertrag_anbieter"],
          },
        })
          .then((res) => {
            el["vertrag_name"] = res.data.sparte_name;
            el["vertrag_anbieter"] = res.data.gesellschaft_name;
            dispatch({
              type: ADD_FULLVERTRAG,
              payload: el,
            });
          })
          .catch((err) => console.log(err));
      });
    });
};

export const deleteFullVertrag = (id) => (dispatch, getState) => {
  axios
    .delete(`/vertragsUebersicht/detail/${id}`, tokenConfig(getState))
    .then((res) => {
      dispatch({
        type: DELETE_FULLVERTRAG,
        payload: id,
      });
    });
};
