import React from "react";
import { Link } from "react-router-dom";

const InfoErledigt = props => {
  if (props.submitValue) {
    return (
      <div id="right-container-info-erledigt">
        <h1>Schon erledigt!</h1>
        <p>Lehn dich ruhig zurück während wir uns um dein Anliegen kümmern.</p>
        <Link to="/vertragsUebersicht">
          <div className="info-handy-button">Zu deinen Verträgen</div>
        </Link>
      </div>
    );
  }
  return null;
};

export default InfoErledigt;
