import React, { Fragment, useEffect, useState } from "react";

const StatistkElement = ({ kosten, color, setKosten, setNameWithKosten }) => {
  let [totalKostenArr, setTotalKostenArr] = useState([]);
  let [totalKosten, setTotalKosten] = useState(0);
  let [vertragNameWithKosten, setVertragNameWithKosten] = useState([]);

  useEffect(() => {
    setKosten(totalKosten);
  }, [totalKosten, setKosten]);

  useEffect(() => {
    setNameWithKosten(vertragNameWithKosten);
  }, [vertragNameWithKosten, setNameWithKosten]);

  useEffect(() => {
    // eslint-disable-next-line
    totalKostenArr = [0];
    // eslint-disable-next-line
    vertragNameWithKosten = [];
    kosten.map(
      (
        el // eslint-disable-next-line
      ) => {
        if (el.zahlweise === "Monatlich") {
          const kosten = el.kosten;
          setTotalKostenArr((totalKostenArr = [...totalKostenArr, kosten]));
          setVertragNameWithKosten(
            (vertragNameWithKosten = [
              ...vertragNameWithKosten,
              { vertragAnbieter: el.vertrag_anbieter, kosten: el.kosten },
            ])
          );
        } else if (el.zahlweise === "Vierteljährlich") {
          const kosten = el.kosten / 3;
          setTotalKostenArr((totalKostenArr = [...totalKostenArr, kosten]));
          setVertragNameWithKosten(
            (vertragNameWithKosten = [
              ...vertragNameWithKosten,
              { vertragAnbieter: el.vertrag_anbieter, kosten: kosten },
            ])
          );
        } else if (el.zahlweise === "Halbjährlich") {
          const kosten = el.kosten / 6;
          setTotalKostenArr((totalKostenArr = [...totalKostenArr, kosten]));
          setVertragNameWithKosten(
            (vertragNameWithKosten = [
              ...vertragNameWithKosten,
              { vertragAnbieter: el.vertrag_anbieter, kosten: kosten },
            ])
          );
        } else if (el.zahlweise === "Jährlich") {
          const kosten = el.kosten / 12;
          setTotalKostenArr((totalKostenArr = [...totalKostenArr, kosten]));
          setVertragNameWithKosten(
            (vertragNameWithKosten = [
              ...vertragNameWithKosten,
              { vertragAnbieter: el.vertrag_anbieter, kosten: kosten },
            ])
          );
        } else {
          const kosten = 0;
          setTotalKostenArr((totalKostenArr = [...totalKostenArr, kosten]));
          setVertragNameWithKosten(
            (vertragNameWithKosten = [
              ...vertragNameWithKosten,
              { vertragAnbieter: el.vertrag_anbieter, kosten: kosten },
            ])
          );
        }
      }
    );
  }, [kosten]);

  useEffect(() => {
    setTotalKosten(totalKostenArr.reduce((acc, curr) => acc + curr));
  }, [totalKostenArr, kosten]);

  return (
    <Fragment>
      {totalKosten !== 0 ? (
        <li style={{ color: color }}>
          <span>
            {new Intl.NumberFormat("de-DE", {
              style: "currency",
              currency: "EUR",
            }).format(totalKosten)}
          </span>
        </li>
      ) : null}
    </Fragment>
  );
};

export default StatistkElement;
