import React, { Fragment, useState, useEffect } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import { loadUser } from "../../redux/index";
import Header from "../vertragsUebersicht/Header/Header";
import "./VertragsAuswahl.scss";
import Vertragspanel from "./Vertragspanel/Vertragspanel";
import VertragsGrid from "./vertragsGrid/VertragsGrid";
import TopNav from "../vertragsUebersicht/Header/Nav/TopNav";
import SideBar from "../vertragsUebersicht/Sidebar/Sidebar";
import SearchElement from "./SearchElement/SearchElement";

const VertragsAuswahl = ({
  handleShowMessage,
  openSidebar,
  showSidebar,
  setShowDetails,
  user,
  vertragsList2,
  vertragsGesellschaften,
}) => {
  let [vertrags, setVertrags] = useState([]);
  const [removedVertragsEl, setRemovedVertragsEl] = useState([]);
  const [vertragsList, setVertragsListElement] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [display] = useState("none");

  useEffect(() => {
    if (vertragsList2.length !== "") {
      setVertrags(vertragsList2);
    }
  }, [vertragsList2]);

  //close VertragsDetail on VertragsUebersicht
  setShowDetails(false);
  //push Vertrag element to vertragspanel
  const handleSetVertrag = (e, f) => {
    let newArray = [...vertragsList];
    if (typeof e === "string") {
      newArray.push({
        name: e,
        anbieter: f,
      });
    } else {
      newArray.push({
        name: e.target.parentNode.parentNode.id,
        anbieter: e.target.innerText,
      });
    }
    setVertragsListElement(newArray);
  };

  //remove Vertrag element from vertragspanel
  const handleRemoveVertragEl = (e) => {
    if (typeof e === "string") {
      console.log(e);
      let newArray2 = vertragsList.filter((vertrag) => vertrag.name !== e);
      setVertragsListElement(newArray2);

      //put removed element to VertragsGrid again
      let pushElToVertrList = removedVertragsEl.find((el) => el.text === e);
      vertrags.splice(pushElToVertrList.id, 0, pushElToVertrList);
      // eslint-disable-next-line
      vertrags.map((el, i) => {
        el.id = i;
        el.isOpen = false;
      });
      let removedEl = removedVertragsEl.filter((el) => el.text !== e);
      setRemovedVertragsEl(removedEl);
    } else {
      let newArray2 = vertragsList.filter(
        (vertrag) => vertrag.name !== e.target.id
      );

      setVertragsListElement(newArray2);

      let removedEl = removedVertragsEl.filter((el) => el.text !== e.target.id);
      setRemovedVertragsEl(removedEl);

      let doubleElement = false;
      // eslint-disable-next-line
      vertrags.map((el) => {
        if (el.text === e.target.id) {
          doubleElement = true;
        }
      });
      //put removed element to VertragsGrid again
      if (e.target.id.toLowerCase().includes(searchText) && !doubleElement) {
        let pushElToVertrList = removedVertragsEl.find(
          (el) => el.text === e.target.id
        );
        vertrags.splice(pushElToVertrList.id, 0, pushElToVertrList);
        // eslint-disable-next-line
        vertrags.map((el, i) => {
          el.id = i;
          el.isOpen = false;
        });

      }
    }
  };

  return (
    <Fragment>
      <TopNav openSidebar={openSidebar} />
      <SideBar
        openSidebar={openSidebar}
        user={user}
        showSidebar={showSidebar}
      />
      <div className="mainVA-header">
        <p>Vertrag hinzufügen</p>
      </div>
      <div className="after">
        <div className="inside-after"></div>
      </div>
      <div id="mainVA" className="mainVA">
        <Header text={"Bitte wählen deine Verträge aus:"} display={display} />
        <SearchElement
          removedVertragsEl={removedVertragsEl}
          setSearchText={setSearchText}
          vertragsList2={vertragsList2}
          vertrags={vertrags}
          setVertrags={setVertrags}
        />
        {vertrags.length !== 0 && vertragsGesellschaften.length !== 0 ? (
          <VertragsGrid
            setRemovedVertragsEl={setRemovedVertragsEl}
            removedVertragsEl={removedVertragsEl}
            vertrags={vertrags}
            setVertrags={setVertrags}
            handleSetVertrag={handleSetVertrag}
            vertragsGesellschaften={vertragsGesellschaften}
            handleShowMessage={handleShowMessage}
          />
        ) : null}
      </div>
      <Vertragspanel
        handleRemoveVertragEl={handleRemoveVertragEl}
        vertragsList={vertragsList}
        handleShowMessage={handleShowMessage}
      />
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  user: state.userReducer.user,
  vertragsList2: state.vertragsListReducer.vertragsList,
  vertragsGesellschaften: state.vertragsListReducer.vertragsGesellschaften,
});

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ loadUser }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(VertragsAuswahl);
