import React, { Fragment, useState, useEffect, useRef } from "react";
import axios from "axios";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";
import { bindActionCreators } from "redux";
import Popup from "reactjs-popup";
import SignaturePad from "react-signature-canvas";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import { Button } from "@material-ui/core";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";

import "./Kuendigung.scss";
import {
  getFullVertragById,
  changeStatusOnIsGekuendigtToTrue,
} from "../../redux/index";
import TopNav from "../vertragsUebersicht/Header/Nav/TopNav";
import SideBar from "../vertragsUebersicht/Sidebar/Sidebar";
import MyDatePickerField from "../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyDatePickerField";
import MyTextField from "../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextField";
import LoadingPage from "../Loading Page/LoadingPage";

const SignupSchema = Yup.object().shape({
  contactVorname: Yup.string().required("Dieses Feld ist erforderlich"),
  contactNachname: Yup.string().required("Dieses Feld ist erforderlich"),
  contactStrasse: Yup.string().required("Dieses Feld ist erforderlich"),
  contactPostleitzahl: Yup.string().required("Dieses Feld ist erforderlich"),
  contactOrt: Yup.string().required("Dieses Feld ist erforderlich"),
  contactGeburtsdatum: Yup.date()
    .required("Dieses Feld ist erforderlich")
    .typeError("Das Datum muss im richtigen Format vorliegen")
    .max(new Date(), "Geburtsdatum muss in der Vergangenheit gewesen sein"),
  contactHandynummer: Yup.string().required("Dieses Feld ist erforderlich"),
  contactEmail: Yup.string().required("Dieses Feld ist erforderlich"),
  kuendigungsTitel: Yup.string().required("Dieses Feld ist erforderlich"),
  briefText: Yup.string().required("Dieses Feld ist erforderlich"),
});

const Kuendigung = ({
  openSidebar,
  showSidebar,
  token,
  getFullVertragById,
  fullVertragById,
  user,
  handleShowMessage,
  changeStatusOnIsGekuendigtToTrue,
}) => {
  const [kuendigungsTitel, setKuendigungsTitel] = useState(
    "Kündigung des Vertrags"
  );
  const [fullVertragsName, setFullVertragById] = useState("");
  const [briefText, setBriefText] = useState("");

  const [redirect, setRedirect] = useState(false);
  const [state, setState] = useState();
  const [imageURL, setImageURL] = useState(null);
  const sigCanvas = useRef({});
  const clear = () => sigCanvas.current.clear();
  const save = () =>
    setImageURL(sigCanvas.current.getTrimmedCanvas().toDataURL("image/png"));

  const { id } = useParams();

  useEffect(() => {
    if (fullVertragById[0]) {
      setFullVertragById(fullVertragById[0].vertrag_name);
      setBriefText(
        `${"\n"}hiermit kündige ich meinen ${fullVertragsName}-Vertrag fristgerecht, hilfsweise zum nächstmöglichen Zeitpunkt. Bitte senden Sie mir eine schriftliche Bestätigung der Kündigung unter Angabe des Beendigungszeitpunktes zu. ${"\n"} ${"\n"}Mit freundlichen Grüßen.`
      );
    }
  }, [fullVertragsName, fullVertragById]);

  useEffect(() => {
    if (redirect) {
      changeStatusOnIsGekuendigtToTrue(id);
    }
  }, [redirect, changeStatusOnIsGekuendigtToTrue, id]);

  useEffect(() => {
    setState(user);
  }, [user]);

  useEffect(
    () => {
      getFullVertragById(id);
    }, // eslint-disable-next-line
    [getFullVertragById, id]
  );
  const addFullVertrags = (values) => {
    axios({
      method: "post",
      url: "/Kuendigung/:id",
      headers: { "x-auth-token": token },
      data: {
        values,
        imageURL: imageURL,
      },
    }).then(() => {
      handleShowMessage("showMessage4");
      setRedirect(true);
    });
  };

  const handleChangeKuendigungsTitel = (event) => {
    setKuendigungsTitel(event.target.value);
    if (event.target.value === "Widerruf des Vertrags") {
      setBriefText(
        `${"\n"}hiermit mache ich von meinem Widerrufsrecht Gebrauch und widerrufe meinen oben genannten Vertrag innerhalb der gesetzlichen Frist.${"\n"} ${"\n"}Ich bitte um eine schriftliche Bestätigung.${"\n"} ${"\n"}Mit freundlichen Grüßen.`
      );
    } else if (event.target.value === "Sonderkündigung wegen Erhöhung") {
      setBriefText(
        `${"\n"}Sie haben angekündigt, die Preise zu erhöhen. Ich mache von meinem gesetzlichen Sonderkündigungsrecht Gebrauch und kündige den Vertrag zum nächstmöglichen Termin..${"\n"} ${"\n"}Ich bitte um eine schriftliche Bestätigung.${"\n"} ${"\n"}Mit freundlichen Grüßen.`
      );
    } else if (event.target.value === "Kündigung des Vertrags") {
      setBriefText(
        `${"\n"}hiermit kündige ich meinen ${fullVertragsName}-Vertrag fristgerecht, hilfsweise zum nächstmöglichen Zeitpunkt. Bitte senden Sie mir eine schriftliche Bestätigung der Kündigung unter Angabe des Beendigungszeitpunktes zu. ${"\n"} ${"\n"}Mit freundlichen Grüßen.`
      );
    }
  };

  if (state !== null && state !== undefined && fullVertragById[0]) {
    return (
      <Fragment>
        <TopNav openSidebar={openSidebar} />
        <SideBar user={user} showSidebar={showSidebar} />
        <div id="mainKG">
          <div className="kuendigung-header">
            <p>
              {fullVertragById[0].vertrag_anbieter} ({" "}
              {fullVertragById[0].vertrag_name} ) kündigen
            </p>
          </div>
          <Formik
            initialValues={{
              contactVorname: state.contactVorname,
              contactNachname: user.contactNachname,
              contactStrasse: user.contactStrasse,
              contactPostleitzahl: user.contactPostleitzahl,
              contactOrt: user.contactOrt,
              contactGeburtsdatum: user.contactGeburtsdatum,
              contactHandynummer: user.contactHandynummer,
              contactEmail: user.contactEmail,
              kuendigungsTitel: kuendigungsTitel,
              briefText: briefText,
            }}
            enableReinitialize={true}
            validationSchema={SignupSchema}
            onSubmit={(values) => {
              addFullVertrags(values);
            }}
          >
            {({ values }) => (
              <Form className="kuendigung-form">
                <div className="kuendigung-form-row-right">
                  <div className="kuendigung-form-field-right">
                    <Field
                      className="dateInput"
                      name="contactGeburtsdatum"
                      component={MyDatePickerField}
                      label="Geburtsdatum"
                    />
                  </div>
                  <div className="kuendigung-form-field-right">
                    <MyTextField
                      placeholder="Handynummer"
                      name="contactHandynummer"
                      type="text"
                      label="Handynummer"
                    />
                  </div>
                </div>
                <div className="kuendigung-form-row-left">
                  <div className="kuendigung-form-field-left">
                    <MyTextField
                      placeholder="Vorname"
                      name="contactVorname"
                      type="text"
                      label="Vorname"
                    />
                    <MyTextField
                      placeholder="Nachname"
                      name="contactNachname"
                      type="text"
                      label="Nachname"
                    />
                  </div>
                  <div className="inputFieldLang">
                    <MyTextField
                      placeholder="Straße"
                      name="contactStrasse"
                      type="text"
                      label="Straße"
                    />
                  </div>
                  <div className="kuendigung-form-field-left">
                    <MyTextField
                      placeholder="Postleitzahl"
                      name="contactPostleitzahl"
                      type="text"
                      label="Postleitzahl"
                    />
                    <MyTextField
                      placeholder="Stadt"
                      name="contactOrt"
                      type="text"
                      label="Stadt"
                    />
                  </div>
                  <p>Adresse von Anbieter.....</p>
                  <br />
                  ......
                  <br />
                  ......
                  <br />
                  <br />
                  <div className="inputFieldLang">
                    <FormControl variant="outlined">
                      <InputLabel id="demo-simple-select-filled-label">
                        Kündigung Titel
                      </InputLabel>
                      <Select
                        name="kuendigungsTitel"
                        labelId="demo-simple-select-outlined-label"
                        id="demo-simple-select-outlined"
                        label="Kündigung Titel"
                        value={values.kuendigungsTitel}
                        onChange={handleChangeKuendigungsTitel}
                      >
                        <MenuItem value="Kündigung des Vertrags">
                          <em>Kündigung des Vertrags</em>
                        </MenuItem>
                        <MenuItem value={"Widerruf des Vertrags"}>
                          Widerruf des Vertrags
                        </MenuItem>
                        <MenuItem value={"Sonderkündigung wegen Erhöhung"}>
                          Sonderkündigung wegen Erhöhung
                        </MenuItem>
                      </Select>
                    </FormControl>
                  </div>
                  <div
                    style={{ marginTop: "20px" }}
                    className="kuendigung-form-field-left"
                  >
                    Sehr geehrte Damen und Herren,
                  </div>
                  <br />
                  <div className="kuendigung-form-field-left">
                    <Field
                      component="textarea"
                      className="kuendigung-text"
                      maxLength="1000"
                      name="briefText"
                      rows="10"
                      cols="30"
                    />
                  </div>
                  <br />
                  <div className="kuendigung-form-field-left">
                    <Popup
                      className="popup"
                      modal
                      trigger={
                        <Button variant="outlined" color="primary">
                          Hier unterschreiben:
                        </Button>
                      }
                      closeOnDocumentClick={false}
                    >
                      {(close) => (
                        <>
                          <SignaturePad
                            ref={sigCanvas}
                            canvasProps={{
                              className: "signatureCanvas",
                            }}
                          />
                          <ButtonGroup
                            variant="contained"
                            aria-label="contained primary button group"
                          >
                            <Button
                              color="primary"
                              onClick={() => {
                                save();
                                close();
                              }}
                            >
                              Speichern
                            </Button>
                            <Button color="default" onClick={clear}>
                              Leeren
                            </Button>
                            <Button color="secondary" onClick={close}>
                              Schließen
                            </Button>
                          </ButtonGroup>
                        </>
                      )}
                    </Popup>
                    <br />
                    <br />
                    {imageURL ? (
                      <img
                        src={imageURL}
                        alt="my signature"
                        className="signatureImage"
                      />
                    ) : null}
                  </div>
                  <br />
                  <div className="kuendigung-form-field-left">
                    {values.contactVorname} {values.contactNachname}
                  </div>
                </div>
                <Button
                  variant="contained"
                  color="primary"
                  className="submit-kuendigung"
                  type="submit"
                  disabled={imageURL === null}
                  onClick={changeStatusOnIsGekuendigtToTrue}
                >
                  Absenden
                </Button>
                {redirect && <Redirect to="/vertragsUebersicht" />}
              </Form>
            )}
          </Formik>
        </div>
      </Fragment>
    );
  } else {
    return <LoadingPage />;
  }
};
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    { getFullVertragById, changeStatusOnIsGekuendigtToTrue },
    dispatch
  );
};

const mapStateToProps = (state) => ({
  token: state.userReducer.token,
  fullVertragById: state.fullVertragReducer.fullVertragById,
  user: state.userReducer.user,
});
export default connect(mapStateToProps, mapDispatchToProps)(Kuendigung);
