import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { Button } from "@material-ui/core";

import "./InfoPanel.scss";

const InfoPanel = ({ fullVertrag, user }) => {
  const [userState, setUserState] = useState();
  const [totalKosten, setTotalKosten] = useState();
  let [total, setTotal] = useState([0]);

  useEffect(() => {
    setUserState(user);
  }, [user]);

  useEffect(() => {
    // eslint-disable-next-line
    total = [0];
    fullVertrag.map(
      (
        el // eslint-disable-next-line
      ) => {
        if (el.zahlweise === "Monatlich") {
          const kosten = el.kosten;
          setTotal((total = [...total, kosten]));
        } else if (el.zahlweise === "Vierteljährlich") {
          const kosten = el.kosten / 3;
          setTotal((total = [...total, kosten]));
        } else if (el.zahlweise === "Halbjährlich") {
          const kosten = el.kosten / 6;
          setTotal((total = [...total, kosten]));
        } else if (el.zahlweise === "Jährlich") {
          const kosten = el.kosten / 12;
          setTotal((total = [...total, kosten]));
        } else {
          const kosten = 0;
          setTotal((total = [...total, kosten]));
        }
      }
    );
  }, [fullVertrag]);

    useEffect(() => {
      setTotalKosten(total.reduce((acc, curr) => acc + curr));
    }, [total, fullVertrag]);

  return (
    <div className="row-info-window">
      <div className="row-center">
        <div className="row-center-1">
          <div className="row-center-content-1">
            <div className="avatar-container"></div>
            <div className="avatar-info-container">
              <p style={{ color: "black" }}>
                Hallo {userState ? userState.name : null}
                ,
                <br />
                was kann ich heute für dich tun?
              </p>
              <i>Rene, Kundenbetreuung</i>
              <Link to="/vertragsUebersicht/kontakt">
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  className="buttonInfoPanel"
                >
                  Kontakt Aufnehmen
                </Button>
              </Link>
            </div>
          </div>
        </div>
        <div className="after">
          <div className="inside-after"></div>
        </div>
        <div className="two-row-center">
          <div className="row-center-2">
            <div className="row-center-content-2">
              <div className="gesamtkosten">
                {totalKosten
                  ? new Intl.NumberFormat("de-DE", {
                      style: "currency",
                      currency: "EUR",
                    }).format(totalKosten)
                  : "0 €"}
              </div>
              <div className="gesamtkosten-text">Gesamtkosten pro Monat</div>
            </div>
          </div>
          <div className="row-center-3">
            <div className="row-center-content-3">
              <div className="laufende-vertrage">{fullVertrag.length}</div>
              <div className="laufende-vertrage-text">
                {fullVertrag.length <= 1
                  ? "Laufende Vertrag"
                  : "Laufende Verträge"}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    fullVertrag: state.fullVertragReducer.fullVertrags,
    user: state.userReducer.user,
  };
};

export default connect(mapStateToProps)(InfoPanel);
