import React from "react";
import { useField } from "formik";
import { Checkbox, FormControlLabel } from "@material-ui/core";

const MyCheckbox = ({ label, ...props }) => {
  const [field] = useField(props);
  return (
    <FormControlLabel
      labelPlacement="top"
      {...field}
      control={<Checkbox />}
      label={label}
    />
  );
};

export default MyCheckbox;
