import React from "react";
import { Link } from "react-router-dom";
import { Button } from "@material-ui/core";

import "./Header.scss";

const Header = ({ text, display }) => {
  return (
    <div className="titel">
      <span>{text}</span>
      <Link to="/vertragsAuswahl">
        <Button
          className="vertrage-hinzufugen-button"
          type="submit"
          variant="contained"
          color="primary"
          style={{
            height: "35px",
            width: "auto",
            borderRadius: "10px",
            marginTop: "5px",
            display: display,
          }}
        >
          Vertrag hinzufügen
        </Button>
      </Link>
    </div>
  );
};
export default Header;
