import React, { useState, useEffect, Fragment } from "react";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import { Link } from "react-router-dom";
import Alert from "@material-ui/lab/Alert";
import { useLocation } from "react-router-dom";
import axios from "axios";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import { getFullVertrag } from "../../../redux/index";
import AutomatischeVervollstaendigung from "./AutomatischeVervollstaendigung/AutomatischeVervollstaendigung";
import "./VertragsList.scss";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <div>{children}</div>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
}

const VertragsList = ({
  vertrag,
  fullVertrag,
  setShowDetails,
  showDetails,
  token,
  getFullVertrag,
  gesellschaftsImage,
}) => {
  const [showGekündigteVerträge, setShowGekündigteVerträge] = useState(false);
  let [vertragIsNotGekuendigt] = useState();
  let [vertragIsGekuendigt] = useState();

  useEffect(() => {
    let elementsArr = document.querySelectorAll("[aria-selected]");
    if (elementsArr.length > 0) {
      for (let i = 0; i < elementsArr.length; i++) {
        if (elementsArr[i].getAttribute("aria-selected") === "true") {
          elementsArr[i].style.backgroundColor = "#fda0e93b";
        } else {
          elementsArr[i].style.backgroundColor = "white";
        }
      }
    }
  });

  let location = useLocation();

  useEffect(() => {
    if (location.pathname.includes("gekündigteVerträge")) {
      setShowGekündigteVerträge(true);
    } else {
      setShowGekündigteVerträge(false);
    }
  }, [location]);

  if (location.pathname === "/vertragsUebersicht/gekündigteVerträge") {
    setShowDetails(false);
  }

  const useStyles = makeStyles((theme) => ({
    root: {
      backgroundColor: theme.palette.background.paper,
      maxWidth: "1100px",
      margin: "60px auto",
    },
  }));

  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = useState(0);

  vertragIsGekuendigt = fullVertrag.filter((el) => el.isGekuendigt === "true");
  vertragIsNotGekuendigt = fullVertrag.filter(
    (el) => el.isGekuendigt === "false"
  );

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  let arrId = [];
  //'Verlaengert Vertraege' for next year when date is this is same as today or later
  useEffect(
    () => {
      // eslint-disable-next-line
      fullVertrag.map((el) => {
        if (
          el.vertragsende.substr(0, 10) ===
            new Date().toISOString().slice(0, 10) ||
          el.vertragsende.substr(0, 10) <= new Date().toISOString().slice(0, 10)
        ) {
          arrId.push({ key: el.vertrag_id, value: el.vertragsende });
        }
      });
      if (arrId.length !== 0) {
        axios({
          method: "post",
          url: "/vertragVerlaengert",
          headers: { "x-auth-token": token },
          data: {
            arrId: arrId,
          },
        }).then(() => getFullVertrag());
      }
    }, // eslint-disable-next-line
    [fullVertrag]
  );

  if (showGekündigteVerträge === false) {
    return (
      <Fragment>
        <AutomatischeVervollstaendigung vertrag={vertrag} />
        <div className="VertraegsListContainer">
          <div className={classes.root}>
            <AppBar position="static" color="default">
              <Tabs
                value={value}
                onChange={handleChange}
                indicatorColor="primary"
                textColor="primary"
                variant="fullWidth"
                aria-label="full width tabs example"
              >
                <Tab
                  label={`Laufende Verträge: ` + vertragIsNotGekuendigt.length}
                  {...a11yProps(0)}
                />
                <Tab
                  label={"Davon unvollständig: " + vertrag.length}
                  {...a11yProps(1)}
                />
              </Tabs>
            </AppBar>
            <SwipeableViews
              axis={theme.direction === "rtl" ? "x-reverse" : "x"}
              index={value}
              onChangeIndex={handleChangeIndex}
              style={{ paddingTop: "10px" }}
            >
              <TabPanel value={value} index={0} dir={theme.direction}>
                <div id="vertrag-auth-box">
                  {vertragIsNotGekuendigt.length === 0 ? (
                    <Alert
                      style={{ margin: "20px auto", maxWidth: "1000px" }}
                      severity="info"
                    >
                      Sie haben keine laufenden Verträge!
                    </Alert>
                  ) : (
                    <div className="vertrag-main1">
                      {vertragIsNotGekuendigt.map((fullVertrag) => {
                        let date1 = new Date(
                          fullVertrag.vertragsanfang
                        ).getTime();
                        let date2 = new Date(
                          fullVertrag.vertragsende
                        ).getTime();
                        let date3 = new Date().getTime();
                        let date4 = Math.floor(
                          Math.abs(date1 - date2) / (1000 * 3600 * 24)
                        );
                        let date5 = Math.floor(
                          Math.abs(date3 - date1) / (1000 * 3600 * 24)
                        );
                        let date6 = Math.floor(
                          Math.abs(date2 - date3) / (1000 * 3600 * 24)
                        );
                        let howManyPercent = Math.floor((date5 / date4) * 100);

                        let imageLogo = gesellschaftsImage.filter(
                          (el) =>
                            el.field_gesellschaft_name_value ===
                            fullVertrag.vertrag_anbieter
                        );
                        let gesellschaftsImages;
                        try {
                          gesellschaftsImages = `${process.env.PUBLIC_URL}/assets/gesellschaftsImages/${imageLogo[0].gesellschaft_image}`;
                          if (!gesellschaftsImages.includes(".png")) {
                            gesellschaftsImages = require(`../../../assets/vertragsImages/${fullVertrag.vertrag_name}.png`);
                          }
                        } catch (e) {}

                        return (
                          <div
                            className="container-for-rowlist"
                            key={fullVertrag.vertrag_id}
                          >
                            <Link
                              className="row-list"
                              to={`/vertragsUebersicht/detail/${fullVertrag.vertrag_id}`}
                            >
                              <div id="image-container">
                                <div className="vertragLogo">
                                  <img
                                    className="vertragLogoImage"
                                    alt={vertrag.name}
                                    src={require(`../../../assets/vertragsImages/${fullVertrag.vertrag_name}.png`)}
                                  />
                                </div>
                                <img
                                  className="image"
                                  alt={fullVertrag.vertrag_anbieter}
                                  src={gesellschaftsImages}
                                />
                              </div>
                              <div className="rest-info-container">
                                <div
                                  className="container"
                                  style={{ justifyContent: "space-between" }}
                                >
                                  <p>{fullVertrag.vertrag_anbieter}</p>

                                  {howManyPercent >= 100 ? (
                                    <p>Vertrag ist abgelaufen.</p>
                                  ) : (
                                    <div>
                                      {date6 > 365 ? (
                                        <span>noch 1 Jahr</span>
                                      ) : date6 > 730 ? (
                                        <span>noch 2 Jahre</span>
                                      ) : date6 > 1095 ? (
                                        <span>noch 3 Jahre</span>
                                      ) : date6 > 1460 ? (
                                        <span>noch 4 Jahre</span>
                                      ) : date6 > 330 ? (
                                        <span>noch 11 Monaten</span>
                                      ) : date6 > 300 ? (
                                        <span>noch 10 Monaten</span>
                                      ) : date6 > 270 ? (
                                        <span>noch 9 Monaten</span>
                                      ) : date6 > 240 ? (
                                        <span>noch 8 Monaten</span>
                                      ) : date6 > 210 ? (
                                        <span>noch 7 Monaten</span>
                                      ) : date6 > 180 ? (
                                        <span>noch 6 Monaten</span>
                                      ) : date6 > 150 ? (
                                        <span>noch 5 Monaten</span>
                                      ) : date6 > 120 ? (
                                        <span>noch 4 Monaten</span>
                                      ) : date6 > 90 ? (
                                        <span>noch 3 Monaten</span>
                                      ) : date6 > 60 ? (
                                        <span>noch 2 Monaten</span>
                                      ) : date6 > 30 ? (
                                        <span>noch 1 Monat</span>
                                      ) : (
                                        <span>noch {date6} Tagen</span>
                                      )}
                                    </div>
                                  )}
                                </div>
                                <progress
                                  id={
                                    date4 >= 152
                                      ? "progressgreen"
                                      : date4 >= 91
                                      ? "progressyellow"
                                      : "progressred"
                                  }
                                  min="0"
                                  max={date4}
                                  value={date5}
                                ></progress>
                              </div>
                            </Link>
                          </div>
                        );
                      })}
                    </div>
                  )}
                </div>
              </TabPanel>
              <TabPanel value={value} index={1} dir={theme.direction}>
                <div id="vertrag-auth-box">
                  {vertrag.length === 0 ? (
                    <Alert
                      style={{
                        color: "black",
                        margin: "20px auto",
                        maxWidth: "1000px",
                      }}
                      severity="info"
                    >
                      Sie haben keine Verträge zum Ausfüllen!
                    </Alert>
                  ) : (
                    <div className="vertrag-main1">
                      {vertrag.map((vertrag) => {
                        let imageLogo = gesellschaftsImage.filter(
                          (el) =>
                            el.field_gesellschaft_name_value ===
                            vertrag.anbieter
                        );
                        let gesellschaftsImages;
                        try {
                          gesellschaftsImages = `${process.env.PUBLIC_URL}/assets/gesellschaftsImages/${imageLogo[0].gesellschaft_image}`;
                        } catch (e) {}
                        if (gesellschaftsImages !== undefined) {
                          if (!gesellschaftsImages.includes(".png")) {
                            gesellschaftsImages = require(`../../../assets/vertragsImages/${vertrag.name}.png`);
                          }
                        }
                        return (
                          <div
                            className="container-for-rowlist"
                            key={vertrag._id}
                          >
                            <Link
                              className="row-list"
                              to={`/vertragsUebersicht/contract/${vertrag._id}`}
                            >
                              <div id="image-container">
                                <div className="vertragLogo">
                                  <img
                                    className="vertragLogoImage"
                                    alt={vertrag.name}
                                    src={require(`../../../assets/vertragsImages/${vertrag.name}.png`)}
                                  />
                                </div>
                                <img
                                  className="image"
                                  alt={vertrag.name}
                                  src={gesellschaftsImages}
                                />
                              </div>
                              <div className="rest-info-container">
                                <p>{vertrag.name}:</p>

                                <p>{vertrag.anbieter}</p>
                              </div>
                            </Link>
                          </div>
                        );
                      })}
                    </div>
                  )}
                </div>
              </TabPanel>
            </SwipeableViews>
          </div>
        </div>
      </Fragment>
    );
  } else {
    return (
      <Fragment>
        <AutomatischeVervollstaendigung vertrag={vertrag} />
        <div className="VertraegsListContainer">
          <div className={classes.root}>
            <AppBar position="static" color="default">
              <Tabs
                value={0}
                indicatorColor="primary"
                textColor="primary"
                variant="fullWidth"
                aria-label="full width tabs example"
              >
                <Tab
                  label={
                    `Verträge bereits gekündigt: ` + vertragIsGekuendigt.length
                  }
                  {...a11yProps(0)}
                />
              </Tabs>
            </AppBar>
            <SwipeableViews
              axis={theme.direction === "rtl" ? "x-reverse" : "x"}
              onChangeIndex={handleChangeIndex}
            >
              <TabPanel dir={theme.direction}>
                <div id="vertrag-auth-box">
                  {vertragIsGekuendigt.length === 0 ? (
                    <Alert
                      style={{ margin: "20px auto", maxWidth: "1000px" }}
                      severity="info"
                    >
                      Sie haben keine Verträge bereits gekündigt!
                    </Alert>
                  ) : (
                    <div className="vertrag-main1">
                      {vertragIsGekuendigt.map((fullVertrag) => {
                        let date1 = new Date(
                          fullVertrag.vertragsanfang
                        ).getTime();
                        let date2 = new Date(
                          fullVertrag.vertragsende
                        ).getTime();
                        let date3 = new Date().getTime();
                        let date4 = Math.floor(
                          Math.abs(date1 - date2) / (1000 * 3600 * 24)
                        );
                        let date5 = Math.floor(
                          Math.abs(date3 - date1) / (1000 * 3600 * 24)
                        );
                        let howManyPercent = Math.floor((date5 / date4) * 100);
                        const vertragsendeDEFormat = fullVertrag.vertragsende
                          .substr(0, 10)
                          .split("-")
                          .join(".");

                        let imageLogo = gesellschaftsImage.filter(
                          (el) =>
                            el.field_gesellschaft_name_value ===
                            fullVertrag.vertrag_anbieter
                        );
                        let gesellschaftsImages;
                        try {
                          gesellschaftsImages = `${process.env.PUBLIC_URL}/assets/gesellschaftsImages/${imageLogo[0].gesellschaft_image}`;
                        } catch (e) {}

                        if (!gesellschaftsImages.includes(".png")) {
                          gesellschaftsImages = require(`../../../assets/vertragsImages/${fullVertrag.vertrag_name}.png`);
                        }
                        return (
                          <div
                            className="container-for-rowlist"
                            key={fullVertrag.vertrag_id}
                          >
                            <Link
                              className="row-list"
                              to={`/vertragsUebersicht/gekündigteVerträge/${fullVertrag.vertrag_id}`}
                            >
                              <div id="image-container">
                                <img
                                  className="image"
                                  alt={fullVertrag.vertrag_anbieter}
                                  src={gesellschaftsImages}
                                />
                              </div>
                              <div className="rest-info-container">
                                <div
                                  className="container"
                                  style={{ justifyContent: "space-between" }}
                                >
                                  {gesellschaftsImages ? (
                                    <img
                                      className="image1"
                                      alt={fullVertrag.vertrag_anbieter}
                                      src={gesellschaftsImages}
                                    />
                                  ) : null}
                                  {howManyPercent >= 100 ? (
                                    <p>Vertrag ist abgelaufen.</p>
                                  ) : (
                                    <div>
                                      <p>Der Vertrag is bereit gekündigt!</p>
                                      <p>
                                        Der Vertrag endet am{" "}
                                        {vertragsendeDEFormat}
                                      </p>
                                    </div>
                                  )}
                                </div>
                                <progress
                                  id={
                                    date4 >= 152
                                      ? "progressgreen"
                                      : date4 >= 91
                                      ? "progressyellow"
                                      : "progressred"
                                  }
                                  min="0"
                                  max={date4}
                                  value={date5}
                                ></progress>
                              </div>
                            </Link>
                          </div>
                        );
                      })}
                    </div>
                  )}
                </div>
              </TabPanel>
            </SwipeableViews>
          </div>
        </div>
      </Fragment>
    );
  }
};

const mapStateToProps = (state) => ({
  gesellschaftsImage: state.gesellschaftenReducer.gesellschaftenList,
  token: state.userReducer.token,
});

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getFullVertrag,
    },
    dispatch
  );
};
export default connect(mapStateToProps, mapDispatchToProps)(VertragsList);
