import {
  GET_VERTRAG,
  ADD_VERTRAG,
  DELETE_VERTRAG,
  VERTRAGS_LOADING,
} from "./vertragTypes";

const initialState = {
  vertrags: [],
  loading: false,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_VERTRAG:
      return {
        ...state,
        vertrags: [...state.vertrags, action.payload],
        loading: false,
      };

    case DELETE_VERTRAG:
      return {
        ...state,
        vertrags: state.vertrags.filter((el) => el._id !== action.payload),
      };

    case ADD_VERTRAG:
      return {
        ...state,
        vertrags: [...state.vertrags, action.payload],
      };

    case VERTRAGS_LOADING:
      return {
        ...state,
        loading: true,
      };

    default:
      return state;
  }
};

export default reducer;
