import React, { useState, useEffect } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Formik, Form } from "formik";
import Button from "@material-ui/core/Button";
import * as Yup from "yup";
import { Link, useParams } from "react-router-dom";
import axios from "axios";

import MyTextField from "../../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextField";
import { passwortVergessen } from "../../../redux/index";
import "../Login/Login.scss";
import "../Register/Register.scss";

const SignupSchema = Yup.object().shape({
  newPassword: Yup.string().required("Dieses Feld ist erforderlich"),
});

export const PasswortZuruecksetzen = ({ error }) => {
  const [state, setState] = useState(false);

  useEffect(() => {
    setState(error);
  }, [error]);

  const { token } = useParams();

  const passwortZurücksetzen = (data) => {
    axios({
      method: "patch",
      url: "/passwort-zuruecksetzen/:token",
      data: {
        token,
        newPassword: data.newPassword,
      },
    });
    setState(true);
  };

  const backgroundImage = require(`../../../assets/images/logo1.png`);
  return (
    <div className="loginWrapper">
      <div className="loginContainer">
        <div className="loginContentContainer">
          <div className="my-text-field">
            <div
              className="logo"
              style={{
                backgroundImage: `url(${backgroundImage})`,
              }}
            ></div>
            <h4 style={{ color: "#3a8169", textAlign: "center" }}>
              Bitte geben Sie ein neues Passwort ein
            </h4>
          </div>
          <Formik
            initialValues={{
              newPassword: "",
            }}
            validationSchema={SignupSchema}
            onSubmit={(data) => {
              passwortZurücksetzen(data);
            }}
          >
            {() => (
              <Form
                style={{
                  display: "flex",
                  flexDirection: "column",
                  width: "300px",
                  margin: "auto",
                }}
              >
                <MyTextField
                  placeholder="Neues Passwort"
                  name="newPassword"
                  type="password"
                  label="Neues Passwort"
                />

                <Button type="submit" variant="contained" className="button">
                  Passwort bestätigen
                </Button>
                {state === true ? (
                  <h4 style={{ textAlign: "center" }}>
                    <Link style={{ color: "#3a8169" }} to="/login">
                      Das Passwort wurde geändert. <br />
                      Einloggen
                    </Link>
                  </h4>
                ) : null}
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => ({
  error: state.errorReducer.msg,
});
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ passwortVergessen }, dispatch);
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PasswortZuruecksetzen);
