import {
  GET_ALL_GESELLSCHAFTEN,
  ALL_GESELLSCHAFTEN_LOADING,
  DELETE_GESELLSCHAFT,
  GET_GESELLSCHAFT_BY_ID,
  CLEAR_GESELLSCHAFT_BY_ID,
} from "./gesellschaftenTypes";

const initialState = {
  gesellschaftById: {},
  gesellschaftenList: [],
  loading: false,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case ALL_GESELLSCHAFTEN_LOADING:
      return {
        ...state,
        loading: true,
      };
    case GET_ALL_GESELLSCHAFTEN:
      return {
        ...state,
        gesellschaftenList: action.payload,
        loading: false,
      };
    case DELETE_GESELLSCHAFT:
      return {
        ...state,
        gesellschaftenList: action.payload,
        loading: false,
      };
    case GET_GESELLSCHAFT_BY_ID:
      return {
        ...state,
        gesellschaftById: action.payload,
        loading: false,
      };
    case CLEAR_GESELLSCHAFT_BY_ID:
      return {
        ...state,
        gesellschaftById: action.payload,
        loading: false,
      };
    default:
      return state;
  }
};

export default reducer;
