import React, { Fragment, useState, useEffect, useRef } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import InputAdornment from "@material-ui/core/InputAdornment";
import TextField from "@material-ui/core/TextField";
import SearchIcon from "@material-ui/icons/Search";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { sendGesellschaft } from "../../../../redux/index";

import "./VertragsGridElement.scss";

const VertragsGridElement = ({
  setRemovedVertragsEl,
  removedVertragsEl,
  vertrags,
  setVertrags,
  handleSetVertrag,
  vertragsGesellschaften,
  sendGesellschaft,
  handleShowMessage,
}) => {
  const [searchGesellschaft, setSearchGesellschaft] = useState("");
  const [searchGesellschaftsNumber, setSearchGesellschaftsNumber] = useState(
    ""
  );
  const matches = useMediaQuery("(max-width:900px)");

  //set gesellschaften to vertrags.vertragsList2 Array
  useEffect(
    () => {
      vertrags.map((ele) =>
        vertragsGesellschaften.map((el) =>
          el.id === ele.sparteId
            ? ele.vertragsList2.push(el.vertragsList2) &&
              ele.vertragsList.push(el.vertragsList2)
            : null
        )
      );
    }, // eslint-disable-next-line
    [vertragsGesellschaften]
  );

  const listRef = useRef(null);

  //check number of searchGesellschaft elements
  useEffect(() => {
    if (listRef.current !== null) {
      setSearchGesellschaftsNumber(listRef.current.childNodes.length);
    }
  }, [searchGesellschaft]);

  const handleSearchGesellschaft = (e) => {
    setSearchGesellschaft(e.target.value);
    setSearchGesellschaftsNumber("");
  };
  //handle open and close VertragsList
  const handleShowList = (vertragId, e) => {
    if (e.target.className === "vertragspanel-button") {
      return;
    }
    //make empty string for searching gesellschafts before open another element
    setSearchGesellschaftsNumber("");
    //close only when click on diferent element as input
    if (
      e.target.className !==
      "MuiInputBase-input MuiOutlinedInput-input MuiInputBase-inputAdornedEnd MuiOutlinedInput-inputAdornedEnd"
    ) {
      if (e.target.className !== "auswahl-list-item") {
        let newVertragsArray = [...vertrags];
        newVertragsArray[vertragId] = {
          ...newVertragsArray[vertragId],
          isOpen: !newVertragsArray[vertragId].isOpen,
        };
        //closing another opened element
        vertrags.map((vertrag) => (vertrag.isOpen = false));

        setVertrags(newVertragsArray);
        setSearchGesellschaft("");
      } else {
        //set element to VertragspanelListe
        handleSetVertrag(e);
        setSearchGesellschaft("");
      }
    }
  };
  //remove vertrag element from Grid
  const handleRemoveVertrag = (e) => {
    //creating new help Array with old ID
    const el = vertrags[e];
    // eslint-disable-next-line
    vertrags.map((vertrag) => {
      if (vertrag.text === el.text) {
        let newObj = { ...el, id: vertrag.id };
        setRemovedVertragsEl([...removedVertragsEl, newObj]);
      }
    });
    //remove Vertrag from Grid
    let newVertragArray2 = vertrags.filter((vertrag) => vertrag.id !== e);
    newVertragArray2.map((vertrag, i) => (vertrag.id = i));
    newVertragArray2.map((vertrag) => (vertrag.isOpen = false));

    vertrags = [];
    setVertrags(newVertragArray2);
  };

  const handleSendGesellschaft = (e, id, f) => {
    sendGesellschaft(searchGesellschaft, e, id);
    handleSetVertrag(
      e,
      f.target.parentNode.parentNode.childNodes[2].childNodes[1].childNodes[0]
        .value
    );

    setSearchGesellschaftsNumber("");
    handleShowMessage("showMessage5");
  };
  return (
    <Fragment>
      {vertrags.map((vertrag, i) => {
        let backgroundColor;
        let backgroundColor2;
        if (
          vertrag.sparteId === 1 ||
          vertrag.sparteId === 2 ||
          vertrag.sparteId === 4 ||
          vertrag.sparteId === 6 ||
          vertrag.sparteId === 8 ||
          vertrag.sparteId === 11 ||
          vertrag.sparteId === 12 ||
          vertrag.sparteId === 44 ||
          vertrag.sparteId === 49
        ) {
          backgroundColor = "#fda0e96c";
          backgroundColor2 = "#fda0e915";
        } else if (vertrag.sparteId === 3 || vertrag.sparteId === 7) {
          backgroundColor = "#8fbc266c";
          backgroundColor2 = "#8eba2615";
        } else if (
          vertrag.sparteId === 5 ||
          vertrag.sparteId === 9 ||
          vertrag.sparteId === 13 ||
          vertrag.sparteId === 17 ||
          vertrag.sparteId === 14 ||
          vertrag.sparteId === 15 ||
          vertrag.sparteId === 32 ||
          vertrag.sparteId === 35 ||
          vertrag.sparteId === 37 ||
          vertrag.sparteId === 48 ||
          vertrag.sparteId === 54 ||
          vertrag.sparteId === 50
        ) {
          backgroundColor = "#f8ad186c";
          backgroundColor2 = "#f8ad1815";
        } else if (
          vertrag.sparteId === 16 ||
          vertrag.sparteId === 18 ||
          vertrag.sparteId === 41 ||
          vertrag.sparteId === 45 ||
          vertrag.sparteId === 46 ||
          vertrag.sparteId === 58
        ) {
          backgroundColor = "#00b0f06c";
          backgroundColor2 = "#00b0f015";
        } else if (
          vertrag.sparteId === 19 ||
          vertrag.sparteId === 24 ||
          vertrag.sparteId === 26 ||
          vertrag.sparteId === 28 ||
          vertrag.sparteId === 30 ||
          vertrag.sparteId === 33 ||
          vertrag.sparteId === 34 ||
          vertrag.sparteId === 36 ||
          vertrag.sparteId === 39 ||
          vertrag.sparteId === 43 ||
          vertrag.sparteId === 47 ||
          vertrag.sparteId === 51 ||
          vertrag.sparteId === 52 ||
          vertrag.sparteId === 53 ||
          vertrag.sparteId === 56 ||
          vertrag.sparteId === 55 ||
          vertrag.sparteId === 57
        ) {
          backgroundColor = "#cb2f216c";
          backgroundColor2 = "#ca2f2115";
        }

        return (
          <div
            onClick={(e) => handleShowList(vertrag.id, e)}
            key={i}
            id={
              vertrag.isOpen && matches
                ? "box-isOpenAndMatch"
                : vertrag.isOpen && !matches
                ? "box-isOpenAndNoMatch"
                : "box"
            }
            className="box"
            style={vertrag.isOpen ? { zIndex: "2" } : {}}
          >
            {vertrag.isOpen ? (
              <Fragment>
                <i id="close-select">
                  <FontAwesomeIcon icon={faTimes} />
                </i>
              </Fragment>
            ) : null}
            <li
              id={vertrag.text}
              className="auswahl-card"
              style={{ backgroundColor: backgroundColor }}
            >
              <div className="background-icon">
                <img
                  alt={vertrag.text}
                  src={require(`../../../../assets/vertragsImages/${vertrag.text}.png`)}
                />
              </div>
              <div className="text-container">
                <p className="auswahl-text">{vertrag.text}</p>
              </div>
              {vertrag.isOpen ? (
                <Fragment>
                  <TextField
                    style={{
                      width: "90%",
                      marginLeft: "5%",
                      background: backgroundColor,
                      opacity: ".8",
                      borderRadius: "15px",
                    }}
                    label="Suchen..."
                    variant="outlined"
                    onChange={(e) => handleSearchGesellschaft(e)}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="start">
                          <div className="search">
                            <SearchIcon />
                          </div>
                        </InputAdornment>
                      ),
                    }}
                  />
                  {searchGesellschaftsNumber !== 0 ? (
                    searchGesellschaft !== "" ? (
                      <ul
                        ref={listRef}
                        id={vertrag.selectId}
                        className="auswahl-select"
                        style={{
                          backgroundImage: `linear-gradient(to left, ${backgroundColor}, ${backgroundColor}`,
                        }}
                      >
                        {vertrag.vertragsList2[0]
                          .filter((el) =>
                            el
                              .toLowerCase()
                              .includes(searchGesellschaft.toLowerCase())
                          )
                          .map((vertragListEl, i) => {
                            return (
                              <li
                                key={i}
                                className="auswahl-list-item"
                                onClick={() => handleRemoveVertrag(vertrag.id)}
                                style={{
                                  borderBottom: "2px solid transparent",
                                  borderLeftWidth: "0",
                                  borderTopWidth: "0",
                                  borderRightWidth: "0",
                                  borderImage: `linear-gradient(to right, ${backgroundColor2} 0%, ${backgroundColor} 50%, ${backgroundColor2} 100%)`,
                                  borderImageSlice: "1",
                                }}
                              >
                                {vertragListEl}
                              </li>
                            );
                          })}
                      </ul>
                    ) : (
                      <ul
                        id={vertrag.selectId}
                        className="auswahl-select"
                        style={{
                          backgroundImage: `linear-gradient(to left, ${backgroundColor}, ${backgroundColor}`,
                        }}
                      >
                        {vertrag.vertragsList[0].map((vertragListEl, i) => {
                          return (
                            <li
                              key={i}
                              className="auswahl-list-item"
                              onClick={() => handleRemoveVertrag(vertrag.id)}
                              style={{
                                borderBottom: "2px solid transparent",
                                borderLeftWidth: "0",
                                borderTopWidth: "0",
                                borderRightWidth: "0",
                                borderImage: `linear-gradient(to right, ${backgroundColor2} 0%, ${backgroundColor} 50%, ${backgroundColor2} 100%)`,
                                borderImageSlice: "1",
                              }}
                            >
                              {vertragListEl}
                            </li>
                          );
                        })}
                      </ul>
                    )
                  ) : (
                    <ul
                      id={vertrag.selectId}
                      className="auswahl-select"
                      style={{
                        backgroundImage: `linear-gradient(to left, ${backgroundColor}, ${backgroundColor}`,
                      }}
                    >
                      <li
                        style={{
                          fontSize: "12px",
                          padding: "35px 10px",
                          lineHeight: "19px",
                        }}
                      >
                        Anbieter nicht gefunden? Vielleicht ein Schreibfehler?
                        Falls nicht, einfach die Gesellschaft eingeben und
                        speichern.
                      </li>
                      <div
                        onClick={(e) => {
                          handleSendGesellschaft(
                            vertrag.text,
                            vertrag.sparteId,
                            e
                          );
                          handleRemoveVertrag(vertrag.id);
                        }}
                        className="vertragspanel-button"
                      >
                        Senden
                      </div>
                    </ul>
                  )}
                </Fragment>
              ) : null}
            </li>
          </div>
        );
      })}
    </Fragment>
  );
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ sendGesellschaft }, dispatch);
};

export default connect(null, mapDispatchToProps)(VertragsGridElement);
