import React from "react";
import Slider from "react-slick";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./StatistikSlider.scss";
import StatistikSliderElement from "./StatistikSliderElement/StatistikSliderElement";

const StatistikSlider = ({
  energyKosten,
  gesundKosten,
  contactKosten,
  geldKosten,
  versicherungKosten,
  energyVertragNameWithKosten,
  contactVertragNameWithKosten,
  gesundVertragNameWithKosten,
  geldVertragNameWithKosten,
  versicherungVertragNameWithKosten,
}) => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  return (
    <Slider {...settings}>
      {energyVertragNameWithKosten.length !== 0 ? (
        <StatistikSliderElement
          headerText={"Energy"}
          vertragNameWithKosten={energyVertragNameWithKosten}
          kosten={energyKosten}
          color={"#8FBC26"}
        />
      ) : null}
      {contactVertragNameWithKosten.length !== 0 ? (
        <StatistikSliderElement
          headerText={"Kontact"}
          vertragNameWithKosten={contactVertragNameWithKosten}
          kosten={contactKosten}
          color={"#a43288"}
        />
      ) : null}
      {gesundVertragNameWithKosten.length !== 0 ? (
        <StatistikSliderElement
          headerText={"Gesund"}
          vertragNameWithKosten={gesundVertragNameWithKosten}
          kosten={gesundKosten}
          color={"#F8AC18"}
        />
      ) : null}
      {geldVertragNameWithKosten.length !== 0 ? (
        <StatistikSliderElement
          headerText={"Geld"}
          vertragNameWithKosten={geldVertragNameWithKosten}
          kosten={geldKosten}
          color={"#00B0F0"}
        />
      ) : null}
      {versicherungVertragNameWithKosten.length !== 0 ? (
        <StatistikSliderElement
          headerText={"Versicherung"}
          vertragNameWithKosten={versicherungVertragNameWithKosten}
          kosten={versicherungKosten}
          color={"#CB2E21"}
        />
      ) : null}
    </Slider>
  );
};

export default StatistikSlider;
