import React from "react";
import InputAdornment from "@material-ui/core/InputAdornment";
import { useField } from "formik";
import { TextField } from "@material-ui/core";
import FormControl from "@material-ui/core/FormControl";

const MyTextFieldKosten = ({ label, placeholder, type, ...props }) => {
  const [field, meta] = useField(props);
  const errorText = meta.error && meta.touched ? meta.error : null;

  return (
    <FormControl variant="outlined" style={{ width: "47.5%" }}>
      <TextField
        {...field}
        label={label}
        helperText={errorText}
        placeholder={placeholder}
        variant="outlined"
        type={type}
        error={errorText ? true : false} //or error={!!errorText}
        InputProps={{
          endAdornment: <InputAdornment position="start">€</InputAdornment>,
        }}
      />
    </FormControl>
  );
};

export default MyTextFieldKosten;
