import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { Link, useParams, Redirect } from "react-router-dom";
import axios from "axios";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import {
  addFullVertrag,
  deleteVertrag,
  getFullVertrag,
} from "../../../redux/index";
import VertragForm from "./Contracts/VertragForm";
import "./VertragsContract.scss";

const VertragsContract = ({
  handleShowMessage,
  user,
  token,
  addFullVertrag,
  deleteVertrag,
  setShowDetails,
  getFullVertrag,
}) => {
  const [vertragById, setVertragById] = useState({});
  const [redirect, setRedirect] = useState(false);

  //closeDetails when is open
  setShowDetails(false);

  const { id } = useParams();

  useEffect(() => {
    axios({
      method: "get",
      url: `/vertragsUebersicht/contract/${id}`,
      headers: { "x-auth-token": token },
    })
      .then((res) => {
        // eslint-disable-next-line
        setVertragById(res.data.vertrag);
      })
      .catch((e) => console.log(e));
  }, [id, token]);

  const addFullVertrags = (values) => {
    addFullVertrag(values);
    deleteVertrag(vertragById._id);
     setRedirect(true);
    /*  getFullVertrag(); */
  };

  const handleDeleteVertrag = (e) => {
    e.preventDefault();
    deleteVertrag(vertragById._id);
    setRedirect(true);
  };

  const alert = (e) => {
    const result = window.confirm(
      "Sie sind sicher, dass Sie diesen Vertrag löschen möchten?"
    );
    if (result) {
      handleShowMessage("showMessage2");
      handleDeleteVertrag(e);
    } else {
      return null;
    }
  };
  let backgroundImage;
  try {
    if (vertragById.anbieter) {
      backgroundImage = require(`../../../assets/gesellschaftsImages/${vertragById.anbieter}.png`);
    }
  } catch (e) {}

  if (vertragById._id && vertragById.user === user._id) {
    return (
      <div id="panel-ausfullen">
        <div id="panel-ausfullen-details">
          <div id="panel-ausfullen-details-main">
            <div className="panel-ausfullen-head">
              <Link to="/vertragsUebersicht">
                <FontAwesomeIcon icon={faTimes} />
              </Link>
              <div className="header-container">
                <div className="text-container">
                  <p>{vertragById.name}</p>
                  <p>{vertragById.anbieter}</p>
                </div>
                <div className="image-container">
                  {backgroundImage ? (
                    <img
                      alt={vertragById.anbieter}
                      src={`${backgroundImage}`}
                    />
                  ) : (
                    <img
                      className="image"
                      alt={vertragById.name}
                      src={require(`../../../assets/vertragsImages/${vertragById.name}.png`)}
                    />
                  )}
                </div>
              </div>
            </div>
            <VertragForm
              addFullVertrags={addFullVertrags}
              alert={alert}
              anbieter={vertragById.anbieter}
              vertragsName={vertragById.name}
            />
            {redirect && <Redirect to={"/vertragsUebersicht"} />}
          </div>
        </div>
      </div>
    );
  } else return null;
};

const mapStateToProps = (state) => ({
  token: state.userReducer.token,
  user: state.userReducer.user,
});

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    { addFullVertrag, deleteVertrag, getFullVertrag },
    dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(VertragsContract);
