import axios from "axios";

import { returnErrors, clearErrors } from "../errors/errorActions";
import {
  USER_LOADING,
  USER_LOADED,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT_SUCCESS,
  REGISTER_SUCCESS,
  REGISTER_FAIL,
  CHANGE_USER_DATA_SUCCESS,
  USER_BIRTHDAY_LOADING,
  USER_BIRTHDAY_LOADED,
  USER_FIRST_NAME_LOADING,
  USER_FIRST_NAME_LOADED,
  USER_LAST_NAME_LOADING,
  USER_LAST_NAME_LOADED,
  USER_ADDRESS_LOADING,
  USER_ADDRESS_LOADED,
  USER_HANDYNUMMER_LOADING,
  USER_HANDYNUMMER_LOADED,
} from "./userTypes";

export const loadUser = () => (dispatch, getState) => {
  dispatch({ type: USER_LOADING });

  axios
    .get("/user", tokenConfig(getState))
    .then((res) => {
      dispatch({
        type: USER_LOADED,
        payload: res.data,
      });
    })
    .catch((error) => {
      dispatch(returnErrors(error.response.data, error.response.status));
      dispatch({
        type: AUTH_ERROR,
      });
    });
};

