export const register = ({
  name,
  vorname,
  email,
  password,
  registerOptionen,
  empfehlung,
  berater,
}) => (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };

  const body = JSON.stringify({
    name,
    vorname,
    email,
    password,
    registerOptionen,
    empfehlung,
    berater,
  });

  dispatch(clearErrors());
  axios
    .post("/signup", body, config)
    .then((res) => {
      dispatch({
        type: REGISTER_SUCCESS,
      });
      dispatch(returnErrors(res.data, res.status, "REGISTER_SUCCESS"));
    })
    .catch((error) => {
      dispatch(
        returnErrors(
          error.response.data,
          error.response.status,
          "REGISTER_FAIL"
        )
      );
      dispatch({
        type: REGISTER_FAIL,
      });
    });
};

export const logout = () => {
  return {
    type: LOGOUT_SUCCESS,
  };
};

export const updateUserData = (user) => (dispatch, getState) => {
  axios
    .patch("/profil", user, tokenConfig(getState))
    .then((res) => {
      dispatch({
        type: CHANGE_USER_DATA_SUCCESS,
        payload: res.config.data,
      });
    })
    .catch((error) => {
      console.log(error);
    });
};

export const login = ({ email, password }) => (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };
  const body = JSON.stringify({ email, password });

  axios
    .post("/login", body, config)
    .then((res) => {
      dispatch(clearErrors());
      dispatch({
        type: LOGIN_SUCCESS,
        payload: res.data,
      });
    })
    .catch((error) => {
      dispatch(
        returnErrors(error.response.data, error.response.status, "LOGIN_FAIL")
      );
      dispatch({
        type: LOGIN_FAIL,
      });
    });
};

export const passwortVergessen = (data) => (dispatch) => {
  axios
    .put("/passwort-vergessen", {
      email: data.email,
    })
    .then((res) => {
      dispatch(clearErrors());
      dispatch(returnErrors(res.data, res.status));
    })
    .catch((err) => {
      dispatch(clearErrors());
      dispatch(returnErrors(err.response.data, err.response.status));
    });
};

export const tokenConfig = (getState) => {
  const token = getState().userReducer.token;

  const config = {
    headers: {
      "Content-type": "application/json",
    },
  };

  if (token) {
    config.headers["x-auth-token"] = token;
  }

  return config;
};
