import React, { Fragment } from "react";
import logo from "../../../../assets/images/logo1.png";
import "./TopNav.scss";
import { Link, useRouteMatch } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { connect } from "react-redux";

import { faEnvelope, faPlus, faUser } from "@fortawesome/free-solid-svg-icons";

const TopNav = ({ openSidebar, user }) => {
  let { url } = useRouteMatch();
  return (
    <Fragment>
      <div className="top-nav">
        <div id="button" className="button" onClick={openSidebar}>
          <div className="button-box">
            <i className="fa fa-bars" aria-hidden="true"></i>
          </div>
        </div>
        <div className="top-nav-post">
          <Link to="/vertragsUebersicht/kontakt">
            <div className="button-box">
              <FontAwesomeIcon icon={faEnvelope} />
            </div>
          </Link>
        </div>
        <div className="icon">
          <div className="button-box-home">
            <Link to="/vertragsUebersicht">
              <img
                id="vertragsengel-logo1"
                src={logo}
                alt="Vertragsengel logo"
              />
            </Link>
          </div>
        </div>
        <div className="top-nav-add">
          <Link style={{ color: "white" }} to="/vertragsAuswahl">
            <div className="button-box">
              <FontAwesomeIcon icon={faPlus} />
            </div>
          </Link>
        </div>
        <div className="top-nav-profil">
          <Link to={`${url}/profil/${user._id}`}>
            <div className="button-box">
              <FontAwesomeIcon icon={faUser} />
            </div>
          </Link>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  user: state.userReducer.user,
});

export default connect(mapStateToProps)(TopNav);
