import React, { Fragment, useRef, useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDoubleUp } from "@fortawesome/free-solid-svg-icons";
import useMediaQuery from "@material-ui/core/useMediaQuery";

import {
  addVertrag,
  getVertragsArray,
  getGesellschaften,
} from "../../../redux/index";
import "./Vertragspanel.scss";

const Vertragspanel = ({
  vertragsList,
  handleRemoveVertragEl,
  handleShowMessage,
  addVertrag,
  getVertragsArray,
  getGesellschaften,
}) => {
  const [vertragspanelId, setVertragspanelId] = useState("");

  const matches = useMediaQuery("(max-width:900px)");

  const vertragspanelRef = useRef(null);

  const handleOpenVertragspanel = () => {
    if (matches && vertragspanelRef.current.id === "vertragspanel") {
      vertragspanelRef.current.id = "vertragspanelIsOpen";
      setVertragspanelId("vertragspanelIsOpen");
    } else if (
      matches &&
      vertragspanelRef.current.id === "vertragspanelIsOpen"
    ) {
      vertragspanelRef.current.id = "vertragspanel";
      setVertragspanelId("vertragspanel");
    }
  };

  const addVertrags = () => {
    // eslint-disable-next-line
    vertragsList.map((vertrag) => {
      addVertrag({ name: vertrag.name, anbieter: vertrag.anbieter });
      /* handleRemoveVertragEl(vertrag.name); */
      getVertragsArray();
      getGesellschaften();
    });

    handleShowMessage("showMessage");
  };

  useEffect(() => {
    if (vertragsList.length !== 0 && matches) {
      addVertrags();
    }
  }, // eslint-disable-next-line
  [vertragsList.length, matches]);

  return (
    <div ref={vertragspanelRef} id="vertragspanel" className="vertragspanel">
      <div className="vertragspanel-titel">
        {vertragsList.length === 0 ? (
          <Fragment>
            <h1>
              Jetzt wird aufgeräumt,
              <br />
              lieber Nutzer!
            </h1>
            {matches ? (
              <div onClick={handleOpenVertragspanel}>
                <FontAwesomeIcon icon={faAngleDoubleUp} />
              </div>
            ) : null}
            <p>
              Klicke jetzt auf eine Kategorie und wähle deinen Anbieter aus.
            </p>
          </Fragment>
        ) : (
          <Fragment>
            <h1>Sehr gut gemacht. Weiter geht's!</h1>
            {matches ? (
              <div onClick={handleOpenVertragspanel}>
                <FontAwesomeIcon icon={faAngleDoubleUp} />
              </div>
            ) : null}
            <p>
              Du hast {vertragsList.length}{" "}
              {vertragsList.length > 1 ? "Verträge" : "Vertrag"} ausgewählt
            </p>
          </Fragment>
        )}
      </div>

      {vertragsList.length !== 0 ? (
        vertragspanelId === "vertragspanel" && matches ? null : (
          <ul id="vertragspanel-vertragslist">
            <p>Deine Auswahl:</p>
            {vertragsList.map((vertrag, i) => {
              return (
                <div className="vertragslist-element" key={i}>
                  <div className="vertragslist-element-image">
                    <img
                      alt={vertrag.name}
                      src={require(`../../../assets/vertragsImages/${vertrag.name}.png`)}
                    />
                  </div>
                  <div className="vertragslist-element-text">
                    <b>{vertrag.name}:</b>
                    <br />
                    <span>{vertrag.anbieter}</span>
                  </div>
                  <i
                    className="fa fa-times"
                    id={vertrag.name}
                    onClick={(e) => handleRemoveVertragEl(e)}
                  />
                </div>
              );
            })}
          </ul>
        )
      ) : null}
      <Link to="/vertragsUebersicht">
        <button
          onClick={addVertrags}
          className="vertragspanel-button"
          disabled={vertragsList.length === 0}
        >
          Speichern
        </button>
      </Link>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    vertrag: state.vertragReducer.vertrags,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    { addVertrag, getVertragsArray, getGesellschaften },
    dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(Vertragspanel);
