import { ADD_FULLVERTRAG } from "./fullVertragTypes";
import { GET_FULLVERTRAG } from "./fullVertragTypes";
import { GET_FULLVERTRAGBYID } from "./fullVertragTypes";
import { DELETE_FULLVERTRAG } from "./fullVertragTypes";
import { CHANGE_STATUS_FULLVERTRAG } from "./fullVertragTypes";

const initialState = {
  fullVertragById: {},
  fullVertrags: [],
  loading: false,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_FULLVERTRAG:
      return {
        ...state,
        fullVertrags: [...state.fullVertrags, action.payload],
      };
    case GET_FULLVERTRAG:
      return {
        ...state,
        fullVertrags: [...state.fullVertrags, action.payload],
        loading: false,
      };
    case GET_FULLVERTRAGBYID:
      return {
        ...state,
        fullVertragById: action.payload,
        loading: false,
      };
    case CHANGE_STATUS_FULLVERTRAG:
      return {
        ...state,
        fullVertrags: action.payload,
        loading: false,
      };
    case DELETE_FULLVERTRAG:
      return {
        ...state,
        fullVertrags: state.fullVertrags.filter(
          (vertrag) => vertrag._id !== action.payload
        ),
      };
    default:
      return state;
  }
};

export default reducer;
