import React, { useState, useEffect, useRef, Fragment } from "react";
import Popup from "reactjs-popup";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheckCircle, faTimes } from "@fortawesome/free-solid-svg-icons";
import { Button } from "@material-ui/core";
import { connect } from "react-redux";
import axios from "axios";
import { bindActionCreators } from "redux";
import useMediaQuery from "@material-ui/core/useMediaQuery";

import { loadUser } from "../../../../../redux/index";
import VertragsCardAssistent from "./VertragsCardAssistent/VertragsCardAssistent";

const DialogPopup = ({ vertrag, token, user, loadUser }) => {
  const [changePosition, setChangePosition] = useState(0);
  const [vertragsLength, setVertragsLength] = useState(0);
  const [assistentArray, setAssistentArray] = useState([]);
  const [showWarningText, setShowWarningText] = useState(false);
  const [changeLastButtonText, setChangeLastButtonText] = useState(false);
  const [checkUserInfo, setCheckUserInfo] = useState(false);
  const [mediaQueryPixelNumber, setMediaQueryPixelNumber] = useState(400);

  const inputEl = useRef(null);

  const matches = useMediaQuery("(max-width:400px)");

  useEffect(() => {
    if (matches) {
      setMediaQueryPixelNumber(320);
      setChangePosition(0);
    } else {
      setMediaQueryPixelNumber(400);
      setChangePosition(0);
    }
  }, [matches]);
  //check if user Info are complete before send post request if not popup form
  useEffect(() => {
    if (user) {
      if (
        user.contactNachname === "" ||
        user.contactVorname === "" ||
        user.contactEmail === "" ||
        user.contactHandynummer === "" ||
        user.contactStrasse === "" ||
        user.contactPostleitzahl === "" ||
        user.contactStrasse === "" ||
        user.contactLand === "" ||
        user.contactOrt === ""
      ) {
        setCheckUserInfo(false);
      } else {
        setCheckUserInfo(true);
      }
    }
  }, [user]);

  //taking vertrag length to definewidth of el. with className 'containerAssistent
  useEffect(() => {
    setVertragsLength(vertrag.length);
  }, [vertrag.length]);

  //move 400px to left
  const handleChangePositionPlus = () => {
    setChangePosition(changePosition + mediaQueryPixelNumber);
  };

  //move 400px to right
  const handleChangePositionMinus = () => {
    setChangePosition(changePosition - mediaQueryPixelNumber);
  };

  //taking assistent.length - if null can't show the last card and sendform to the server
  const onButtonClick = () => {
    // `current` points to the mounted text input element
    if (
      inputEl.current.style.right ===
        `${vertragsLength * mediaQueryPixelNumber}px` &&
      assistentArray.length === 0 &&
      inputEl.current.style.right !== "0px"
    ) {
      setShowWarningText(true);
    } else {
      handleChangePositionPlus();
    }
  };

  //changing text from the last button to 'Daten abschicken'
  // eslint-disable-next-line
  useEffect(() => {
    if (
      inputEl.current.style.right ===
      `${vertragsLength * mediaQueryPixelNumber}px`
    ) {
      setChangeLastButtonText(true);
    } else {
      setChangeLastButtonText(false);
    }
  });

  //let us send the form only when the last card is displayed
  const sendFormToTheServer = () => {
    if (
      assistentArray.length !== 0 &&
      inputEl.current.style.right ===
        `${vertragsLength * mediaQueryPixelNumber + mediaQueryPixelNumber}px`
    ) {
      //send only this element which one was fully descriptioned
      let obj = [];
      assistentArray.map((arr) => {
        // eslint-disable-next-line
        return arr.map((assArr) => {
          let obj1 = Object.assign({}, assArr);
          //change key and value names from '0' and '1' to 'key' and 'value'
          const clone = (obj) => Object.assign({}, obj);
          const renameKey = (obj, key, newKey) => {
            const clonedObj = clone(obj);
            const targetKey = clonedObj[key];
            delete clonedObj[key];
            clonedObj[newKey] = targetKey;
            return clonedObj;
          };

          obj1 = renameKey(obj1, "0", "key");
          obj1 = renameKey(obj1, "1", "value");
          obj.push(obj1);
        });
      });
      axios({
        method: "post",
        url: "/dialogAssistent",
        headers: { "x-auth-token": token },
        data: {
          obj: obj,
          name: user.contactVorname,
          nachname: user.contactNachname,
          email: user.contactEmail,
        },
      });
    }
  };

  //let invoke 'sendFormToTheServer' after change the 'assistentArray'
  //send automat this form
  useEffect(
    () => {
      sendFormToTheServer();
    }, // eslint-disable-next-line
    [assistentArray, changePosition]
  );

  //push element to the assistentArray when element not empty and store there
  const handleDateiFromForm = (values) => {
    const result = Object.keys(values).map((key) => {
      return [key, values[key]];
    });

    let newArr = result.filter((arr) => arr[1] !== "");
    //if only 2 fields can't add to 'assistentArray' - 'vertragsName' and 'vertragsAnbieter' are always added we need some field more
    if (newArr.length === 2) {
      onButtonClick();
      return;
    } else {
      setAssistentArray([...assistentArray, newArr]);
    }
  };
  //run onButttonClick when props is changed
  useEffect(
    () => {
      if (assistentArray.length === 0) {
        return;
      } else {
        onButtonClick();
      }
    }, // eslint-disable-next-line
    [assistentArray]
  );

  return (
    <Popup
      className="popup2"
      style={{ width: "400px !important" }}
      modal
      closeOnDocumentClick={false}
      defaultOpen={true}
      closeOnEscape={false}
    >
      <div
        ref={inputEl}
        className="containerAssistent"
        style={{
          width: `${
            mediaQueryPixelNumber * 2 + vertragsLength * mediaQueryPixelNumber
          }px`,
          right: `${changePosition}px`,
        }}
      >
        <div className="firstCard">
          <div className="containerAssistentHeader">
            <Link to="/vertragsUebersicht">
              <i id="buttonCloseDialog">
                <FontAwesomeIcon icon={faTimes} />
              </i>
            </Link>
            <div className="headerPhoto" />
            <div className="headerTextContainer">
              <p>Rene</p>
              <i>Kundenbetreuung</i>
            </div>
          </div>
          <div className="containerAssistentContent">
            <h1 className="title">Automatische Vervollständigung</h1>
            <p>
              Lass uns das machen: Wir kontaktieren deine Anbieter und
              vervollständigen automatisiert und kostenfrei alle Details für
              dich – innerhalb von nur 10 Tagen!
            </p>
            <ul>
              <li>
                <span>
                  <FontAwesomeIcon icon={faCheckCircle} />
                </span>
                Keine Vertragsänderungen
              </li>
              <li>
                <span>
                  <FontAwesomeIcon icon={faCheckCircle} />
                </span>
                Absolut kostenfrei
              </li>
              <li>
                <span>
                  <FontAwesomeIcon icon={faCheckCircle} />
                </span>
                Einmalige Aktualisierung
              </li>
              <li>
                <span>
                  <FontAwesomeIcon icon={faCheckCircle} />
                </span>
                SSL-Datenverschlüsslung
              </li>
            </ul>
            {checkUserInfo ? (
              <Button
                style={{
                  textTransform: "none",
                  marginTop: "40px",
                  width: "60%",
                  height: "45px",
                  borderRadius: "22.5px",
                }}
                variant="contained"
                color="primary"
                onClick={handleChangePositionPlus}
              >
                Einverstanden
              </Button>
            ) : (
              <Fragment>
                <p
                  style={{
                    color: "#A43288",
                    fontWeight: "600",
                    marginBottom: "15px",
                  }}
                >
                  Um diesen Service zu nutzen, geben Sie bitte zuerst Ihre
                  Kontaktdaten ein.
                </p>
                {user ? (
                  <Link to={`/vertragsUebersicht/profil/${user._id}`}>
                    <Button
                      style={{
                        width: "60%",
                        height: "45px",
                        textTransform: "none",
                        borderRadius: "22.5px",
                      }}
                      variant="contained"
                      color="primary"
                    >
                      zu meinem Profil
                    </Button>
                  </Link>
                ) : null}
              </Fragment>
            )}
          </div>
        </div>
        {vertrag.map((vertrag, i) => {
          return (
            <VertragsCardAssistent
              vertragsName={vertrag.name}
              vertragsAnbieter={vertrag.anbieter}
              handleChangePositionMinus={handleChangePositionMinus}
              handleDateiFromForm={handleDateiFromForm}
              showWarningText={showWarningText}
              assistentArray={assistentArray}
              changeLastButtonText={changeLastButtonText}
              key={i}
            ></VertragsCardAssistent>
          );
        })}
        <div className="lastCard" style={{ right: `${0}px` }}>
          <div className="containerAssistentHeader">
            <Link to="/vertragsUebersicht">
              <i id="buttonCloseDialog1">
                <FontAwesomeIcon icon={faTimes} />
              </i>
            </Link>
            <div className="headerPhoto" />
            <div className="headerTextContainer">
              <p>Rene</p>
              <i style={{ color: "black" }}>Kundenbetreuung</i>
            </div>
          </div>
          <div className="containerAssistentContent" style={{ height: "100%" }}>
            <h1 className="title">
              Wir vervollständigen diese Verträge jetzt kostenlos für dich!
            </h1>
            <p>
              Du kannst uns später aber trotzdem etwas zahlen und damit unseren
              Service unterstützen – wenn du zufrieden bist!
            </p>
          </div>
        </div>
      </div>
    </Popup>
  );
};

const mapStateToProps = (state) => {
  return {
    vertrag: state.vertragReducer.vertrags,
    token: state.userReducer.token,
    user: state.userReducer.user,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ loadUser }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(DialogPopup);
