import React, { Fragment } from "react";
import { Link, useRouteMatch } from "react-router-dom";
import useMediaQuery from "@material-ui/core/useMediaQuery";

import NavElement from "./NavElement/NavElement";
import Logout from "../../../../Authorization/Logout/Logout";

const NavElements = ({ user, openSidebar }) => {
  const matches = useMediaQuery("(min-width:700px)");
  let { url } = useRouteMatch();
  return (
    <Fragment>
      {matches ? (
        <Link onClick={openSidebar} to="/vertragsUebersicht">
          <NavElement text="Meine Verträge" />
        </Link>
      ) : null}
      <Link onClick={openSidebar} to="/vertragsUebersicht/gekündigteVerträge">
        <NavElement text="Gekündigte Verträge" />
      </Link>
      <Link onClick={openSidebar} to="/Datenschutz">
        <NavElement text="Datenschutz" />
      </Link>
      {matches ? (
        <Link to={`${url}/profil/${user._id}`}>
          <NavElement text="Profil" />
        </Link>
      ) : null}
      {matches ? (
        <Link to="/Statistiken">
          <NavElement text="Statistiken" />
        </Link>
      ) : null}
      <Logout />

      {user.contactEmail ? (
        user.contactEmail.includes("vertragsengel.de") ? (
          <Link onClick={openSidebar} to="/admin">
            <NavElement text="Admin" />
          </Link>
        ) : null
      ) : null}
    </Fragment>
  );
};

export default NavElements;
