import React, { useState } from "react";
import { connect } from "react-redux";
import Popup from "reactjs-popup";
import { Formik, Form } from "formik";
import { Button } from "@material-ui/core";
import * as Yup from "yup";
import MyTextField from "../../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextField";
import { Redirect, Link } from "react-router-dom";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import { bindActionCreators } from "redux";

import {
  addNewGesellschaft,
  getAllGesellschaften,
  getGesellschaften,
} from "../../../redux/index";
import "./NewGesellschaft.scss";

const SignupSchema = Yup.object().shape({
  name: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  anschrift: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  telefonnummer: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  fax: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  email: Yup.string()
    .email()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  seite: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  plz: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
  ort: Yup.string()
    .required("Dieses Feld ist erforderlich")
    .typeError("Dieses Feld ist erforderlich"),
});

const NewGesellschaft = ({ addNewGesellschaft, getAllGesellschaften }) => {
  const [spartenList, setSpartenList] = useState([]);
  const [spartenList2, setSpartenList2] = useState([]);
  const [redirect, setRedirect] = useState(false);

  const handleSparten = (e) => {
    if (typeof e.target.value !== "string") {
      return null;
    } else {
      const sparteName = e.target.value.split(" ");
      setSpartenList([...spartenList, sparteName]);
      setSpartenList2([...spartenList2, sparteName[0]]);
    }
  };

  const handleDeleteEl = (e) => {
    setSpartenList(spartenList.filter((el) => el[1] !== e[1]));
    setSpartenList2(spartenList2.filter((el) => el !== e[0]));
  };

  const handleChangeGesellschaft = (values) => {
    addNewGesellschaft(values);
    getAllGesellschaften();
    getGesellschaften();

    setRedirect(true);
  };

  return (
    <Popup
      className="popupAdmin"
      contentStyle={{ overflowY: "auto" }}
      modal
      closeOnDocumentClick={false}
      defaultOpen={true}
      closeOnEscape={false}
    >
      <Formik
        initialValues={{
          name: "",
          anschrift: "",
          plz: "",
          ort: "",
          telefonnummer: "",
          fax: "",
          email: "",
          seite: "",
          sparte: "",
        }}
        enableReinitialize={true}
        validationSchema={SignupSchema}
        onSubmit={(values) => {
          values.sparte = spartenList2;
          handleChangeGesellschaft(values);
        }}
      >
        {({ values }) => (
          <Form
            style={{
              backgroundColor: "white",
              display: "flex",
              flexDirection: "column",
              width: "auto",
              margin: "auto",
            }}
          >
            <Link
              style={{
                display: "flex",
                justifyContent: "flex-end",
                margin: "20px",
              }}
              to="/admin"
            >
              <ExitToAppIcon />
            </Link>
            <MyTextField
              placeholder="Name"
              name="name"
              type="text"
              label="Name"
            />
            <MyTextField
              placeholder="Anschrift"
              name="anschrift"
              type="text"
              label="Anschrift"
            />
            <MyTextField placeholder="PLZ" name="plz" type="text" label="PLZ" />
            <MyTextField placeholder="Ort" name="ort" type="text" label="Ort" />
            <MyTextField
              placeholder="Telefonnummer"
              name="telefonnummer"
              type="text"
              label="Telefonnummer"
            />
            <MyTextField
              placeholder="Web Seite"
              name="seite"
              type="text"
              label="Web Seite"
            />
            <MyTextField placeholder="Fax" name="fax" type="text" label="Fax" />
            <MyTextField
              placeholder="E-Mail"
              name="email"
              type="email"
              label="E-Mail"
            />
            <FormControl variant="outlined">
              <InputLabel id="demo-simple-select-filled-label">
                Sparte
              </InputLabel>
              <Select
                name="sparte"
                labelId="demo-simple-select-outlined-label"
                id="demo-simple-select-outlined"
                label="Sparte"
                value={values.sparte}
                onClick={(e) => handleSparten(e)}
              >
                <MenuItem value="'1' Mobiles Internet">
                  Mobiles Internet
                </MenuItem>
                <MenuItem value="'2' Internet">Internet</MenuItem>
                <MenuItem value="'3' Gas">Gas</MenuItem>
                <MenuItem value="'4' Kabel-Fernsehen">Kabel-Fernsehen</MenuItem>
                <MenuItem value="'5' Krankenversicherung">
                  Krankenversicherung
                </MenuItem>
                <MenuItem value="'6' Handy">Handy</MenuItem>
                <MenuItem value="'7' Strom">Strom</MenuItem>
                <MenuItem value="'8' Pay-TV">Pay-TV</MenuItem>
                <MenuItem value="'9' Zahnzusatz-Versicherung">
                  Zahnzusatz-Versicherung
                </MenuItem>
                <MenuItem value="'11' Festnetz">Festnetz</MenuItem>
                <MenuItem value="'12' Kabel-Internet">Kabel-Internet</MenuItem>
                <MenuItem value="'13' Patientenverfügung">
                  Patientenverfügung
                </MenuItem>
                <MenuItem value="'14' Lebensversicherung">
                  Lebensversicherung
                </MenuItem>
                <MenuItem value="'15' Riester Rente">Riester Rente</MenuItem>
                <MenuItem value="'16' Rückabwicklung">Rückabwicklung</MenuItem>
                <MenuItem value="'17' Fitnessstudio">Fitnessstudio</MenuItem>
                <MenuItem value="'18' Kredit">Kredit</MenuItem>
                <MenuItem value="'19' Bauherrenhaftpflicht">
                  Bauherrenhaftpflicht
                </MenuItem>
                <MenuItem value="'24' KFZ-Versicherung">
                  KFZ-Versicherung
                </MenuItem>
                <MenuItem value="'26' Rechtsschutz">Rechtsschutz</MenuItem>
                <MenuItem value="'28' Hausrat">Hausrat</MenuItem>
                <MenuItem value="'30' Haftpflicht">Haftpflicht</MenuItem>
                <MenuItem value="'32' Krankenzusatz-Versicherung">
                  Krankenzusatz-Versicherung
                </MenuItem>
                <MenuItem value="'33' Gebäudeversicherung">
                  Gebäudeversicherung
                </MenuItem>
                <MenuItem value="'34' Reiseversicherung">
                  Reiseversicherung
                </MenuItem>
                <MenuItem value="'35' Unfallversicherung">
                  Unfallversicherung
                </MenuItem>
                <MenuItem value="'36' Motorrad-Versicherung">
                  Motorrad-Versicherung
                </MenuItem>
                <MenuItem value="'37' Sterbegeld-Versicherung">
                  Sterbegeld-Versicherung
                </MenuItem>
                <MenuItem value="'39' Tierhalterhaftpflicht">
                  Tierhalterhaftpflicht
                </MenuItem>
                <MenuItem value="'41' Tagesgeldkonto">Tagesgeldkonto</MenuItem>
                <MenuItem value="'43' Berufsunfähigkeit">
                  Berufsunfähigkeit
                </MenuItem>
                <MenuItem value="'45' Festgeldkonto">Festgeldkonto</MenuItem>
                <MenuItem value="'46' Sparkonto">Sparkonto</MenuItem>
                <MenuItem value="'47' Grundbesitzer-Haftpflicht">
                  Grundbesitzer-Haftpflicht
                </MenuItem>
                <MenuItem value="'48' Pflegeversicherung">
                  Pflegeversicherung
                </MenuItem>
                <MenuItem value="'49' Automobil-Club">Automobil-Club</MenuItem>
                <MenuItem value="'50' Betriebliche Altersvorsorge">
                  Betriebliche Altersvorsorge
                </MenuItem>
                <MenuItem value="'51' Auslandsversicherung">
                  Auslandsversicherung
                </MenuItem>
                <MenuItem value="'52' Elektrogeräte-Versicherung">
                  Elektrogeräte-Versicherung
                </MenuItem>
                <MenuItem value="'53' Bausparvertrag">Bausparvertrag</MenuItem>
                <MenuItem value="'54' Rürup Rente">Rürup Rente</MenuItem>
                <MenuItem value="'55' Bootsversicherung">
                  Bootsversicherung
                </MenuItem>
                <MenuItem value="'56' Fahrrad-Versicherung">
                  Fahrrad-Versicherung
                </MenuItem>
                <MenuItem value="'57' Glasversicherung">
                  Glasversicherung
                </MenuItem>
                <MenuItem value="'58' Depot">Depot</MenuItem>
              </Select>
            </FormControl>
            {spartenList.map((el, i) => (
              <li
                style={{
                  listStyle: "none",
                  margin: "5px",
                  display: "flex",
                  justifyContent: "space-between",
                  cursor: "pointer",
                }}
                key={i}
              >
                {el[1]}
                <DeleteForeverIcon onClick={() => handleDeleteEl(el)} />
              </li>
            ))}
            <br />
            <Button type="submit" variant="contained" color="primary">
              Speichern
            </Button>
            {redirect && <Redirect to="/admin" />}
          </Form>
        )}
      </Formik>
    </Popup>
  );
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      addNewGesellschaft,
      getAllGesellschaften,
      getGesellschaften,
    },
    dispatch
  );
};

export default connect(null, mapDispatchToProps)(NewGesellschaft);
