import React, { useState, Fragment, useEffect } from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import {
  getAllGesellschaften,
  getVertragsArray,
  getGesellschaften,
  loadUser,
  getVertrag,
  getFullVertrag,
  loadUserBirthdays,
  loadUserFirstName,
  loadUserLastName,
  loadUserAddress,
  loadUserHandynummer,
} from "./redux/index";
import "./App.scss";
import VertragsUebersicht from "./components/vertragsUebersicht/VertragsUebersicht";
import VertragsAuswahl from "./components/vertragsAuswahl/VertragsAuswahl";
import Kontakt from "./components/kontaktPage/Kontakt";
import VertragsContract from "./components/vertragsUebersicht/VertragsContract/VertragsContract";
import VertragsDetail from "./components/vertragsUebersicht/VertragsDetail/VertragsDetail";
import Kuendigung from "./components/Kuendigung/Kuendigung";
import Profil from "./components/Profil/Profil";
import NotFound from "./components/NotFound/NotFound";
import NotFound2 from "./components/NotFound/NotFound2";
import Login from "./components/Authorization/Login/Login2";
import Register from "./components/Authorization/Register/Register2";
import PasswortVergessen from "./components/Authorization/PasswortVergessen/PasswortVergessen";
import PasswortZuruecksetzen from "./components/Authorization/PasswortZuruecksetzen/PasswortZuruecksetzen";
import DialogPopup from "./components/vertragsUebersicht/VertragsList/AutomatischeVervollstaendigung/DialogPopup/DialogPopup";
import Admin from "./components/Admin/Admin";
import GesellschaftManage from "./components/Admin/GesellschaftManage/GesellschaftManage";
import NewGesellschaft from "./components/Admin/NewGesellschaft/NewGesellschaft";
import Datenschutz from "./components/Datenschutz/Datenschutz";
import Statistiken from "./components/Statistiken/Statistiken";

const App = ({
  token,
  getVertragsArray,
  vertrags,
  getGesellschaften,
  loadUser,
  getVertrag,
  getFullVertrag,
  loadUserBirthdays,
  loadUserFirstName,
  loadUserLastName,
  loadUserAddress,
  loadUserHandynummer,
  getAllGesellschaften,
}) => {
  const [state, setState] = useState({
    showSidebar: false,
    showMessage: false,
    showMessage2: false,
    showMessage3: false,
    showMessage4: false,
    showMessage5: false,
  });
  const [showDetails, setShowDetails] = useState(false);
  const openSidebar = () => {
    setState({
      showSidebar: !state.showSidebar,
    });
  };

  //get Sparten und gesellschaften from SQL
  useEffect(() => {
    if (token) {
      getAllGesellschaften();
      getVertragsArray();
      getGesellschaften();
      getFullVertrag();
      loadUser();
      getVertrag();
      loadUserBirthdays();
      loadUserFirstName();
      loadUserLastName();
      loadUserAddress();
      loadUserHandynummer();
    }
  }, [
    getGesellschaften,
    getAllGesellschaften,
    getVertragsArray,
    token,
    loadUser,
    getVertrag,
    getFullVertrag,
    loadUserBirthdays,
    loadUserFirstName,
    loadUserLastName,
    loadUserAddress,
    loadUserHandynummer,
  ]);

  //show messages function
  const handleShowMessage = (value) => {
    setState({
      [value]: true,
    });
    setTimeout(() => {
      setState({
        [value]: false,
      });
    }, 3000);
  };

  let data = sessionStorage.getItem("login");
  if (data) {
    return (
      <Router>
        <div className="App">
          <Switch>
            {token ? (
              <Fragment>
                {state.showMessage4 ? (
                  <div className="info-header1">Kündigung wurde gesendet</div>
                ) : state.showMessage3 ? (
                  <div className="info-header">Profil erfolgreich geändert</div>
                ) : state.showMessage ? (
                  <div className="info-header">
                    Vertragsliste wurde aktualisiert.
                  </div>
                ) : state.showMessage2 ? (
                  <div className="info-header2">Der Vertrag wurde gelöscht</div>
                ) : state.showMessage5 ? (
                  <div className="info-header1">
                    Ihre Anfrage wurde gesendet.
                  </div>
                ) : null}
                <Route path="/Statistiken">
                  <Statistiken
                    showSidebar={state.showSidebar}
                    openSidebar={openSidebar}
                  />
                </Route>
                <Route path="/vertragsUebersicht">
                  <VertragsUebersicht
                    showDetails={showDetails}
                    setShowDetails={setShowDetails}
                    showSidebar={state.showSidebar}
                    openSidebar={openSidebar}
                  />
                </Route>
                <Route path="/admin">
                  <Admin />
                </Route>
                <Route path="/Datenschutz">
                  <Datenschutz
                    showDetails={showDetails}
                    setShowDetails={setShowDetails}
                    showSidebar={state.showSidebar}
                    openSidebar={openSidebar}
                  />
                </Route>
                <Route path="/admin/:id">
                  <GesellschaftManage />
                </Route>
                <Route path="/admin/addNewGesellschaft">
                  <NewGesellschaft />
                </Route>
                <Route path="/vertragsUebersicht/kontakt">
                  <Kontakt />
                </Route>
                <Route path="/vertragsUebersicht/dialogAssistent">
                  <DialogPopup />
                </Route>
                <Route path="/vertragsUebersicht/contract/:id">
                  <VertragsContract
                    setShowDetails={setShowDetails}
                    handleShowMessage={handleShowMessage}
                  />
                </Route>
                <Route path="/vertragsUebersicht/gekündigteVerträge/:id">
                  <VertragsDetail
                    handleShowMessage={handleShowMessage}
                    showDetails={showDetails}
                    setShowDetails={setShowDetails}
                  />
                </Route>
                <Route path="/vertragsUebersicht/detail/:id">
                  <VertragsDetail
                    handleShowMessage={handleShowMessage}
                    showDetails={showDetails}
                    setShowDetails={setShowDetails}
                  />
                </Route>
                <Route path="/:path/profil/:userId">
                  <Profil
                    handleShowMessage={handleShowMessage}
                    setShowDetails={setShowDetails}
                  />
                </Route>
                <Route path="/vertragsAuswahl">
                  <VertragsAuswahl
                    setShowDetails={setShowDetails}
                    handleShowMessage={handleShowMessage}
                    openSidebar={openSidebar}
                    showSidebar={state.showSidebar}
                  />
                </Route>
                <Route path="/Kuendigungs/:id">
                  <Kuendigung
                    handleShowMessage={handleShowMessage}
                    openSidebar={openSidebar}
                    showSidebar={state.showSidebar}
                  />
                  <Route path="/Kuendigungs/:id/profil">
                    <Profil
                      handleShowMessage={handleShowMessage}
                      setShowDetails={setShowDetails}
                    />
                  </Route>
                </Route>
                <Route path="/login">
                  <Redirect to="/vertragsUebersicht" />
                </Route>
                <Route path="*">
                  <NotFound />
                </Route>
              </Fragment>
            ) : null}
          </Switch>
        </div>
      </Router>
    );
  } else {
    return (
      <Router>
        <div className="App">
          <Switch>
            <Fragment>
              <Route exact path={["/homePage", ""]}>
                <Redirect to="/login" />
              </Route>
              <Route path="/login">
                <Login />
              </Route>
              <Route path="/register">
                <Register />
              </Route>
              <Route path="/passwort-vergessen">
                <PasswortVergessen />
              </Route>
              <Route path="/passwort-zuruecksetzen/:token">
                <PasswortZuruecksetzen />
              </Route>
              <Route path="*">
                <NotFound2 />
              </Route>
            </Fragment>
          </Switch>
        </div>
      </Router>
    );
  }
};

const mapStateToProps = (state) => ({
  token: state.userReducer.token,
  vertrags: state.vertragsListReducer.vertragsList,
});
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      getVertragsArray,
      getGesellschaften,
      loadUser,
      getVertrag,
      getFullVertrag,
      loadUserBirthdays,
      loadUserFirstName,
      loadUserLastName,
      loadUserAddress,
      loadUserHandynummer,
      getAllGesellschaften,
    },
    dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
