import React, { useState, Fragment } from "react";
import Popup from "reactjs-popup";
import { Link, useParams, Redirect } from "react-router-dom";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import { Button } from "@material-ui/core";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTimes,
  faChevronLeft,
  faUser,
} from "@fortawesome/free-solid-svg-icons";
import useMediaQuery from "@material-ui/core/useMediaQuery";

import {
  updateUserData,
  loadUser,
  loadUserBirthdays,
  loadUserFirstName,
  loadUserLastName,
  loadUserAddress,
  loadUserHandynummer,
} from "../../redux/index";
import "./Profil.scss";
import MyTextField from "../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextField";
import MyDatePickerField from "../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyDatePickerField";

const SignupSchema = Yup.object().shape({
  contactGeburtsdatum: Yup.date()
    .required("Dieses Feld ist erforderlich")
    .typeError("Das Datum muss im richtigen Format vorliegen")
    .max(new Date(), "Geburtsdatum muss in der Vergangenheit gewesen sein"),
  contactVorname: Yup.string().required("Dieses Feld ist erforderlich"),
  contactNachname: Yup.string().required("Dieses Feld ist erforderlich"),
  contactStrasse: Yup.string().required("Dieses Feld ist erforderlich"),
  contactPostleitzahl: Yup.string().required("Dieses Feld ist erforderlich"),
  contactOrt: Yup.string().required("Dieses Feld ist erforderlich"),
  contactLand: Yup.string().required("Dieses Feld ist erforderlich"),
  contactHandynummer: Yup.number()
    .required("Dieses Feld ist erforderlich")
    .typeError("Bitte geben Sie die Nummer ein."),
  contactEmail: Yup.string().email().required("Dieses Feld ist erforderlich"),
});

const Profil = ({
  user,
  updateUserData,
  loadUser,
  handleShowMessage,
  setShowDetails,
  loadUserBirthdays,
  loadUserFirstName,
  loadUserLastName,
  loadUserAddress,
  loadUserHandynummer,
}) => {
  const [redirect, setRedirect] = useState(false);
  let { path, id } = useParams();

  const changeUserData = async (values) => {
    await updateUserData(values);
    loadUser();
    loadUserBirthdays();
    loadUserFirstName();
    loadUserLastName();
    loadUserAddress();
    loadUserHandynummer();
    handleShowMessage("showMessage3");
    setRedirect(true);
  };

  setShowDetails(false);
  const matches = useMediaQuery("(max-width:700px)");

  if (user !== null) {
    return (
      <Popup
        contentStyle={{ width: "100% !important" }}
        className="popup4"
        modal
        closeOnDocumentClick={false}
        modalOptions={{ dismissible: false }}
        closeOnEscape={false}
        defaultOpen={true}
      >
        {(close) => (
          <Formik
            initialValues={{
              contactVorname: user.contactVorname,
              contactNachname: user.contactNachname,
              contactStrasse: user.contactStrasse,
              contactPostleitzahl: user.contactPostleitzahl,
              contactOrt: user.contactOrt,
              contactLand: user.contactLand,
              contactGeburtsdatum: user.contactGeburtsdatum,
              contactHandynummer: user.contactHandynummer,
              contactEmail: user.contactEmail,
            }}
            enableReinitialize={true}
            validationSchema={SignupSchema}
            onSubmit={(values) => {
              if (!values.contactGeburtsdatum.length) {
                values.contactGeburtsdatum =
                  values.contactGeburtsdatum.getDate() +
                  "." +
                  (values.contactGeburtsdatum.getMonth() + 1) +
                  "." +
                  values.contactGeburtsdatum.getFullYear();
              }
              changeUserData(values);
            }}
          >
            {(values) => (
              <Form className="panel-ausfullen-form">
                <div className="form-header">
                  <div className="panel-ausfullen-form-row">
                    {matches ? <p>Dein Profil</p> : <p>Persönliche Angaben</p>}
                    <Link
                      to={id !== undefined ? `/Kuendigungs/${id}` : `/${path}`}
                    >
                      <i
                        onClick={() => {
                          close();
                        }}
                        id="button-close-info-vertrag-uebersicht"
                      >
                        <FontAwesomeIcon icon={faTimes} />
                      </i>
                      <i
                        onClick={() => {
                          close();
                        }}
                        id="button-close-info-vertrag-uebersicht2"
                      >
                        <FontAwesomeIcon icon={faChevronLeft} />
                      </i>
                    </Link>
                  </div>
                  {!matches ? <div className="text">Profil:</div> : null}
                  <p>
                    Deine Kontaktdaten werden automatisch in deine
                    Kündigungsvorlagen eingetragen, das spart Zeit und
                    ermöglicht dir eine blitzschnelle Kündigung.
                  </p>
                </div>
                <div className="after">
                  <div className="inside-after"></div>
                </div>
                <div className="form-body">
                  {matches ? (
                    <div
                      className="panel-ausfullen-first-content"
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "space-between",
                      }}
                    >
                      <div className="panel-ausfullen-user-photo">
                        <FontAwesomeIcon icon={faUser} />
                      </div>
                      <div
                        className={`${
                          matches
                            ? "panel-ausfullen-form-row-700px"
                            : "panel-ausfullen-form-row"
                        }`}
                        style={{ width: "60%" }}
                      >
                        <MyTextField
                          placeholder="Vorname"
                          name="contactVorname"
                          type="text"
                          label="Vorname"
                        />
                        <MyTextField
                          placeholder="Nachname"
                          name="contactNachname"
                          type="text"
                          label="Nachname"
                        />
                        {matches ? (
                          <Field
                            style={{ width: "100%", marginBottom: "20px" }}
                            name="contactGeburtsdatum"
                            label="Geburtsdatum"
                            component={MyDatePickerField}
                          />
                        ) : null}
                      </div>
                    </div>
                  ) : (
                    <div
                      className={`${
                        matches
                          ? "panel-ausfullen-form-row-700px"
                          : "panel-ausfullen-form-row"
                      }`}
                    >
                      <MyTextField
                        placeholder="Vorname"
                        name="contactVorname"
                        type="text"
                        label="Vorname"
                      />
                      <MyTextField
                        placeholder="Nachname"
                        name="contactNachname"
                        type="text"
                        label="Nachname"
                      />
                    </div>
                  )}
                  {!matches ? null : (
                    <Fragment>
                      <div
                        className={`${
                          matches
                            ? "panel-ausfullen-form-row2-700px"
                            : "panel-ausfullen-form-row2"
                        }`}
                      >
                        <MyTextField
                          placeholder="Handynummer"
                          name="contactHandynummer"
                          type="text"
                          label="Handynummer"
                        />
                      </div>
                      <div
                        className={`${
                          matches
                            ? "panel-ausfullen-form-row-700px"
                            : "panel-ausfullen-form-row"
                        }`}
                      >
                        <MyTextField
                          placeholder="E-Mail"
                          name="contactEmail"
                          type="email"
                          label="E-Mail"
                        />
                      </div>
                    </Fragment>
                  )}

                  <div
                    className={`${
                      matches
                        ? "panel-ausfullen-form-row2-700px"
                        : "panel-ausfullen-form-row2"
                    }`}
                  >
                    <MyTextField
                      placeholder="Straße"
                      name="contactStrasse"
                      type="text"
                      label="Straße"
                    />
                  </div>
                  <div
                    className={`${
                      matches
                        ? "panel-ausfullen-form-row-700px"
                        : "panel-ausfullen-form-row"
                    }`}
                  >
                    {matches ? null : (
                      <MyTextField
                        placeholder="Postleitzahl"
                        name="contactPostleitzahl"
                        type="text"
                        label="Postleitzahl"
                      />
                    )}
                    <MyTextField
                      placeholder="Ort"
                      name="contactOrt"
                      type="text"
                      label="Ort"
                    />
                    {!matches ? null : (
                      <MyTextField
                        placeholder="Postleitzahl"
                        name="contactPostleitzahl"
                        type="text"
                        label="Postleitzahl"
                      />
                    )}
                  </div>
                  <div
                    className={`${
                      matches
                        ? "panel-ausfullen-form-row-700px"
                        : "panel-ausfullen-form-row"
                    }`}
                  >
                    <MyTextField
                      placeholder="Land"
                      name="contactLand"
                      type="text"
                      label="Land"
                    />
                    {!matches ? (
                      <Field
                        style={{ width: "46.5%" }}
                        name="contactGeburtsdatum"
                        label="Geburtsdatum"
                        component={MyDatePickerField}
                      />
                    ) : null}
                  </div>
                  {matches ? null : <div className="text">Kontaktdaten:</div>}
                  {matches ? null : (
                    <div
                      className={`${
                        matches
                          ? "panel-ausfullen-form-row2-700px"
                          : "panel-ausfullen-form-row2"
                      }`}
                    >
                      <MyTextField
                        placeholder="Handynummer"
                        name="contactHandynummer"
                        type="text"
                        label="Handynummer"
                      />
                    </div>
                  )}
                  {matches ? null : (
                    <div
                      className={`${
                        matches
                          ? "panel-ausfullen-form-row2-700px"
                          : "panel-ausfullen-form-row2"
                      }`}
                    >
                      <MyTextField
                        placeholder="E-Mail"
                        name="contactEmail"
                        type="email"
                        label="E-Mail"
                      />
                    </div>
                  )}
                </div>
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  style={{
                    height: "50px",
                    margin: "20px",
                    display: "flex",
                    width: "auto",
                    borderRadius: "15px",
                  }}
                >
                  Speichern
                </Button>
                {redirect ? (
                  <Redirect
                    to={id !== undefined ? `/Kuendigung/${id}` : `/${path}`}
                  />
                ) : null}
              </Form>
            )}
          </Formik>
        )}
      </Popup>
    );
  } else {
    return null;
  }
};

const mapStateToProps = (state) => ({
  user: state.userReducer.user,
});

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      updateUserData,
      loadUser,
      loadUserBirthdays,
      loadUserFirstName,
      loadUserLastName,
      loadUserAddress,
      loadUserHandynummer,
    },
    dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(Profil);
