import React, { Fragment, useState, useRef, useEffect } from "react";
import MyDatePickerField from "./ContractsElements/MyDatePickerField";
import { Formik, Form, Field } from "formik";
import { Button, Checkbox } from "@material-ui/core";
import * as Yup from "yup";
import IconButton from "@material-ui/core/IconButton";
import PhotoCamera from "@material-ui/icons/PhotoCamera";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFileAlt,
  faCloudDownloadAlt,
  faTrashAlt,
} from "@fortawesome/free-solid-svg-icons";
import { useLocation, Link } from "react-router-dom";

import "./VertragsForm.scss";
import MyCheckbox from "./ContractsElements/MyCheckBox";
import MyTextField from "./ContractsElements/MyTextField";
import MyTextFieldKosten from "./ContractsElements/MyTextFieldKosten";
import MySelectField from "./ContractsElements/MySelectField";
import MyTextAreaField from "./ContractsElements/MyTextAreaField";

const SignupSchema = Yup.object().shape({
  vertragsName: Yup.string().required("Dieses Feld ist erforderlich"),
  vertragsnummer: Yup.mixed().when("vertragsName", {
    is: (val) =>
      val === "Fernsehen" ||
      val === "Fitnessstudio" ||
      val === "Kredit" ||
      val === "Bauherrenhaftpflicht" ||
      val === "Mobiles Internet" ||
      val === "Rechtsschutz" ||
      val === "Hausrat" ||
      val === "Krankenzusatz-Versicherung" ||
      val === "Reiseversicherung" ||
      val === "Gebäudeversicherung" ||
      val === "Unfallversicherung" ||
      val === "Sterbegeld-Versicherung" ||
      val === "Berufsunfähigkeit" ||
      val === "Grundbesitzer-Haftpflicht" ||
      val === "Pflegeversicherung" ||
      val === "Automobil-Club" ||
      val === "Betriebliche Altersvorsorge" ||
      val === "Auslandsversicherung" ||
      val === "Rürup Rente",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  kundennummer: Yup.mixed().when("vertragsName", {
    is: (val) =>
      val === "Strom" ||
      val === "Fernsehen" ||
      val === "Gas" ||
      val === "Handy" ||
      val === "Internet" ||
      val === "Mobiles Internet" ||
      val === "Pay-TV" ||
      val === "Zahnzusatz-Versicherung" ||
      val === "Depot",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  zählernummer: Yup.mixed().when("vertragsName", {
    is: (val) => val === "Strom" || val === "Gas",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  mobilfunknummer: Yup.mixed().when("vertragsName", {
    is: "Handy",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Telefonnummer ein."),
    otherwise: Yup.number(),
  }),
  mitgliedsnummer: Yup.mixed().when("vertragsName", {
    is: (val) => val === "Fitnessstudio" || val === "Automobil-Club",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  darlehensnummer: Yup.mixed().when("vertragsName", {
    is: "Kredit",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  versicherungsnummer: Yup.mixed().when("vertragsName", {
    is: (val) =>
      val === "Riester Rente" ||
      val === "Bauherrenhaftpflicht" ||
      val === "Lebensversicherung" ||
      val === "KFZ-Versicherung" ||
      val === "Krankenversicherung" ||
      val === "Rechtsschutz" ||
      val === "Hausrat" ||
      val === "Haftpflicht" ||
      val === "Krankenzusatz-Versicherung" ||
      val === "Reiseversicherung" ||
      val === "Gebäudeversicherung" ||
      val === "Unfallversicherung" ||
      val === "Motorrad-Versicherung" ||
      val === "Sterbegeld-Versicherung" ||
      val === "Tierhalterhaftpflicht" ||
      val === "Berufsunfähigkeit" ||
      val === "Grundbesitzer-Haftpflicht" ||
      val === "Pflegeversicherung" ||
      val === "Betriebliche Altersvorsorge" ||
      val === "Auslandsversicherung" ||
      val === "Elektrogeräte-Versicherung" ||
      val === "Zahnzusatz-Versicherung" ||
      val === "Rürup Rente" ||
      val === "Bootsversicherung" ||
      val === "Fahrrad-Versicherung" ||
      val === "Glasversicherung",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  policennummer: Yup.mixed().when("vertragsName", {
    is: (val) =>
      val === "Riester Rente" ||
      val === "Lebensversicherung" ||
      val === "Haftpflicht" ||
      val === "Bootsversicherung" ||
      val === "Fahrrad-Versicherung" ||
      val === "Glasversicherung",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  festnetzNummer: Yup.mixed().when("vertragsName", {
    is: "Internet",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  kfzKennzeichen: Yup.mixed().when("vertragsName", {
    is: "KFZ-Versicherung",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  krankenkassennummer: Yup.mixed().when("vertragsName", {
    is: "Krankenversicherung",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  registrierteEmailAdresse: Yup.mixed().when("vertragsName", {
    is: (val) => val === "Pay-TV" || val === "Online-Games & Konsole",
    then: Yup.string()
      .email("ungültige E-Mail-Adresse")
      .required("Dieses Feld ist erforderlich"),
    otherwise: Yup.string().email(),
  }),
  kennzeichen: Yup.mixed().when("vertragsName", {
    is: "Motorrad-Versicherung",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  registriernummer: Yup.mixed().when("vertragsName", {
    is: "Tierhalterhaftpflicht",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  kontonummer: Yup.mixed().when("vertragsName", {
    is: (val) =>
      val === "Tagesgeldkonto" ||
      val === "Festgeldkonto" ||
      val === "Sparkonto",
    then: Yup.string()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.string(),
  }),
  benutzername: Yup.mixed().when("vertragsName", {
    is: (val) => val === "Online-Games & Konsole",
    then: Yup.string().required("Dieses Feld ist erforderlich"),
    otherwise: Yup.string(),
  }),
  sparbuchnummer: Yup.mixed().when("vertragsName", {
    is: "Sparkonto",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  gerätenummer: Yup.mixed().when("vertragsName", {
    is: "Elektrogeräte-Versicherung",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  bausparvertragsnummer: Yup.mixed().when("vertragsName", {
    is: "Bausparvertrag",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  anschrift: Yup.mixed().when("vertragsName", {
    is: "Bausparvertrag",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  depotnummer: Yup.mixed().when("vertragsName", {
    is: "Depot",
    then: Yup.number()
      .required("Dieses Feld ist erforderlich")
      .typeError("Bitte geben Sie die Nummer ein."),
    otherwise: Yup.number(),
  }),
  vertragsende: Yup.date()
    .typeError("Das Datum muss im richtigen Format vorliegen")
    .required("Dieses Feld ist erforderlich")
    .min(new Date(), "Das Vertragsende muss in der Zukunft liegen."),
  vertragsanfang: Yup.date()
    .max(
      Yup.ref("vertragsende"),
      "Der Vertrag sollte vor Vertragsende beginnen"
    )
    .typeError("Das Datum muss im richtigen Format vorliegen")
    .required("Dieses Feld ist erforderlich"),
  kosten: Yup.number()
    .required("Dieses Feld ist erforderlich")
    .typeError("Bitte einen Wert zwischen 0,00 und 9999,99 € eingeben."),
  zahlweise: Yup.string().required(),
});

const VertragForm = ({
  addFullVertrags,
  editFullVertrags,
  anbieter,
  vertragsName,
  alert,
  vertragDetail = {},
  userId = "",
}) => {
  const [file, setFile] = useState([]);
  const [detailForm, setDetailForm] = useState(false);
  const [enableReinitialize, setEnableReinitialize] = useState(false);
  const [oldFiles, setOldFiles] = useState([]);
  const inputEl = useRef(null);
  const fileSelectedHandler = (e) => {
    setFile([...file, e.target.files[0]]);
    e.target.value = null;
  };

  //only when clicked on path element
  const handleDeleteFileFromList = (e) => {
    let target = "";
    if (e.target.className.baseVal === "") {
      target = e.target.parentNode.parentNode.parentNode.childNodes[1].getAttribute(
        "class"
      );
    } else {
      target = e.target.parentNode.parentNode.childNodes[1].getAttribute(
        "class"
      );
    }
    let newArrList = file.filter((el) => {
      console.log(el)
      if (el.downloadFile !== undefined && el.downloadFile === target) {
        setOldFiles([...oldFiles, '"' + el.downloadFile + '"']);
        return el.downloadFile !== target;
      } else {
        return el.name !== target;
      }
    });
    setFile(newArrList);
  };
  console.log(oldFiles);
  console.log(file)
  //IF DETAIL FORM
  const location = useLocation();

  useEffect(() => {
    if (location.pathname.includes("detail")) {
      setDetailForm(true);
      setEnableReinitialize(true);
    }
  }, [location]);

  let setInitialValues = {};

  useEffect(
    () => {
      if (detailForm) {
        let filesExist = vertragDetail.files.split(", ");
        if (filesExist.length !== 0 && filesExist[0] !== "") {
          filesExist.map((el) => {
            if (el.replace(/\s/g, "").length === 0) {
              return true;
            } else {
              //delete """ from string and make array from string
              let nameArr = el.substring(1).slice(0, -1).split("");
              //reverse array and find extension position
              let reverseArr = nameArr.reverse();
              let extensionPosition = reverseArr.indexOf(".");
              //delete extension
              let nameWithoutExt = reverseArr.splice(extensionPosition + 1);
              //delete timestamp from name
              let cleanName = nameWithoutExt.splice(14);
              //convert from array to string
              let name = cleanName.reverse().join("");
              return file.push({
                name: name,
                downloadFile: el.substring(1).slice(0, -1),
              });
            }
          });
        }
      }
    }, // eslint-disable-next-line
    [detailForm]
  );

  if (detailForm) {
    let vertragDetailArr = Object.keys(vertragDetail).map((key) => [
      key,
      vertragDetail[key],
    ]);
    // eslint-disable-next-line
    vertragDetailArr.map((el) => {
      if (
        el[0] !== "user_id" &&
        el[0] !== "vertrag_anbieter" &&
        el[0] !== "vertrag_name" &&
        el[0] !== "isGekuendigt" &&
        el[1] !== null
      ) {
        setInitialValues[el[0]] = el[1];
      } else if (el[0] === "vertrag_anbieter") {
        setInitialValues["anbieter"] = el[1];
      } else if (el[0] === "vertrag_name") {
        setInitialValues["vertragsName"] = el[1];
      } else if (el[0] === "isGekuendigt") {
        let myBoolVal = false;
        if (el[1] === "true") {
          myBoolVal = true;
        }
        setInitialValues[el[0]] = myBoolVal;
      }
    });
    setInitialValues["filesToDelete"] = [];
  } else {
    setInitialValues = {
      vertragsName: vertragsName,
      anbieter: anbieter,
      vertragsnummer: "",
      kundennummer: "",
      zählernummer: "",
      mobilfunknummer: "",
      mitgliedsnummer: "",
      darlehensnummer: "",
      versicherungsnummer: "",
      policennummer: "",
      festnetzNummer: "",
      kfzKennzeichen: "",
      krankenkassennummer: "",
      registrierteEmailAdresse: "",
      registriernummer: "",
      kontonummer: "",
      kennzeichen: "",
      benutzername: "",
      sparbuchnummer: "",
      gerätenummer: "",
      bausparvertragsnummer: "",
      anschrift: "",
      depotnummer: "",
      isGekuendigt: false,
      vertragsanfang: new Date(),
      vertragsende: new Date(),
      kosten: "",
      zahlweise: "",
      file: [],
      notizen: "",
    };
  }

  return (
    <Formik
      initialValues={setInitialValues}
      enableReinitialize={enableReinitialize}
      validationSchema={SignupSchema}
      onSubmit={(values) => {
        values.vertragsende = new Date(values.vertragsende)
          .toISOString()
          .slice(0, 10);
        values.vertragsanfang = new Date(values.vertragsanfang)
          .toISOString()
          .slice(0, 10);
        values.file = file;
        values.filesToDelete = oldFiles.join(",");
        if (detailForm) {
          let newFileArr = [];
          // eslint-disable-next-line
          values.file.map((el, i) => {
            if (el.downloadFile === undefined) {
              newFileArr.push(el);
            }
          });
          values.file = newFileArr;
          console.log(values);
          editFullVertrags(values);
        } else {
          addFullVertrags(values);
        }
      }}
    >
      {({ values }) => (
        <div className="panel-ausfullen-details">
          <Form noValidate className="panel-ausfullen-form">
            <div className="text">Details:</div>
            <div
              className="panel-ausfullen-form-row"
              style={{ flexDirection: "column" }}
            >
              <div style={{ display: "flex", flexDirection: "row" }}>
                <label
                  htmlFor="raised-button-file"
                  className="raised-button-file"
                >
                  <input
                    id="icon-button-file"
                    type="file"
                    style={{ display: "none" }}
                    onChange={(e) => fileSelectedHandler(e)}
                    disabled={file.length === 10}
                    ref={inputEl}
                  />
                  <label htmlFor="icon-button-file">
                    <IconButton
                      color="primary"
                      aria-label="upload picture"
                      component="span"
                    >
                      <PhotoCamera />
                    </IconButton>
                  </label>
                </label>
                <p style={{ margin: "auto", fontSize: "14px", color: "black" }}>
                  Sie können noch {10 - file.length} Dateien hochladen
                </p>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                {file.map((file, i) => (
                  <div className="uploadFileElement" key={i}>
                    <i className="fileIcon">
                      <FontAwesomeIcon icon={faFileAlt} />
                    </i>
                    {file.name.length > 80 ? (
                      <p
                        className={`${
                          file.downloadFile !== undefined
                            ? file.downloadFile
                            : file.name
                        }`}
                      >{`${file.name.substring(0, 80)}...`}</p>
                    ) : (
                      <p
                        className={`${
                          file.downloadFile !== undefined
                            ? file.downloadFile
                            : file.name
                        }`}
                      >{`${file.name}`}</p>
                    )}
                    <i className="trashIcon">
                      <FontAwesomeIcon
                        onClick={(e) => handleDeleteFileFromList(e)}
                        icon={faTrashAlt}
                      />
                    </i>
                    {file.downloadFile !== undefined ? (
                      <a
                        style={{ padding: "20px 15px", color: "#3f51b5" }}
                        href={`${process.env.PUBLIC_URL}/assets/contractFiles/${userId}/${file.downloadFile}`}
                        download={`${file.name}`}
                      >
                        <i>
                          <FontAwesomeIcon icon={faCloudDownloadAlt} />
                        </i>
                      </a>
                    ) : null}
                  </div>
                ))}
              </div>
            </div>
            <div
              className="panel-ausfullen-form-row"
              style={{ marginTop: "20px" }}
            >
              {values.vertragsName === "Fernsehen" ||
              values.vertragsName === "Fitnessstudio" ||
              values.vertragsName === "Kredit" ||
              values.vertragsName === "Bauherrenhaftpflicht" ||
              values.vertragsName === "Mobiles Internet" ||
              values.vertragsName === "Rechtsschutz" ||
              values.vertragsName === "Hausrat" ||
              values.vertragsName === "Krankenzusatz-Versicherung" ||
              values.vertragsName === "Reiseversicherung" ||
              values.vertragsName === "Gebäudeversicherung" ||
              values.vertragsName === "Unfallversicherung" ||
              values.vertragsName === "Sterbegeld-Versicherung" ||
              values.vertragsName === "Berufsunfähigkeit" ||
              values.vertragsName === "Grundbesitzer-Haftpflicht" ||
              values.vertragsName === "Pflegeversicherung" ||
              values.vertragsName === "Automobil-Club" ||
              values.vertragsName === "Betriebliche Altersvorsorge" ||
              values.vertragsName === "Auslandsversicherung" ||
              values.vertragsName === "Rürup Rente" ? (
                <MyTextField
                  placeholder="Vertragsnummer"
                  name="vertragsnummer"
                  type="text"
                  label="Vertragsnummer"
                />
              ) : null}
              {values.vertragsName === "Fernsehen" ||
              values.vertragsName === "Strom" ||
              values.vertragsName === "Gas" ||
              values.vertragsName === "Handy" ||
              values.vertragsName === "Internet" ||
              values.vertragsName === "Mobiles Internet" ||
              values.vertragsName === "Pay-TV" ||
              values.vertragsName === "Zahnzusatz-Versicherung" ||
              values.vertragsName === "Depot" ? (
                <MyTextField
                  placeholder="Kundennummer"
                  name="kundennummer"
                  type="text"
                  label="Kundennummer"
                />
              ) : null}
              {values.vertragsName === "Gas" ||
              values.vertragsName === "Strom" ? (
                <MyTextField
                  placeholder="Zählernummer"
                  name="zählernummer"
                  type="text"
                  label="Zählernummer"
                />
              ) : null}
              {values.vertragsName === "Handy" ? (
                <MyTextField
                  placeholder="Mobilfunknummer"
                  name="mobilfunknummer"
                  type="text"
                  label="Mobilfunknummer"
                />
              ) : null}
              {values.vertragsName === "Fitnessstudio" ||
              values.vertragsName === "Automobil-Club" ? (
                <MyTextField
                  placeholder="Mitgliedsnummer"
                  name="mitgliedsnummer"
                  type="text"
                  label="Mitgliedsnummer"
                />
              ) : null}
              {values.vertragsName === "Kredit" ? (
                <MyTextField
                  placeholder="Darlehensnummer"
                  name="darlehensnummer"
                  type="text"
                  label="Darlehensnummer"
                />
              ) : null}
              {values.vertragsName === "Riester Rente" ||
              values.vertragsName === "Bauherrenhaftpflicht" ||
              values.vertragsName === "Lebensversicherung" ||
              values.vertragsName === "KFZ-Versicherung" ||
              values.vertragsName === "Krankenversicherung" ||
              values.vertragsName === "Rechtsschutz" ||
              values.vertragsName === "Hausrat" ||
              values.vertragsName === "Haftpflicht" ||
              values.vertragsName === "Krankenzusatz-Versicherung" ||
              values.vertragsName === "Reiseversicherung" ||
              values.vertragsName === "Gebäudeversicherung" ||
              values.vertragsName === "Unfallversicherung" ||
              values.vertragsName === "Motorrad-Versicherung" ||
              values.vertragsName === "Sterbegeld-Versicherung" ||
              values.vertragsName === "Tierhalterhaftpflicht" ||
              values.vertragsName === "Berufsunfähigkeit" ||
              values.vertragsName === "Grundbesitzer-Haftpflicht" ||
              values.vertragsName === "Pflegeversicherung" ||
              values.vertragsName === "Betriebliche Altersvorsorge" ||
              values.vertragsName === "Auslandsversicherung" ||
              values.vertragsName === "Elektrogeräte-Versicherung" ||
              values.vertragsName === "Zahnzusatz-Versicherung" ||
              values.vertragsName === "Rürup Rente" ||
              values.vertragsName === "Bootsversicherung" ||
              values.vertragsName === "Fahrrad-Versicherung" ||
              values.vertragsName === "Glasversicherung" ? (
                <MyTextField
                  placeholder="Versicherungsnummer"
                  name="versicherungsnummer"
                  type="text"
                  label="Versicherungsnummer"
                />
              ) : null}
              {values.vertragsName === "Riester Rente" ||
              values.vertragsName === "Lebensversicherung" ||
              values.vertragsName === "Haftpflicht" ||
              values.vertragsName === "Bootsversicherung" ||
              values.vertragsName === "Fahrrad-Versicherung" ||
              values.vertragsName === "Glasversicherung" ? (
                <MyTextField
                  placeholder="Policennummer"
                  name="policennummer"
                  type="text"
                  label="Policennummer"
                />
              ) : null}
              {values.vertragsName === "Internet" ? (
                <MyTextField
                  placeholder="Festnetz-Nummer"
                  name="festnetzNummer"
                  type="text"
                  label="Festnetz-Nummer"
                />
              ) : null}
              {values.vertragsName === "KFZ-Versicherung" ? (
                <MyTextField
                  placeholder="KFZ-Kennzeichen"
                  name="kfzKennzeichen"
                  type="text"
                  label="KFZ-Kennzeichen"
                />
              ) : null}
              {values.vertragsName === "Krankenversicherung" ? (
                <MyTextField
                  placeholder="Krankenkassennummer"
                  name="krankenkassennummer"
                  type="text"
                  label="Krankenkassennummer"
                />
              ) : null}
              {values.vertragsName === "Pay-TV" ||
              values.vertragsName === "Online-Games & Konsole" ? (
                <MyTextField
                  placeholder="Registrierte E-Mail Adresse"
                  name="registrierteEmailAdresse"
                  type="text"
                  label="Registrierte E-Mail Adresse"
                />
              ) : null}
              {values.vertragsName === "Motorrad-Versicherung" ? (
                <MyTextField
                  placeholder="Kennzeichen"
                  name="kennzeichen"
                  type="text"
                  label="Kennzeichen"
                />
              ) : null}
              {values.vertragsName === "Tierhalterhaftpflicht" ? (
                <MyTextField
                  placeholder="Registriernummer"
                  name="registriernummer"
                  type="text"
                  label="Registriernummer"
                />
              ) : null}
              {values.vertragsName === "Tagesgeldkonto" ||
              values.vertragsName === "Festgeldkonto" ||
              values.vertragsName === "Sparkonto" ? (
                <MyTextField
                  placeholder="Kontonnumer"
                  name="kontonummer"
                  type="text"
                  label="Kontonummer"
                />
              ) : null}
              {values.vertragsName === "Online-Games & Konsole" ? (
                <MyTextField
                  placeholder="Benutzername"
                  name="benutzername"
                  type="text"
                  label="Benutzername"
                />
              ) : null}
              {values.vertragsName === "Sparkonto" ? (
                <MyTextField
                  placeholder="Sparbuchnummer"
                  name="sparbuchnummer"
                  type="text"
                  label="Sparbuchnummer"
                />
              ) : null}
              {values.vertragsName === "Elektrogeräte-Versicherung" ? (
                <MyTextField
                  placeholder="Gerätenummer"
                  name="gerätenummer"
                  type="text"
                  label="Gerätenummer"
                />
              ) : null}
              {values.vertragsName === "Bausparvertrag" ? (
                <MyTextField
                  placeholder="Bausparvertragsnummer"
                  name="bausparvertragsnummer"
                  type="text"
                  label="Bausparvertragsnummer"
                />
              ) : null}
              {values.vertragsName === "Bausparvertrag" ? (
                <MyTextField
                  placeholder="Anschrift"
                  name="anschrift"
                  type="text"
                  label="Anschrift"
                />
              ) : null}
              {values.vertragsName === "Depot" ? (
                <MyTextField
                  placeholder="Depotnummer"
                  name="depotnummer"
                  type="text"
                  label="Depotnummer"
                />
              ) : null}
            </div>
            <div className="text">Laufzeit und Fristen:</div>
            <MyCheckbox
              name="isGekuendigt"
              type="checkbox"
              label="Schon gekündigt?"
              as={Checkbox}
            />
            <Fragment>
              <Field
                name="vertragsanfang"
                label="Vertragsanfang"
                component={MyDatePickerField}
              />
              <div
                style={{
                  marginTop: "30px",
                }}
              />
              <Field
                name="vertragsende"
                label="Vertragsende"
                component={MyDatePickerField}
              />
              <div
                style={{
                  marginTop: "30px",
                }}
              />
              <div
                className="panel-ausfullen-form-row"
                style={{ marginBottom: "20px" }}
              >
                <MySelectField
                  name="zahlweise"
                  label="Zahlweise"
                  placeholder="Zahlweise"
                />
                <MyTextFieldKosten
                  placeholder="0,00"
                  name="kosten"
                  label="Kosten"
                />
              </div>
              <div
                className="panel-ausfullen-form-row textField-row"
                style={{ justifyContent: "space-around", marginBottom: "20px" }}
              >
                <MyTextAreaField
                  id="outlined-multiline-static"
                  name="notizen"
                  label="Notizen"
                />
              </div>
            </Fragment>
            <div
              className="panel-ausfullen-form-row"
              style={{ justifyContent: "space-around", marginBottom: "20px" }}
            >
              <Button type="submit" variant="contained" color="primary">
                Speichern
              </Button>
              <Button variant="contained" color="secondary" onClick={alert}>
                Vertrag löschen
              </Button>
            </div>
            {detailForm ? (
              <div
                className="panel-ausfullen-form-row"
                style={{ justifyContent: "space-around", marginBottom: "20px" }}
              >
                <Button variant="contained" className="kuendigung-button">
                  <Link to={`/Kuendigungs/${vertragDetail.vertrag_id}`}>
                    Vertrag kündigen
                  </Link>
                </Button>
              </div>
            ) : null}
          </Form>
        </div>
      )}
    </Formik>
  );
};

export default VertragForm;
