import React from "react";
import { useField } from "formik";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import FormHelperText from "@material-ui/core/FormHelperText";

const MySelectField = ({ label, placeholder, type, ...props }) => {
  const [field, meta] = useField(props);
  const errorText = meta.error && meta.touched ? meta.error : null;

  return (
    <FormControl variant="outlined" style={{ marginBottom: "20px" }}>
      <InputLabel id="outlined-adornment-amount">
        Wie hast du uns kennengelernt?
      </InputLabel>
      <Select
        {...field}
        label={label}
        placeholder={placeholder}
        variant="outlined"
        type={type}
        error={errorText ? true : false} //or error={!!errorText}
      >
        <MenuItem value="1">Berater</MenuItem>
        <MenuItem value="2">Internet</MenuItem>
        <MenuItem value="3">Radio Werbung</MenuItem>
        <MenuItem value="4">Zeitung</MenuItem>
        <MenuItem value="5">Empfehlung</MenuItem>
        <MenuItem value="6">Sonstiges</MenuItem>
      </Select>
      {errorText ? (
        <FormHelperText style={{ color: "#f44336" }}>
          Dieses Field ist erforderlich
        </FormHelperText>
      ) : null}
    </FormControl>
  );
};

export default MySelectField;
