import React from "react";
import { Link } from "react-router-dom";

const InfoHandy = (props) => {
  if (props.value0 === "1" || props.value1 === "1" || props.value1 === "2") {
    return (
      <div id="right-container-info-handy">
        <h1>
          <a href="tel:+49 12 2343 345 53">+49 12 2343 345 53</a>
        </h1>
        <p>
          Bitte ruf uns am besten direkt an, damit wir deinen
          Versicherungsschaden aufnehmen können. Du erreichst uns Montag bis
          Freitag von 09 - 17 Uhr.
        </p>
        <Link to="/vertragsUebersicht">
          <div className="info-handy-button">Zu deinen Verträgen</div>
        </Link>
      </div>
    );
  } else if (props.submit) {
    return null;
  }
  return null;
};

export default InfoHandy;
