import axios from "axios";
import { tokenConfig } from "../index";
import {
  VERTRAGS_LOADING,
  GET_VERTRAG,
  ADD_VERTRAG,
  DELETE_VERTRAG,
} from "./vertragTypes";

export const getVertrag = () => (dispatch, getState) => {
  dispatch(setVertragsLoading());
  getState().vertragReducer.vertrags = [];

  axios
    .get("/vertragsUebersicht/getVertrags", tokenConfig(getState))
    .then((res) => {
      // eslint-disable-next-line
      res.data.results.map((el) => {
        axios({
          method: "post",
          url: "/vertragsUebersicht/getVertrags2",
          headers: tokenConfig(getState).headers,
          data: {
            sparteId: el.vertrag_sparte,
            gesellschaftId: el.vertrag_gesellschaft,
            vertragId: el.vertrag_id,
          },
        }).then((res) => {
          dispatch({
            type: GET_VERTRAG,
            payload: res.data.vertrag,
          });
        });
      });
    });
};

export const addVertrag = (vertrag) => (dispatch, getState) => {
  axios.post("/vertragsAuswahl", vertrag, tokenConfig(getState)).then((res) => {
    dispatch({
      type: ADD_VERTRAG,
      payload: res.data.vertrag,
    });
  });
};

export const deleteVertrag = (id) => (dispatch, getState) => {
  axios
    .delete(`/vertragsUebersicht/contract/${id}`, tokenConfig(getState))
    .then((res) => {
      dispatch({
        type: DELETE_VERTRAG,
        payload: id,
      });
    });
};

export const setVertragsLoading = () => {
  return {
    type: VERTRAGS_LOADING,
  };
};
