import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronLeft, faTimes } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";

import VertragsFormAssistent from "../VertragsFormAssistent/VertragsFormAssistent";
import "./VertragsCardAssistent.scss";

const VertragsCardAssistent = ({
  handleChangePositionMinus,
  vertragsName,
  vertragsAnbieter,
  handleDateiFromForm,
  showWarningText,
  assistentArray,
  changeLastButtonText,
  sendFormToTheServer,
}) => {
  return (
    <div className="cardAssistentContainer">
      <div className="cardAssistentHeader">
        <div className="back" onClick={handleChangePositionMinus}>
          <FontAwesomeIcon icon={faChevronLeft} />
        </div>
        <div className="image-container">
          <img
            src={require(`../../../../../../assets/vertragsImages/${vertragsName}.png`)}
            alt={vertragsName}
          />
        </div>
        <div className="info-container">
          <p>{vertragsName}</p>
          <p>{vertragsAnbieter}</p>
        </div>
        <Link to="/vertragsUebersicht">
          <i id="buttonCloseDialog1">
            <FontAwesomeIcon icon={faTimes} />
          </i>
        </Link>
      </div>
      <div className="cardAssistentBody">
        <VertragsFormAssistent
          vertragsAnbieter={vertragsAnbieter}
          handleDateiFromForm={handleDateiFromForm}
          vertragsName={vertragsName}
          showWarningText={showWarningText}
          assistentArray={assistentArray}
          changeLastButtonText={changeLastButtonText}
        />
      </div>
    </div>
  );
};

export default VertragsCardAssistent;
