import axios from "axios";
import { tokenConfig } from "../index";
import {
  GET_SPARTEN,
  SPARTEN_LOADING,
  GET_GESELLSCHAFTEN,
  GESELLSCHAFTEN_LOADING,
} from "./vertragsArrayTypes";

export const sendGesellschaft = (anbieter, sparte, id) => (
  dispatch,
  getState
) => {
  axios({
    method: "post",
    url: "/sendGesellschaft",
    headers: tokenConfig(getState).headers,
    data: { anbieter: anbieter, sparte: sparte, sparteId: id },
  });
};

export const getVertragsArray = () => (dispatch, getState) => {
  dispatch(vertragsArrayLoading());
  axios({
    method: "get",
    url: `/sparten`,
    headers: tokenConfig(getState).headers,
  })
    .then((res) => {
      dispatch({
        type: GET_SPARTEN,
        payload: res.data.vertragsArray,
      });
    })
    .catch((err) => console.log(err));
};

export const getGesellschaften = () => (dispatch, getState) => {
  dispatch(vertragsGesellschaftenLoading());
  axios({
    method: "get",
    url: `/gesellschaften`,
    headers: tokenConfig(getState).headers,
  })
    .then((res) => {
      dispatch({
        type: GET_GESELLSCHAFTEN,
        payload: res.data.gesellschaften,
      });
    })
    .catch((err) => console.log(err));
};

export const vertragsArrayLoading = () => {
  return {
    type: SPARTEN_LOADING,
  };
};

export const vertragsGesellschaftenLoading = () => {
  return {
    type: GESELLSCHAFTEN_LOADING,
  };
};
