import React from "react";
import { Formik, Form } from "formik";
import MyTextField from "../../../../VertragsContract/Contracts/ContractsElements/MyTextField";
import { Button } from "@material-ui/core";

import "./VertragsFormAssistent.scss";

const VertragsFormAssistent = ({
  vertragsName,
  handleDateiFromForm,
  showWarningText,
  assistentArray,
  changeLastButtonText,
  vertragsAnbieter,
}) => {
  return (
    <Formik
      initialValues={{
        vertragsAnbieter: vertragsAnbieter,
        vertragsName: vertragsName,
        vertragsnummer: "",
        kundennummer: "",
        zählernummer: "",
        mobilfunknummer: "",
        mitgliedsnummer: "",
        darlehensnummer: "",
        versicherungsnummer: "",
        policennummer: "",
        festnetzNummer: "",
        kfzKennzeichen: "",
        krankenkassennummer: "",
        registrierteEmailAdresse: "",
        registriernummer: "",
        kontonummer: "",
        kennzeichen: "",
        benutzername: "",
        sparbuchnummer: "",
        gerätenummer: "",
        bausparvertragsnummer: "",
        anschrift: "",
        depotnummer: "",
      }}
      enableReinitialize={true}
      onSubmit={(values) => {
        handleDateiFromForm(values);
      }}
    >
      {({ values }) => (
        <div className="panel-ausfullen-details">
          <Form
            noValidate
            className="panel-ausfullen-form"
            style={{ margin: "0px" }}
          >
            <div
              className="panel-ausfullen-form-row"
              style={{ flexDirection: "column", margin: "10px" }}
            >
              {values.vertragsName === "Fernsehen" ||
              values.vertragsName === "Fitnessstudio" ||
              values.vertragsName === "Kredit" ||
              values.vertragsName === "Bauherrenhaftpflicht" ||
              values.vertragsName === "Mobiles Internet" ||
              values.vertragsName === "Rechtsschutz" ||
              values.vertragsName === "Hausrat" ||
              values.vertragsName === "Krankenzusatz-Versicherung" ||
              values.vertragsName === "Reiseversicherung" ||
              values.vertragsName === "Gebäudeversicherung" ||
              values.vertragsName === "Unfallversicherung" ||
              values.vertragsName === "Sterbegeld-Versicherung" ||
              values.vertragsName === "Berufsunfähigkeit" ||
              values.vertragsName === "Grundbesitzer-Haftpflicht" ||
              values.vertragsName === "Pflegeversicherung" ||
              values.vertragsName === "Automobil-Club" ||
              values.vertragsName === "Betriebliche Altersvorsorge" ||
              values.vertragsName === "Auslandsversicherung" ||
              values.vertragsName === "Rürup Rente" ? (
                <MyTextField
                  placeholder="Vertragsnummer"
                  name="vertragsnummer"
                  type="text"
                  label="Vertragsnummer"
                />
              ) : null}
              {values.vertragsName === "Fernsehen" ||
              values.vertragsName === "Gas" ||
              values.vertragsName === "Strom" ||
              values.vertragsName === "Handy" ||
              values.vertragsName === "Internet" ||
              values.vertragsName === "Mobiles Internet" ||
              values.vertragsName === "Pay-TV" ||
              values.vertragsName === "Zahnzusatz-Versicherung" ||
              values.vertragsName === "Depot" ? (
                <MyTextField
                  placeholder="Kundennummer"
                  name="kundennummer"
                  type="text"
                  label="Kundennummer"
                />
              ) : null}
              {values.vertragsName === "Gas" ||
              values.vertragsName === "Strom" ? (
                <MyTextField
                  placeholder="Zählernummer"
                  name="zählernummer"
                  type="text"
                  label="Zählernummer"
                />
              ) : null}
              {values.vertragsName === "Handy" ? (
                <MyTextField
                  placeholder="Mobilfunknummer"
                  name="mobilfunknummer"
                  type="text"
                  label="Mobilfunknummer"
                />
              ) : null}
              {values.vertragsName === "Fitnessstudio" ||
              values.vertragsName === "Automobil-Club" ? (
                <MyTextField
                  placeholder="Mitgliedsnummer"
                  name="mitgliedsnummer"
                  type="text"
                  label="Mitgliedsnummer"
                />
              ) : null}
              {values.vertragsName === "Kredit" ? (
                <MyTextField
                  placeholder="Darlehensnummer"
                  name="darlehensnummer"
                  type="text"
                  label="Darlehensnummer"
                />
              ) : null}
              {values.vertragsName === "Riester Rente" ||
              values.vertragsName === "Bauherrenhaftpflicht" ||
              values.vertragsName === "Lebensversicherung" ||
              values.vertragsName === "KFZ-Versicherung" ||
              values.vertragsName === "Krankenversicherung" ||
              values.vertragsName === "Rechtsschutz" ||
              values.vertragsName === "Hausrat" ||
              values.vertragsName === "Haftpflicht" ||
              values.vertragsName === "Krankenzusatz-Versicherung" ||
              values.vertragsName === "Reiseversicherung" ||
              values.vertragsName === "Gebäudeversicherung" ||
              values.vertragsName === "Unfallversicherung" ||
              values.vertragsName === "Motorrad-Versicherung" ||
              values.vertragsName === "Sterbegeld-Versicherung" ||
              values.vertragsName === "Tierhalterhaftpflicht" ||
              values.vertragsName === "Berufsunfähigkeit" ||
              values.vertragsName === "Grundbesitzer-Haftpflicht" ||
              values.vertragsName === "Pflegeversicherung" ||
              values.vertragsName === "Betriebliche Altersvorsorge" ||
              values.vertragsName === "Auslandsversicherung" ||
              values.vertragsName === "Elektrogeräte-Versicherung" ||
              values.vertragsName === "Zahnzusatz-Versicherung" ||
              values.vertragsName === "Rürup Rente" ||
              values.vertragsName === "Bootsversicherung" ||
              values.vertragsName === "Fahrrad-Versicherung" ||
              values.vertragsName === "Glasversicherung" ? (
                <MyTextField
                  placeholder="Versicherungsnummer"
                  name="versicherungsnummer"
                  type="text"
                  label="Versicherungsnummer"
                />
              ) : null}
              {values.vertragsName === "Riester Rente" ||
              values.vertragsName === "Lebensversicherung" ||
              values.vertragsName === "Haftpflicht" ||
              values.vertragsName === "Bootsversicherung" ||
              values.vertragsName === "Fahrrad-Versicherung" ||
              values.vertragsName === "Glasversicherung" ? (
                <MyTextField
                  placeholder="Policennummer"
                  name="policennummer"
                  type="text"
                  label="Policennummer"
                />
              ) : null}
              {values.vertragsName === "Internet" ? (
                <MyTextField
                  placeholder="Festnetz-Nummer"
                  name="festnetzNummer"
                  type="text"
                  label="Festnetz-Nummer"
                />
              ) : null}
              {values.vertragsName === "KFZ-Versicherung" ? (
                <MyTextField
                  placeholder="KFZ-Kennzeichen"
                  name="kfzKennzeichen"
                  type="text"
                  label="KFZ-Kennzeichen"
                />
              ) : null}
              {values.vertragsName === "Krankenversicherung" ? (
                <MyTextField
                  placeholder="Krankenkassennummer"
                  name="krankenkassennummer"
                  type="text"
                  label="Krankenkassennummer"
                />
              ) : null}
              {values.vertragsName === "Pay-TV" ||
              values.vertragsName === "Online-Games & Konsole" ? (
                <MyTextField
                  placeholder="Registrierte E-Mail Adresse"
                  name="registrierteEmailAdresse"
                  type="text"
                  label="Registrierte E-Mail Adresse"
                />
              ) : null}
              {values.vertragsName === "Motorrad-Versicherung" ? (
                <MyTextField
                  placeholder="Kennzeichen"
                  name="kennzeichen"
                  type="text"
                  label="Kennzeichen"
                />
              ) : null}
              {values.vertragsName === "Tierhalterhaftpflicht" ? (
                <MyTextField
                  placeholder="Registriernummer"
                  name="registriernummer"
                  type="text"
                  label="Registriernummer"
                />
              ) : null}
              {values.vertragsName === "Tagesgeldkonto" ||
              values.vertragsName === "Festgeldkonto" ||
              values.vertragsName === "Sparkonto" ? (
                <MyTextField
                  placeholder="Kontonnumer"
                  name="kontonummer"
                  type="text"
                  label="Kontonummer"
                />
              ) : null}
              {values.vertragsName === "Online-Games & Konsole" ? (
                <MyTextField
                  placeholder="Benutzername"
                  name="benutzername"
                  type="text"
                  label="Benutzername"
                />
              ) : null}
              {values.vertragsName === "Sparkonto" ? (
                <MyTextField
                  placeholder="Sparbuchnummer"
                  name="sparbuchnummer"
                  type="text"
                  label="Sparbuchnummer"
                />
              ) : null}
              {values.vertragsName === "Elektrogeräte-Versicherung" ? (
                <MyTextField
                  placeholder="Gerätenummer"
                  name="gerätenummer"
                  type="text"
                  label="Gerätenummer"
                />
              ) : null}
              {values.vertragsName === "Bausparvertrag" ? (
                <MyTextField
                  placeholder="Bausparvertragsnummer"
                  name="bausparvertragsnummer"
                  type="text"
                  label="Bausparvertragsnummer"
                />
              ) : null}
              {values.vertragsName === "Bausparvertrag" ? (
                <MyTextField
                  placeholder="Anschrift"
                  name="anschrift"
                  type="text"
                  label="Anschrift"
                />
              ) : null}
              {values.vertragsName === "Depot" ? (
                <MyTextField
                  placeholder="Depotnummer"
                  name="depotnummer"
                  type="text"
                  label="Depotnummer"
                />
              ) : null}
            </div>
            {showWarningText && assistentArray.length === 0 ? (
              <p className="warningText">
                Bitte bei mindestens einem Vertrag füllen Sie die
                Vertragsinformationen aus.
              </p>
            ) : null}
            <div
              className="panel-ausfullen-form-row"
              style={{
                backgroundColor: "rgba(207, 203, 203, 0.14)",
                margin: "0px",
                height: "80px",
              }}
            >
              {values.vertragsnummer !== "" ||
              values.kundennummer !== "" ||
              values.zählernummer !== "" ||
              values.mobilfunknummer !== "" ||
              values.mitgliedsnummer !== "" ||
              values.darlehensnummer !== "" ||
              values.versicherungsnummer !== "" ||
              values.policennummer !== "" ||
              values.festnetzNummer !== "" ||
              values.kfzKennzeichen !== "" ||
              values.krankenkassennummer !== "" ||
              values.registrierteEmailAdresse !== "" ||
              values.registriernummer !== "" ||
              values.kontonummer !== "" ||
              values.kennzeichen !== "" ||
              values.benutzername !== "" ||
              values.sparbuchnummer !== "" ||
              values.gerätenummer !== "" ||
              values.bausparvertragsnummer !== "" ||
              values.anschrift !== "" ||
              values.depotnummer !== "" ? (
                changeLastButtonText ? (
                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                    className="buttonVertragsFormAssistent"
                  >
                    Daten abschicken
                  </Button>
                ) : (
                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                    className="buttonVertragsFormAssistent"
                  >
                    Weiter
                  </Button>
                )
              ) : changeLastButtonText ? (
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  className="buttonVertragsFormAssistent"
                >
                  Daten abschicken
                </Button>
              ) : (
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  className="buttonVertragsFormAssistent"
                >
                  Überspringen
                </Button>
              )}
            </div>
          </Form>
        </div>
      )}
    </Formik>
  );
};

export default VertragsFormAssistent;
