import React from "react";
import InputAdornment from "@material-ui/core/InputAdornment";
import TextField from "@material-ui/core/TextField";
import SearchIcon from "@material-ui/icons/Search";

import "./SearchElement.scss";

const SearchElement = ({
  vertrags,
  setVertrags,
  vertragsList2,
  setSearchText,
  removedVertragsEl,
}) => {
  const changeInput = (e) => {
    setVertrags((vertrags = vertragsList2));

    let vertragsHelpArr = [];

    for (let i = 0; i < removedVertragsEl.length; i++) {
      vertragsHelpArr = vertrags.filter(
        (el) => el.text !== removedVertragsEl[i].text
      );
      vertrags = vertragsHelpArr;
    }

    let searchValue = e.target.value.toLowerCase();
    let filteredVertragArray = vertrags.filter((el) =>
      el.text.toLowerCase().includes(searchValue)
    );

    filteredVertragArray.map((el, i) => (el.id = i));
    setVertrags(filteredVertragArray);
    setSearchText(e.target.value);
  };

  return (
    <div className="searchField-container">
      <TextField
        className="searchInput"
        label="Suchen..."
        variant="outlined"
        onChange={(e) => changeInput(e)}
        InputProps={{
          endAdornment: (
            <InputAdornment position="start">
              <div className="search">
                <SearchIcon />
              </div>
            </InputAdornment>
          ),
        }}
      />
    </div>
  );
};

export default SearchElement;
