import React, { Fragment, useEffect } from "react";
import { connect } from "react-redux";
/* import { useLocation } from "react-router-dom"; */

import Header from "./Header/Header";
import "./VertragsUebersicht.scss";
import InfoPanel from "./InfoPanel.js/InfoPanel";
import VertragsList from "./VertragsList/VertragsList";
import TopNav from "../vertragsUebersicht/Header/Nav/TopNav";
import SideBar from "../vertragsUebersicht/Sidebar/Sidebar";
import Scrollbar from "smooth-scrollbar";

const VertragsUebersicht = ({
  setShowDetails,
  showDetails,
  openSidebar,
  showSidebar,
  user,
  fullVertrag,
  vertrag,
}) => {
  /* let location = useLocation(); */

  useEffect(() => {
    Scrollbar.init(document.querySelector("#mainVU"));
  }, []);

  /* if (location.pathname === "/vertragsUebersicht") {
    setShowDetails(false);
  } */
  /*   const backgroundImage = require(`../../assets/images/logo1.png`);
   */ return (
    <Fragment>
      <TopNav openSidebar={openSidebar} />
      <SideBar
        openSidebar={openSidebar}
        showSidebar={showSidebar}
        user={user}
      />
      <div
        id="mainVU"
        /* className={`${showDetails ? "mainVUDetailIsOpen" : "mainVU"}`} */
        className="mainVU"
      >
        <Header text={"Meine Verträge:"} />
        <InfoPanel showDetails={showDetails} />
        <VertragsList
          vertrag={vertrag}
          fullVertrag={fullVertrag}
          setShowDetails={setShowDetails}
        />
      </div>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  user: state.userReducer.user,
  vertrag: state.vertragReducer.vertrags,
  fullVertrag: state.fullVertragReducer.fullVertrags,
});

export default connect(mapStateToProps)(VertragsUebersicht);
