import React from "react";
import InputAdornment from "@material-ui/core/InputAdornment";
import { useField } from "formik";
import { TextField } from "@material-ui/core";
import { Link } from "react-router-dom";

const MyTextFieldPasswortVergessen = ({
  label,
  placeholder,
  type,
  ...props
}) => {
  const [field, meta] = useField(props);
  const errorText = meta.error && meta.touched ? meta.error : null;

  return (
    <TextField
      {...field}
      label={label}
      helperText={errorText}
      placeholder={placeholder}
      variant="outlined"
      type={type}
      error={errorText ? true : false} //or error={!!errorText}
      style={{ marginBottom: "20px" }}
      InputProps={{
        endAdornment: (
          <InputAdornment position="start">
            <Link to="/passwort-vergessen">
              <p className="text">Vergessen?</p>
            </Link>
          </InputAdornment>
        ),
      }}
    />
  );
};

export default MyTextFieldPasswortVergessen;
