import React from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { Formik, Form } from "formik";
import Button from "@material-ui/core/Button";
import * as Yup from "yup";
import { Link } from "react-router-dom";

import MyTextField from "../../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextField";
import MyTextFieldPasswortVergessen from "../../vertragsUebersicht/VertragsContract/Contracts/ContractsElements/MyTextFieldPasswortVergessen";
import { login } from "../../../redux/index";
import "./Login.scss";
import "../Register/Register.scss";

const SignupSchema = Yup.object().shape({
  email: Yup.string().email().required("Dieses Feld ist erforderlich"),
  password: Yup.string().required("Dieses Feld ist erforderlich"),
});

export const Formular = ({ login, error }) => {
  const backgroundImage = require(`../../../assets/images/logo1.png`);
  const backgroundImage2 = require(`../../../assets/images/logo.png`);
  return (
    <div className="loginWrapper">
      <div className="loginContainer">
        <div
          className="logo1"
          style={{
            backgroundImage: `url(${backgroundImage})`,
          }}
        ></div>
        <div
          className="logo2"
          style={{
            backgroundImage: `url(${backgroundImage})`,
          }}
        ></div>
        <div className="loginContentContainer">
          <div className="my-text-field">
            <div
              className="logo"
              style={{
                backgroundImage: `url(${backgroundImage2})`,
              }}
            ></div>
            {error === "Ihre E-Mail-Adresse oder Ihr Passwort ist falsch" ? (
              <h4 className="alert-info">{error}</h4>
            ) : null}
            {error === "Benutzer existiert nicht" ? (
              <h4 className="alert-info">{error}</h4>
            ) : null}
          </div>
          <Formik
            initialValues={{
              email: "",
              password: "",
            }}
            validationSchema={SignupSchema}
            onSubmit={(data) => {
              login(data);
            }}
          >
            {() => (
              <Form
                style={{
                  display: "flex",
                  flexDirection: "column",
                  width: "300px",
                  margin: "auto",
                }}
              >
                <MyTextField
                  placeholder="E-mail"
                  name="email"
                  type="email"
                  label="E-mail"
                />
                <MyTextFieldPasswortVergessen
                  placeholder="Passwort"
                  name="password"
                  type="password"
                  label="Passwort"
                />
                <Button type="submit" variant="contained" className="button">
                  Einloggen
                </Button>
                <Link to="/register">
                  <p className="text">Ein Konto anlegen</p>
                </Link>
              </Form>
            )}
          </Formik>
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = (state) => ({
  error: state.errorReducer.msg,
});
const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ login }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Formular);
